export default "<template>\n  <div class=\"case\">\n    <ev-chart-group\n      v-model:groupSelectedLabel=\"defaultGroupSelectLabel\"\n      v-model:zoomStartIdx='zoomStartIdx'\n      v-model:zoomEndIdx='zoomEndIdx'\n      :options=\"chartGroupOptions\"\n    >\n      <ev-chart\n        :data=\"chartData\"\n        :options=\"chartOptions\"\n        @click=\"onClick\"\n      />\n      <ev-chart\n        :data=\"chartData2\"\n        :options=\"chartOptions\"\n        @click=\"onClick\"\n      />\n      <ev-chart-brush :options=\"brushOptions\"/>\n    </ev-chart-group>\n\n    <div class=\"description\">\n      <div class=\"badge yellow\">\n          v-model\n      </div>\n      {{ defaultGroupSelectLabel }}\n      <br><br>\n      <div class=\"badge yellow\">\n         \n      </div>\n      {{ clickedLabel }}\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref, reactive, onMounted } from 'vue';\nimport dayjs from 'dayjs';\n\nexport default {\n  setup() {\n    let timeValue = dayjs().format('YYYY-MM-DD');\n    const chartData = reactive({\n      series: {\n        series1: { name: 'series#1' },\n        series2: { name: 'series#2' },\n      },\n      labels: [],\n      data: {\n        series1: [],\n        series2: [],\n      },\n    });\n    const chartData2 = reactive({\n      series: {\n        series11: { name: 'DB CPU' },\n        series22: { name: 'DB Time' },\n      },\n      labels: [],\n      data: {\n        series11: [],\n        series22: [],\n      },\n    });\n    const chartOptions = reactive({\n      type: 'line',\n      width: '100%',\n      height: '80%',\n      title: {\n        show: false,\n      },\n      legend: {\n        show: true,\n        position: 'right',\n      },\n      axesX: [{\n        type: 'time',\n        timeFormat: 'YYYY-MM-DD',\n        interval: 'day',\n      }],\n      axesY: [{\n        type: 'linear',\n        showGrid: true,\n        startToZero: true,\n        autoScaleRatio: 0.1,\n      }],\n      selectLabel: {\n        use: true,\n        useClick: true,\n        limit: 2,\n        useDeselectOverflow: true,\n        useApproximateValue: false,\n        tipBackground: '#FF0000',\n        useSeriesOpacity: false,\n        useLabelOpacity: false,\n        showTextTip: true,\n        tipText: 'label',\n        showIndicator: true,\n      },\n    });\n\n    const zoomStartIdx = ref();\n    const zoomEndIdx = ref();\n    const brushOptions = reactive({\n      show: true,\n      useDebounce: true,\n      chartIdx: 0,\n      height: 80,\n      showLabel: true,\n      selection: {\n        fillColor: 'gray',\n        opacity: 0.5,\n      },\n    });\n\n    const chartGroupOptions = reactive({\n      zoom: {\n        toolbar: {\n          show: true,\n        },\n      },\n    });\n\n    const defaultGroupSelectLabel = ref({\n      dataIndex: [0, 3],\n    });\n\n    const clickedLabel = ref(\"''\");\n    const onClick = (target) => {\n      clickedLabel.value = target.selected.label.map(label => dayjs(label).format('YYYY-MM-DD'));\n    };\n\n    const addRandomChartLabel = () => {\n      timeValue = dayjs(timeValue).add(1, 'day');\n      const date = dayjs(timeValue);\n\n      chartData.labels.push(date);\n      chartData2.labels.push(date);\n    };\n\n    const addRandomChartData = (ix) => {\n      let val;\n      let val2;\n      let val3;\n      let val4;\n\n      if (ix < 20 || ix > 40) {\n        val = Math.floor(Math.random() * ((5000 - 5) + 1)) + 5;\n        val2 = Math.floor(Math.random() * ((5000 - 5) + 1)) + 5;\n        val3 = Math.floor(Math.random() * ((5000 - 5) + 1)) + 5;\n        val4 = Math.floor(Math.random() * ((5000 - 5) + 1)) + 5;\n      }\n\n      Object.values(chartData.data).forEach((seriesData, idx) => {\n        seriesData.push(idx ? val : val2);\n      });\n\n      Object.values(chartData2.data).forEach((seriesData, idx) => {\n        seriesData.push(idx ? val3 : val4);\n      });\n    };\n\n    onMounted(() => {\n      for (let ix = 0; ix < 400; ix++) {\n        addRandomChartLabel();\n        addRandomChartData(ix);\n      }\n    });\n\n    return {\n      chartData,\n      chartData2,\n      chartOptions,\n      clickedLabel,\n      brushOptions,\n      chartGroupOptions,\n      defaultGroupSelectLabel,\n      zoomStartIdx,\n      zoomEndIdx,\n      onClick,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\">\n</style>\n";