export default "<template>\n  <div class=\"case\">\n    <ev-chart\n      v-model:selectedLabel=\"selectedLabel\"\n      :data=\"chartData\"\n      :options=\"chartOptions\"\n    />\n    <div class=\"description\">\n      <div class=\"option\">\n        <span>scrollbar </span>\n        <ev-toggle v-model=\"useScrollbar\" />\n      </div>\n      <div class=\"option\">\n        <span>x range </span>\n        <ev-toggle v-model=\"useRangeX\" />\n      </div>\n      <div class=\"option\">\n        <span>min</span>\n        <ev-input-number\n          v-model=\"xMin\"\n          :step=\"xInterval\"\n          :min=\"minDate\"\n          :max=\"xMax - xInterval\"\n        />\n        <span>max</span>\n        <ev-input-number\n          v-model=\"xMax\"\n          :step=\"xInterval\"\n          :min=\"xMin + xInterval\"\n          :max=\"maxDate\"\n        />\n      </div>\n      <div class=\"option\">\n        <span>y range </span>\n        <ev-toggle v-model=\"useRangeY\" />\n      </div>\n      <div class=\"option\">\n        <span>min</span>\n        <ev-input-number\n          v-model=\"yMin\"\n          :step=\"1\"\n          :min=\"0\"\n          :max=\"yMax - 1\"\n        />\n        <span>max</span>\n        <ev-input-number\n          v-model=\"yMax\"\n          :step=\"1\"\n          :min=\"yMin + 1\"\n          :max=\"LABEL_Y_COUNT - 1\"\n        />\n      </div>\n      <label class=\"badge yellow\"> v-model:selectedLabel</label>\n      <span>{{ selectedLabel }}</span>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { computed, nextTick, onMounted, ref, reactive } from 'vue';\nimport dayjs from 'dayjs';\n\nexport default {\n  setup() {\n    const chartData = reactive({\n      series: {\n        series1: {\n          name: 'series#1',\n        },\n      },\n      labels: {\n        x: [],\n        y: [],\n      },\n      data: {\n        series1: [],\n      },\n    });\n\n    const useScrollbar = ref(true);\n\n    const LABEL_X_COUNT = 10;\n    const LABEL_Y_COUNT = 24;\n    const xInterval = 86400000;\n    const maxDate = +dayjs();\n    const minDate = +dayjs(maxDate).subtract(LABEL_X_COUNT - 1, 'd');\n    const xMin = ref(minDate);\n    const xMax = ref(maxDate);\n    const yMin = ref(16);\n    const yMax = ref(21);\n\n    const useRangeX = ref(true);\n    const useRangeY = ref(true);\n    const xRange = computed(() => (useRangeX.value ? [xMin.value, xMax.value] : null));\n    const yRange = computed(() => (useRangeY.value ? [yMin.value, yMax.value] : null));\n\n    const chartOptions = reactive({\n      type: 'heatMap',\n      width: '100%',\n      height: '300px',\n      title: {\n        text: 'Chart Title',\n        show: true,\n      },\n      indicator: {\n        use: true,\n      },\n      axesX: [{\n        type: 'time',\n        showGrid: false,\n        categoryMode: true,\n        range: xRange,\n        scrollbar: {\n          use: useScrollbar,\n          showButton: true,\n          background: '#E0E1DD',\n          thumbStyle: {\n            background: '#415A77',\n            radius: 2,\n          },\n        },\n        timeFormat: 'MMM.DD',\n        interval: 'day',\n      }],\n      axesY: [{\n        type: 'step',\n        showGrid: false,\n        range: yRange,\n        scrollbar: {\n          use: useScrollbar,\n          showButton: false,\n          background: '#E0E1DD',\n          thumbStyle: {\n            background: '#415A77',\n            radius: 2,\n          },\n        },\n      }],\n      heatMapColor: {\n        colorsByRange: [\n          { color: '#D9ED92', label: '-30 ' },\n          { color: '#B5E48C', label: '-15 ' },\n          { color: '#99D98C', label: '0 ' },\n          { color: '#76C893', label: '15 ' },\n          { color: '#52B69A', label: '30 ' },\n        ],\n        stroke: {\n          show: true,\n          lineWidth: 2,\n          radius: 2,\n          color: '#FFFFFF',\n        },\n      },\n      tooltip: {\n        use: true,\n      },\n      selectLabel: {\n        use: true,\n        useClick: true,\n        limit: 2,\n        useDeselectOverflow: true,\n        useSeriesOpacity: true,\n        useLabelOpacity: true,\n        useBothAxis: true,\n      },\n    });\n\n    const createChartLegend = () => {\n      const labelX = chartData.labels.x;\n      for (let i = 0; i < LABEL_X_COUNT; i++) {\n        labelX.push(+dayjs(maxDate).subtract(LABEL_X_COUNT - i - 1, 'd'));\n      }\n\n      xMin.value = labelX[0];\n      xMax.value = labelX[3];\n\n      const labelY = chartData.labels.y;\n      for (let i = 0; i < LABEL_Y_COUNT; i++) {\n        let hour = i.toString();\n        if (i < 10) {\n          hour = `0${i}`;\n        }\n        labelY.push(`${hour}:00`);\n      }\n    };\n\n    const createChartData = () => {\n      const labelX = chartData.labels.x;\n      const labelY = chartData.labels.y;\n      for (let ix = 0; ix < labelX.length; ix++) {\n        for (let iy = 0; iy < labelY.length; iy++) {\n          const randomCount = Math.floor(Math.random() * 5) + 1;\n          chartData.data.series1.push({\n            x: labelX[ix],\n            y: labelY[iy],\n            value: randomCount,\n          });\n        }\n      }\n    };\n\n    const selectedLabel = ref({\n      dataIndex: [],\n    });\n\n    onMounted(() => {\n      nextTick();\n      createChartLegend();\n      createChartData();\n    });\n\n    return {\n      useScrollbar,\n      useRangeX,\n      useRangeY,\n      xInterval,\n      minDate,\n      maxDate,\n      xMin,\n      xMax,\n      yMin,\n      yMax,\n      chartData,\n      chartOptions,\n      selectedLabel,\n      LABEL_Y_COUNT,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\" scoped>\n.description {\n  position: relative;\n\n  .option {\n    display: flex;\n    gap: 10px;\n    height: 30px;\n    line-height: 20px;\n    margin: 10px 0;\n  }\n\n  .ev-input-number + span {\n    line-height: 35px;\n  }\n\n  .ev-button {\n    height: 20px;\n    line-height: 20px !important;\n  }\n}\n</style>\n";