export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Common</p>\n    <ev-radio\n      v-model=\"radio1\"\n      label=\"Option A\"\n    >Radio Option A</ev-radio>\n    <ev-radio\n      v-model=\"radio1\"\n      label=\"Option B\"\n    >Radio Option B</ev-radio>\n    <ev-radio\n      v-model=\"radio1\"\n      label=\"Option C\"\n    >Radio Option C</ev-radio>\n    <div class=\"description\">\n      <span class=\"badge\">\n        Selected Value\n      </span>\n      {{ radio1 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Disabled</p>\n    <ev-radio\n      v-model=\"radio2\"\n      label=\"Option A\"\n      disabled\n    />\n    <ev-radio\n      v-model=\"radio2\"\n      label=\"Option B\"\n      disabled\n    />\n    <ev-radio\n      v-model=\"radio2\"\n      label=\"Option C\"\n    />\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Event</p>\n    <ev-radio\n      v-model=\"radio3\"\n      label=\"Option A\"\n      @change=\"changeRadio\"\n    />\n    <ev-radio\n      v-model=\"radio3\"\n      label=\"Option B\"\n      @change=\"changeRadio\"\n    />\n    <ev-radio\n      v-model=\"radio3\"\n      label=\"Option C\"\n      @change=\"changeRadio\"\n    />\n    <div class=\"description\">\n      <span class=\"badge\">\n        Change Event\n      </span>\n      {{ eventText }}\n    </div>\n    <div class=\"description\">\n      <span class=\"badge\">\n        Button Event\n      </span>\n      <button\n        class=\"btn\"\n        @click=\"selectValue('Option C')\"\n      >\n        Select Option C\n      </button>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref } from 'vue';\n\nexport default {\n  setup() {\n    const radio1 = ref('Option A');\n    const radio2 = ref('Option A');\n    const radio3 = ref('Option A');\n    const eventText = ref('No event occurred');\n    const changeRadio = (newValue) => {\n      eventText.value = `Click radio and change value '${newValue}'`;\n    };\n    const selectValue = (val) => {\n      radio3.value = val;\n    };\n    return {\n      radio1,\n      radio2,\n      radio3,\n      eventText,\n      changeRadio,\n      selectValue,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\">\n</style>\n";