export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Size</p>\n    <div style=\"font-size: 40px;\">\n      <ev-icon icon=\"ev-icon-user\" />\n      <ev-icon icon=\"ev-icon-topology\" />\n      <i class=\"ev-icon-server\"/>\n    </div>\n    <div class=\"description\" style=\"margin-bottom: 20px;\">\n      font-size   \n    </div>\n    <div>\n      <ev-icon\n        icon=\"ev-icon-user2\"\n        size=\"small\"\n      />\n      <ev-icon\n        icon=\"ev-icon-user2\"\n        size=\"medium\"\n      />\n      <ev-icon\n        icon=\"ev-icon-user2\"\n        size=\"large\"\n      />\n    </div>\n    <div class=\"description\">\n      size (small, medium, large) \n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Event</p>\n    <div style=\"font-size: 40px;\">\n      <ev-icon\n        icon=\"ev-icon-save\"\n        @click=\"clickIcon\"\n        @dblClick=\"dblClickIcon\"\n        @contextmenu=\"contextMenuIcon\"\n      />\n    </div>\n    <div class=\"description\">\n      <span class=\"badge\">\n        Click Event Name\n      </span>\n      {{ eventText }}\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref } from 'vue';\n\nexport default {\n  setup() {\n    const eventText = ref('No event occurred');\n    const clickIcon = (e) => { eventText.value = e.type; };\n    const dblClickIcon = (e) => { eventText.value = e.type; };\n    const contextMenuIcon = (e) => { eventText.value = e.type; };\n    return {\n      eventText,\n      clickIcon,\n      dblClickIcon,\n      contextMenuIcon,\n    };\n  },\n};\n</script>\n<style scoped>\n.case i {\n  margin-right: 10px;\n}\n</style>\n";