export default "<template>\n  <div class=\"case\">\n    <ev-chart\n      ref=\"evChartRef\"\n      v-model:selectedItem=\"defaultSelectItem\"\n      :data=\"chartData\"\n      :options=\"chartOptions\"\n      @click=\"onClick\"\n    />\n    <div class=\"description\">\n      <div class=\"option\">\n        <ev-toggle v-model=\"useSeriesOpacity\" />\n        <span>useSeriesOpacity</span>\n      </div>\n      <div class=\"option\">\n        <ev-toggle v-model=\"showBorder\" />\n        <span>showBorder</span>\n      </div>\n      <div class=\"option\">\n        <ev-toggle v-model=\"useClick\" />\n        <span>  enable ( false  v-model   )</span>\n      </div>\n      <div class=\"option\">\n        <ev-button @click=\"updateSelectedItem\">\n          select by v-model\n        </ev-button>\n        <span>\n             v-model:selectedItem   dataIndex   \n        </span>\n      </div>\n      <div class=\"option\">\n        <ev-toggle v-model=\"useDeselectItem\" />\n        <span>    deselect </span>\n      </div>\n      <div>\n        <div class=\"badge yellow\">\n          v-model:selectedItem\n        </div>\n        {{ defaultSelectItem }}\n        <br>\n        <br>\n        <div class=\"badge yellow\">\n            \n        </div>\n        {{ clickedItem }}\n      </div>\n    </div>\n    </div>\n</template>\n\n<script>\nimport { reactive, ref } from 'vue';\n\n  export default {\n    setup() {\n      const chartData = reactive({\n        series: {\n          series1: {\n            name: 'series#1',\n            showValue: {\n              use: true,\n              decimalPoint: 1,\n            },\n          },\n        },\n        labels: {\n          x: ['1', '2', '3', '4', '5'],\n          y: ['Metric1', 'Metric2', 'Metric3'],\n        },\n        data: {\n          series1: [\n            { x: '1', y: 'Metric1', value: 22.5 },\n            { x: '2', y: 'Metric1', value: 35.2 },\n            { x: '3', y: 'Metric1', value: 42.1 },\n            { x: '4', y: 'Metric1', value: 33.5 },\n            { x: '5', y: 'Metric1', value: 24 },\n            { x: '1', y: 'Metric2', value: 15.2 },\n            { x: '2', y: 'Metric2', value: 18 },\n            { x: '3', y: 'Metric2', value: 5.7 },\n            { x: '4', y: 'Metric2', value: 3.1 },\n            { x: '5', y: 'Metric2', value: 22.1 },\n            { x: '1', y: 'Metric3', value: 52.5 },\n            { x: '2', y: 'Metric3', value: 11.2 },\n            { x: '3', y: 'Metric3', value: 27.1 },\n            { x: '4', y: 'Metric3', value: 1.5 },\n            { x: '5', y: 'Metric3', value: 8 },\n          ],\n        },\n      });\n\n      const useSeriesOpacity = ref(true);\n      const showBorder = ref(true);\n      const useClick = ref(true);\n      const useDeselectItem = ref(false);\n\n      const chartOptions = reactive({\n        type: 'heatMap',\n        width: '100%',\n        height: '300px',\n        title: {\n          text: 'Chart Title',\n          show: true,\n        },\n        axesX: [{\n          type: 'step',\n          showGrid: true,\n        }],\n        axesY: [{\n          type: 'step',\n          showGrid: true,\n        }],\n        itemHighlight: false,\n        heatMapColor: {\n          rangeCount: 3,\n          min: '#FCEFB4',\n          max: '#F9DC5C',\n          decimalPoint: 1,\n          stroke: {\n            show: true,\n            lineWidth: 1,\n            color: '#000000',\n          },\n        },\n        tooltip: {\n          use: true,\n        },\n        selectItem: {\n          use: true,\n          useSeriesOpacity,\n          useClick,\n          showBorder,\n          borderStyle: {\n            color: '#E84855',\n            lineWidth: 4,\n            radius: 1,\n          },\n          useDeselectItem,\n        },\n      });\n\n      const evChartRef = ref();\n      const defaultSelectItem = ref({\n        seriesID: 'series1',\n        dataIndex: 1,\n      });\n      const clickedItem = ref();\n      const onClick = (target) => {\n        clickedItem.value = target;\n      };\n\n      const updateSelectedItem = () => {\n        const dataLength = Object.values(chartData.data)[0].length;\n        defaultSelectItem.value.dataIndex = Math.floor(Math.random() * dataLength);\n      };\n\n      return {\n        chartData,\n        chartOptions,\n        defaultSelectItem,\n        clickedItem,\n        useSeriesOpacity,\n        showBorder,\n        useClick,\n        evChartRef,\n        useDeselectItem,\n        onClick,\n        updateSelectedItem,\n      };\n    },\n  };\n</script>\n\n<style lang=\"scss\" scoped>\n.description {\n  position: relative;\n\n  .option {\n    display: flex;\n    gap: 10px;\n    height: 30px;\n    line-height: 20px;\n    margin: 10px 0;\n  }\n\n  .ev-button {\n    height: 20px;\n    line-height: 20px !important;\n  }\n}\n</style>\n";