export default "<template>\n  <div class=\"case\">\n    <ev-chart\n      :data=\"chartData\"\n      :options=\"chartOptions\"\n    />\n  </div>\n</template>\n\n<script>\n  export default {\n    setup() {\n      const chartData = {\n        series: {\n          series1: { name: 'series#1' },\n          series2: { name: 'series#2' },\n          series3: { name: 'series#3' },\n        },\n        data: {\n          series1: [10],\n          series2: [20],\n          series3: [70],\n        },\n      };\n\n      const chartOptions = {\n        type: 'pie',\n        width: '100%',\n        title: {\n          text: 'Chart Title',\n          show: true,\n        },\n        legend: {\n          show: true,\n          position: 'right',\n        },\n        tooltip: {\n          formatter: ({ value }) => `${value}%`,\n        },\n      };\n\n      return {\n        chartData,\n        chartOptions,\n      };\n    },\n  };\n</script>\n\n<style lang=\"scss\" scoped>\n  .case {\n    height: 100%;\n    background-color: #EEEEEE;\n  }\n</style>\n";