export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Common</p>\n    <ev-window\n      v-model:visible=\"isVisible1\"\n      title=\"WINDOW TITLE\"\n    >\n      <div>visible prop     .</div>\n    </ev-window>\n    <div class=\"description\">\n      <button\n        class=\"btn\"\n        @click=\"clickButton1\"\n      >\n        click to open window!\n      </button>\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Nested</p>\n    <ev-window\n      v-model:visible=\"isVisible2\"\n      title=\"WINDOW TITLE\"\n      icon-class=\"ev-icon-binder\"\n      fullscreen\n    >\n      <template #header>\n        <p class=\"nested-btn\">\n          #header slot       .\n          <span class=\"header-right-icon\">\n            <i class=\"ev-icon-moon\" />\n            <i class=\"ev-icon-shard\" />\n          </span>\n        </p>\n      </template>\n      <div>\n        window    .<br/>\n            window !\n      </div>\n      <span class=\"height-box\"/>\n      <button\n        class=\"nested-btn\"\n        @click=\"clickButton3\"\n      >\n        click to nested window!\n      </button>\n      <span class=\"height-box\" />\n      <div>default slot    window   .</div>\n      <span class=\"height-box\" />\n      <template #footer>\n        <div>#footer slot      .</div>\n      </template>\n    </ev-window>\n    <ev-window\n      v-model:visible=\"isVisible3\"\n      title=\"[NESTED] WINDOW TITLE\"\n      icon-class=\"ev-icon-pig\"\n      width=\"40%\"\n      height=\"60%\"\n    >\n      <div>\n        NESTED CONTENTS\n      </div>\n      <template #footer>\n        <div>NESTED FOOTER</div>\n      </template>\n    </ev-window>\n    <div class=\"description\">\n      <button\n        class=\"btn\"\n        @click=\"clickButton2\"\n      >\n        click to open window!\n      </button>\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Hide Dim Layer (Modeless)</p>\n    <ev-window\n      v-model:visible=\"isVisible4\"\n      title=\"WINDOW TITLE\"\n      :is-modal=\"false\"\n    >\n      <div>is-modal prop  window  modal    .</div>\n    </ev-window>\n    <div class=\"description\">\n      <button\n        class=\"btn\"\n        @click=\"clickButton4\"\n      >\n        click to open window!\n      </button>\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Block Hide Scroll</p>\n    <ev-window\n      v-model:visible=\"isVisible5\"\n      title=\"WINDOW TITLE\"\n      :hide-scroll=\"false\"\n    >\n      <div>hide-scroll prop  body     .</div>\n    </ev-window>\n    <div class=\"description\">\n      <button\n        class=\"btn\"\n        @click=\"clickButton5\"\n      >\n        click to open window!\n      </button>\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Draggable / Resizable / Maximizable</p>\n    <ev-window\n      v-model:visible=\"isVisible6\"\n      title=\" WINDOW\"\n      draggable\n      resizable\n      maximizable\n      @mousedown=\"mousedown\"\n      @mousedown-mouseup=\"mouseup\"\n      @mousedown-mousemove=\"mousemove\"\n      @resize=\"resize\"\n    >\n      <div>\n        Draggable & Resizable & Maximizable    .\n      </div>\n      <span class=\"height-box\" />\n      <button\n        class=\"nested-btn\"\n        @click=\"clickButton7\"\n      >\n        click to open nested window!\n      </button>\n    </ev-window>\n    <ev-window\n      v-model:visible=\"isVisible7\"\n      title=\"[NESTED]  WINDOW\"\n      width=\"80vw\"\n      height=\"30vh\"\n      min-width=\"50vw\"\n      min-height=\"30vh\"\n      draggable\n      resizable\n      maximizable\n    >\n      <button\n        class=\"nested-btn\"\n        @click=\"clickButton8\"\n      >\n        click to open nested window!\n      </button>\n    </ev-window>\n    <ev-window\n      v-model:visible=\"isVisible8\"\n      title=\"[NESTED]  WINDOW\"\n      :is-modal=\"false\"\n      :hide-scroll=\"false\"\n      width=\"30vw\"\n      height=\"40vh\"\n      draggable\n      resizable\n      maximizable\n    >\n      <div>WINDOW CONTENTS</div>\n    </ev-window>\n\n    <div class=\"description\">\n      <button\n        class=\"btn\"\n        @click=\"clickButton6\"\n      >\n        click to open window!\n      </button>\n    </div>\n  </div>\n\n  <div class=\"case\">\n    <p class=\"case-title\">Esc Key Close</p>\n    <!--Window 1-->\n    <ev-window\n      v-model:visible=\"isVisible10\"\n      title=\"Esc Close Available Window\"\n      :esc-close=\"true\"\n    >\n      <div>esc-close prop     .</div>\n    </ev-window>\n\n    <!--Window -->\n    <ev-window\n      v-model:visible=\"isVisible11[0]\"\n      title=\" Window\"\n      :esc-close=\"true\"\n      maximizable\n      fullscreen\n    >\n      <div>\n        Nested + Maximizable Window\n        <br />\n        <button class=\"nested-btn\">\n          Esc key   .\n        </button>\n      </div>\n    </ev-window>\n    <ev-window\n      v-model:visible=\"isVisible11[1]\"\n      title=\" Window\"\n      :esc-close=\"true\"\n      width=\"85%\"\n      height=\"85%\"\n      resizable\n    >\n      <div>\n        Resizable Window\n        <br />\n        <button class=\"nested-btn\">\n          Esc key   .\n        </button>\n      </div>\n    </ev-window>\n    <ev-window\n      v-model:visible=\"isVisible11[2]\"\n      title=\" Window\"\n      :esc-close=\"true\"\n      :is-modal=\"false\"\n      width=\"70%\"\n      height=\"70%\"\n      draggable\n    >\n      <div>\n        Draggable Window\n        <br />\n        <button class=\"nested-btn\">\n          Esc key   .\n        </button>\n      </div>\n    </ev-window>\n    <ev-window\n      v-model:visible=\"isVisible11[3]\"\n      title=\" Window\"\n      :esc-close=\"isEscClose\"\n      width=\"60%\"\n      height=\"60%\"\n      draggable\n    >\n      <div>\n        Draggable Window\n        <div class=\"esc-close-toggle-box\">\n          <label>\n            esc-close prop \n            <ev-icon icon=\"ev-icon-allow2-right\" />\n          </label>\n          <ev-toggle\n            v-model=\"isEscClose\"\n            @change=\"changeEscClose\"\n          />\n        </div>\n        <p class=\"nested-btn\">\n             Window esc-close prop false ,\n          <strong>  </strong>   Window Esc    .\n        </p>\n      </div>\n    </ev-window>\n\n    <div class=\"description\">\n      <button\n        class=\"btn\"\n        @click=\"clickButton10\"\n      >\n        click to open window!\n      </button>\n      <br />\n      <br />\n      <button\n        class=\"btn\"\n        @click=\"clickButton11\"\n      >\n        click to open different types of windows!\n      </button>\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Focusable</p>\n    <ev-window\n      v-model:visible=\"isVisible13\"\n      title=\"Focusable\"\n      :is-modal=\"false\"\n      :esc-close=\"true\"\n      maximizable\n      fullscreen\n    >\n      <div>\n        <p>\n          <button\n            class=\"nested-btn\"\n            @click=\"clickButton12\"\n          >\n            click to open stacked windows!\n          </button>\n          <br />\n          <i class=\"ev-icon-moon\" />\n             1    .\n          <br/>\n          <i class=\"ev-icon-moon\" />\n            focusable prop  false    .\n          <br/>\n          <i class=\"ev-icon-moon\" />\n          Esc      .\n          <br/>\n          <i class=\"ev-icon-moon\" />\n              Window .\n        </p>\n        <ev-window\n          v-for=\"(_, idx) in isVisible12\"\n          :key=\"idx\"\n          v-model:visible=\"isVisible12[idx]\"\n          :title=\"`No. ${idx + 1}`\"\n          :is-modal=\"false\"\n          :esc-close=\"true\"\n          :focusable=\"true\"\n          draggable\n          width=\"300px\"\n          height=\"300px\"\n          :style=\"{\n            transform: randomPositions[idx],\n          }\"\n        >\n          <div\n            :style=\"{\n              backgroundColor: randomColors[idx],\n              width: '100%',\n              height: '100%',\n            }\"\n          ></div>\n        </ev-window>\n      </div>\n    </ev-window>\n    <div class=\"description\">\n      <button\n        class=\"btn\"\n        @click=\"clickButton13\"\n      >\n        click to open window!\n      </button>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { nextTick, ref, watch } from 'vue';\n\nexport default {\n  setup() {\n    const isVisible1 = ref(false);\n    const clickButton1 = () => {\n      isVisible1.value = true;\n    };\n\n    const isVisible2 = ref(false);\n    const clickButton2 = () => {\n      isVisible2.value = true;\n    };\n\n    const isVisible3 = ref(false);\n    const clickButton3 = () => {\n      isVisible3.value = true;\n    };\n\n    const isVisible4 = ref(false);\n    const clickButton4 = () => {\n      isVisible4.value = true;\n    };\n\n    const isVisible5 = ref(false);\n    const clickButton5 = () => {\n      isVisible5.value = true;\n    };\n\n    const isVisible6 = ref(false);\n    const clickButton6 = () => {\n      isVisible6.value = true;\n    };\n\n    const isVisible7 = ref(false);\n    const clickButton7 = () => {\n      isVisible7.value = true;\n    };\n\n    const isVisible8 = ref(false);\n    const clickButton8 = () => {\n      isVisible8.value = true;\n    };\n\n    // Test\n    const isVisible9 = ref(false);\n    const clickButton9 = () => {\n      isVisible9.value = true;\n    };\n\n    const isVisible10 = ref(false);\n    const clickButton10 = () => {\n      isVisible10.value = true;\n    };\n\n    const isVisible11 = ref([false, false, false, false]);\n    const clickButton11 = () => {\n      for (let i = 0; i < isVisible11.value.length; i++) {\n        isVisible11.value[i] = true;\n      }\n    };\n\n    const isVisible12 = ref([\n      false, false, false, false, false,\n      false, false,\n    ]);\n    const clickButton12 = () => {\n      for (let i = 0; i < isVisible12.value.length; i++) {\n        isVisible12.value[i] = true;\n      }\n    };\n\n    const randomColors = Array(isVisible12.value.length)\n      .fill(null)\n      .map(() => `rgb(${Math.floor(255 * Math.random())}, ${Math.floor(255 * Math.random())}, ${Math.floor(255 * Math.random())})`);\n    const randomPositions = Array(isVisible12.value.length)\n      .fill(null)\n      .map(() => `translate(${Math.floor(400 * (Math.random() - 0.5))}px, ${Math.floor(400 * (Math.random() - 0.5))}px)`);\n\n    const isVisible13 = ref(false);\n    const clickButton13 = () => {\n      isVisible13.value = true;\n    };\n\n    watch(isVisible13, (isVisible) => {\n      if (isVisible) return;\n      nextTick(() => {\n        for (let i = 0; i < isVisible12.value.length; i++) {\n          isVisible12.value[i] = isVisible;\n        }\n      });\n    });\n\n    const isEscClose = ref(false);\n    const changeEscClose = (newValue) => {\n      isEscClose.value = newValue;\n    };\n\n    const mousedown = clickedInfo => console.log('mousedown', clickedInfo);\n    const mouseup = event => console.log('mousedown-mouseup', event);\n    const mousemove = clickedInfo => console.log('mousedown-mousemove', clickedInfo);\n    const resize = (event, positionInfo) => console.log('resize', event, positionInfo);\n\n    return {\n      isVisible1,\n      clickButton1,\n      isVisible2,\n      clickButton2,\n      isVisible3,\n      clickButton3,\n      isVisible4,\n      clickButton4,\n      isVisible5,\n      clickButton5,\n      isVisible6,\n      clickButton6,\n      isVisible7,\n      clickButton7,\n      isVisible8,\n      clickButton8,\n      isVisible9,\n      clickButton9,\n      isVisible10,\n      clickButton10,\n      isVisible11,\n      clickButton11,\n      isVisible12,\n      clickButton12,\n      randomColors,\n      randomPositions,\n      isVisible13,\n      clickButton13,\n      isEscClose,\n      changeEscClose,\n      mousedown,\n      mouseup,\n      mousemove,\n      resize,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\">\n@import '../../../style/index.scss';\n\n.nested-btn {\n  padding: 4px 7px;\n  outline: none;\n  cursor: pointer;\n  background-color: rgba($color-yellow, 0.5);\n  border: 1px solid $color-yellow;\n\n  @include themify() {\n    color: themed('font-color-base');\n  }\n  &:hover {\n    background-color: $color-yellow;\n  }\n}\n.height-box {\n  display: block;\n  height: 300px;\n  background-color: #F5F5F5;\n}\n.header-right-title {\n  margin-left: 10px;\n}\n.header-right-icon {\n  margin-left: 20px;\n}\n.esc-close-toggle-box {\n  display: flex;\n\n  & > * {\n    margin-right: 5px;\n  }\n}\n</style>\n";