export default "<template>\n  <div class=\"case\">\n    <ev-tree-grid\n      :columns=\"columns\"\n      :rows=\"tableData\"\n      :option=\"{\n        useCheckbox: {\n          use: useCheckboxMV.use,\n          headerCheck: useCheckboxMV.headerCheck,\n        },\n      }\"\n    >\n      <!-- renderer start -->\n      <template #gridButton>\n        <ev-button\n          type=\"ghost\"\n          size=\"small\"\n          @click=\"onRowDelete\"\n        >\n          Delete\n        </ev-button>\n        <ev-button\n          size=\"small\"\n          @click=\"onRowEdit\"\n        >\n          Edit\n        </ev-button>\n      </template>\n      <template #check=\"{ item }\">\n        <ev-checkbox\n          v-model=\"item.data[item.fieldName]\"\n          label=\"check\"\n          @click.stop=\"\"\n          @dblclick.stop=\"\"\n        />\n      </template>\n      <template #slide=\"{ item }\">\n        <ev-slider\n          v-model=\"item.data[item.fieldName]\"\n          range\n          readonly\n          :mark=\"{\n            33.33: 'W',\n            66.66: 'C',\n          }\"\n          :color=\"['#3C81F6', '#FADE4C', '#FF470E']\"\n          :show-tooltip=\"false\"\n        />\n      </template>\n      <template #custom=\"{ item }\">\n        <span\n          :class=\"getStateClass(item.data[item.fieldName])\"\n        >\n          <span class=\"v-chip__content\"> {{ item.data[item.fieldName] }} </span>\n        </span>\n      </template>\n      <template #inputNumber=\"{ item }\">\n        <ev-input-number\n          v-model=\"item.data[item.fieldName]\"\n          :max=\"100\"\n          :min=\"0\"\n          @click.stop=\"onInputNumberClick\"\n          @dblclick.stop=\"\"\n        />\n      </template>\n    </ev-tree-grid>\n  </div>\n</template>\n<script>\nimport { ref, reactive } from 'vue';\n\nexport default {\n  setup() {\n    const tableData = ref([]);\n    const useCheckboxMV = reactive({\n      use: true,\n      headerCheck: true,\n    });\n    const getData = () => {\n      tableData.value = [{\n        check: true,\n        slide: [33.33, 66.66],\n        inputNumber: 10,\n        custom: Math.floor(Math.random() * (99 - 10 + 1)) + 10,\n        expand: true,\n        children: [{\n          check: true,\n          slide: [33.33, 66.66],\n          inputNumber: 10,\n          custom: Math.floor(Math.random() * (99 - 10 + 1)) + 10,\n          expand: false,\n          children: [{\n            check: true,\n            slide: [33.33, 66.66],\n            inputNumber: 10,\n            custom: Math.floor(Math.random() * (99 - 10 + 1)) + 10,\n          }, {\n            check: true,\n            slide: [33.33, 66.66],\n            inputNumber: 10,\n            custom: Math.floor(Math.random() * (99 - 10 + 1)) + 10,\n            expand: false,\n            children: [{\n              check: true,\n              slide: [33.33, 66.66],\n              inputNumber: 10,\n              custom: Math.floor(Math.random() * (99 - 10 + 1)) + 10,\n              children: [{\n                check: true,\n                slide: [33.33, 66.66],\n                inputNumber: 10,\n                custom: Math.floor(Math.random() * (99 - 10 + 1)) + 10,\n                children: [{\n                  check: true,\n                  slide: [33.33, 66.66],\n                  inputNumber: 10,\n                  custom: Math.floor(Math.random() * (99 - 10 + 1)) + 10,\n                }],\n              }],\n            }, {\n              check: true,\n              slide: [33.33, 66.66],\n              inputNumber: 10,\n              custom: Math.floor(Math.random() * (99 - 10 + 1)) + 10,\n            }],\n          }],\n        }, {\n          check: true,\n          slide: [33.33, 66.66],\n          inputNumber: 10,\n          custom: Math.floor(Math.random() * (99 - 10 + 1)) + 10,\n          children: [{\n            check: true,\n            slide: [33.33, 66.66],\n            inputNumber: 10,\n            custom: Math.floor(Math.random() * (99 - 10 + 1)) + 10,\n          }, {\n            check: true,\n            slide: [33.33, 66.66],\n            inputNumber: 10,\n            custom: Math.floor(Math.random() * (99 - 10 + 1)) + 10,\n          }, {\n            check: true,\n            slide: [33.33, 66.66],\n            inputNumber: 10,\n            custom: Math.floor(Math.random() * (99 - 10 + 1)) + 10,\n          }],\n        }, {\n          check: true,\n          slide: [33.33, 66.66],\n          inputNumber: 10,\n          custom: Math.floor(Math.random() * (99 - 10 + 1)) + 10,\n        }],\n      }];\n    };\n    const columns = ref([\n      {\n        caption: 'Check',\n        field: 'check',\n        type: 'boolean',\n        width: 170,\n      },\n      {\n        caption: 'Slide',\n        field: 'slide',\n        type: 'string',\n        width: 120,\n      },\n      {\n        caption: 'InputNumber',\n        field: 'inputNumber',\n        type: 'number',\n        width: 120,\n      },\n      {\n        caption: 'Custom',\n        field: 'custom',\n        type: 'string',\n        width: 100,\n      },\n      {\n        caption: '',\n        field: 'gridButton',\n        type: 'boolean',\n        width: 120,\n      },\n    ]);\n    const onInputNumberClick = () => {\n      console.log('On click InputNumber');\n    };\n    const onRowDelete = () => {\n      console.log('On click Delete');\n    };\n    const onRowEdit = () => {\n      console.log('On click RowEdit');\n    };\n    const getStateClass = (value) => {\n      let stateColor = 'green';\n      if (value >= 70) {\n        stateColor = 'yellow';\n      } else if (value >= 50) {\n        stateColor = 'red';\n      }\n      return {\n        'v-chip': true,\n        'v-size--default': true,\n        [stateColor]: true,\n      };\n    };\n    getData();\n    return {\n      columns,\n      tableData,\n      useCheckboxMV,\n      onInputNumberClick,\n      onRowDelete,\n      onRowEdit,\n      getStateClass,\n    };\n  },\n};\n</script>\n<style lang=\"scss\" scoped>\n.description {\n  min-width: 200px;\n}\n.form-rows {\n  display: flex;\n  margin-bottom: 5px;\n}\n.form-row {\n  width: 50%;\n}\n.ev-text-field, .ev-input-number, .ev-select {\n  width: 80%;\n}\n.badge {\n  margin-bottom: 2px;\n  margin-right: 5px !important;\n}\n.ev-toggle {\n  margin-right: 10px;\n}\n.v-chip {\n  display: inline-flex;\n  position: relative;\n  height: 32px;\n  padding: 0 12px;\n  align-items: center;\n  cursor: default;\n  color: #FFFFFF;\n  line-height: 20px;\n  max-width: 100%;\n  outline: none;\n  overflow: hidden;\n  text-decoration: none;\n  transition-duration: .28s;\n  transition-property: box-shadow, opacity;\n  transition-timing-function: cubic-bezier(.4,0,.2,1);\n  vertical-align: middle;\n  white-space: nowrap;\n  border-radius: 16px;\n  font-size: 14px;\n\n  &.green {\n    background-color: #4CAF50;\n    border-color: #4CAF50;\n  }\n  &.yellow {\n    background-color: #F7DF6A;\n    border-color: #F7DF6A;\n  }\n  &.red {\n    background-color: #FF4949;\n    border-color: #FF4949;\n  }\n}\n.v-chip .v-chip__content {\n  display: inline-flex;\n  height: 100%;\n  align-items: center;\n  max-width: 100%;\n}\n</style>\n";