export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Common</p>\n    <ev-progress\n      v-model=\"progressVal1\"\n    />\n    <br>\n    <ev-progress\n      v-model=\"progressVal1\"\n      color=\"#67C23A\"\n    >\n      <div class=\"label-cls\">\n        <p>{{ progressVal1 }} %</p>\n      </div>\n    </ev-progress>\n    <br>\n    <ev-progress\n      v-model=\"progressVal1\"\n      color=\"#F56C6C\"\n    >\n      <div class=\"label-cls\">\n        <p>{{ progressVal1 }} / 100</p>\n      </div>\n    </ev-progress>\n    <br>\n    <ev-progress\n      v-model=\"progressVal1\"\n      color=\"#FADE4C\"\n      :stroke-width=\"15\"\n    />\n    <br>\n    <ev-progress\n      v-model=\"progressVal1\"\n      :color=\"customColors\"\n      :stroke-width=\"20\"\n      :inner-text=\"`${progressVal1}%`\"\n    />\n    <div class=\"description\">\n      <button\n        class=\"btn\"\n        @click=\"changeVal1\"\n      >\n        Change Value : {{ progressVal1 }}\n      </button>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref } from 'vue';\n\nexport default {\n  setup() {\n    const progressVal1 = ref(50);\n    const changeVal1 = () => {\n      if (progressVal1.value === 100) {\n        progressVal1.value = 0;\n      } else {\n        progressVal1.value += 10;\n      }\n    };\n    const customColors = [\n      { color: '#F56C6C', value: 20 },\n      { color: '#1989FA', value: 80 },\n      { color: '#E6A23C', value: 40 },\n      { color: '#6F7AD3', value: 100 },\n      { color: '#5CB87A', value: 60 },\n    ];\n\n    return {\n      progressVal1,\n      changeVal1,\n      customColors,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\">\n.label-cls {\n  width: 70px;\n}\n</style>\n";