export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Common</p>\n    <ev-button\n      type=\"info\"\n      @click=\"showInfo\"\n    >\n      Show Info\n    </ev-button>\n    <ev-button\n      type=\"primary\"\n      @click=\"showSuccess\"\n    >\n      Show Success\n    </ev-button>\n    <ev-button\n      type=\"warning\"\n      @click=\"showWarning\"\n    >\n      Show Warning\n    </ev-button>\n    <ev-button\n      type=\"error\"\n      @click=\"showError\"\n    >\n      Show Error\n    </ev-button>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">With Close Button</p>\n    <ev-button\n      @click=\"showClose\"\n    >\n      Show Close\n    </ev-button>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Custom Duration</p>\n    <ev-button\n      @click=\"showDuration\"\n    >\n      Show Duration\n    </ev-button>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Icon</p>\n    <ev-button\n      @click=\"showIcon\"\n    >\n      Show Icon\n    </ev-button>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">After Closing</p>\n    <ev-button\n      @click=\"showOnClose\"\n    >\n      Show onClose\n    </ev-button>\n    <div class=\"description\">\n      <span class=\"badge\">\n        After close behavior\n      </span>\n      {{ onCloseMsg }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">HTML</p>\n    <ev-button\n      @click=\"showHTML\"\n    >\n      Show HTML\n    </ev-button>\n  </div>\n</template>\n\n<script>\nimport { ref, getCurrentInstance } from 'vue';\n\nexport default {\n  setup() {\n    const { ctx } = getCurrentInstance();\n    const showInfo = () => {\n      ctx.$message('Infomation. This is an Info type message.');\n    };\n    const showSuccess = () => {\n      ctx.$message({\n        message: 'Success! This is an Success type message.',\n        type: 'success',\n      });\n    };\n    const showWarning = () => {\n      ctx.$message({\n        message: 'Warning. This is an Warning type message.',\n        type: 'warning',\n      });\n    };\n    const showError = () => {\n      ctx.$message({\n        message: 'Error. This is an Error type message.',\n        type: 'error',\n      });\n    };\n    const showClose = () => {\n      ctx.$message({\n        message: 'You can choose to close properties',\n        showClose: true,\n      });\n    };\n    const showDuration = () => {\n      ctx.$message({\n        message: 'You can change the duration.',\n        duration: 1500,\n      });\n    };\n    const showIcon = () => {\n      ctx.$message({\n        message: 'You can add an icon.',\n        iconClass: 'ev-icon-getmore',\n      });\n    };\n    const onCloseMsg = ref();\n    const showOnClose = () => {\n      ctx.$message({\n        message: 'You can set the behavior after closing.',\n        showClose: true,\n        onClose: () => {\n          onCloseMsg.value = 'You can set the behavior after closing!';\n        },\n      });\n    };\n    const showHTML = () => {\n      ctx.$message({\n        message: 'You <i>can enter</i> your message in <strong>html</strong>.',\n        useHTML: true,\n      });\n    };\n    return {\n      showInfo,\n      showSuccess,\n      showWarning,\n      showError,\n      showClose,\n      showDuration,\n      showIcon,\n      onCloseMsg,\n      showOnClose,\n      showHTML,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\" scoped>\n.case .ev-button {\n  margin-right: 10px;\n}\n</style>\n";