export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Common</p>\n    <ev-slider\n      v-model=\"modelValue1\"\n      range\n    />\n    <div class=\"description\">\n      <span class=\"badge dark\">\n        Slider Value\n        {{ modelValue1 }}\n      </span>\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Step</p>\n    <ev-slider\n      v-model=\"modelValue2\"\n      :step=\"8\"\n      range\n      show-step\n    />\n    <div class=\"description\">\n      <span class=\"badge yellow\">\n        step=8\n      </span>\n      <span class=\"badge dark\">\n        Slider Value\n        {{ modelValue2 }}\n      </span>\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Fill Color</p>\n    <ev-slider\n      v-model=\"modelValue2\"\n      :step=\"8\"\n      :color=\"'#FADE4C'\"\n      range\n    />\n    <div class=\"description\">\n      <span class=\"badge yellow\">\n        color=\"#FADE4C\"\n      </span>\n    </div>\n    <ev-slider\n      v-model=\"modelValue2\"\n      :step=\"8\"\n      :color=\"['#3C81F6', '#FADE4C', '#FF470E']\"\n      range\n    />\n    <div class=\"description\">\n      <span class=\"badge yellow\">\n        color=['#3C81F6', '#FADE4C', '#FF470E']\n      </span>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref } from 'vue';\n\nexport default {\n  setup() {\n    const modelValue1 = ref([40, 84]);\n    const modelValue2 = ref([20, 45]);\n    return {\n      modelValue1,\n      modelValue2,\n    };\n  },\n};\n</script>\n";