export default "<template>\n  <ev-chart\n    :data=\"chartData\"\n    :options=\"chartOptions\"\n  />\n</template>\n\n<script>\nexport default {\n  setup() {\n    const chartData = {\n      series: {\n        series1: { name: 'series#1', showValue: { use: true } },\n        series2: { name: 'series#2', showValue: { use: true } },\n      },\n      labels: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri'],\n      groups: [['series1', 'series2']],\n      data: {\n        series1: [null, 100, 58, 40, 50],\n        series2: [null, 200, 200, 60, 55],\n      },\n    };\n\n    const chartOptions = {\n      type: 'bar',\n      width: '100%',\n      height: '100%',\n      thickness: 1,\n      title: {\n        text: 'Chart Title',\n        show: true,\n      },\n      legend: {\n        show: true,\n        position: 'right',\n      },\n      horizontal: false,\n      overlapping: {\n        use: true,\n      },\n      axesX: [{\n        type: 'step',\n        showGrid: false,\n        labelStyle: {\n          fitWidth: true,\n          fitDir: 'left',\n        },\n      }],\n      axesY: [{\n        type: 'linear',\n        startToZero: true,\n        autoScaleRatio: 0.1,\n        showGrid: false,\n      }],\n    };\n\n    return {\n      chartData,\n      chartOptions,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\">\n</style>\n";