export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Common</p>\n    <ev-toggle\n      v-model=\"checkVal1\"\n    />\n    <div class=\"description\">\n      {{ checkVal1 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Readonly</p>\n    <ev-toggle\n      v-model=\"checkVal1\"\n      readonly\n    />\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Disabled</p>\n    <ev-toggle\n      v-model=\"checkVal1\"\n      disabled\n    />\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Custom width</p>\n    <ev-toggle\n      v-model=\"checkVal1\"\n      :width=\"100\"\n    />\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Custom color</p>\n    <ev-toggle\n      v-model=\"checkVal1\"\n      active-color=\"#FFDD57\"\n      inactive-color=\"#FB8C00\"\n    />\n  </div>\n</template>\n\n<script>\nimport { ref } from 'vue';\n\nexport default {\n  setup() {\n    const checkVal1 = ref(true);\n\n    return {\n      checkVal1,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\">\n</style>\n";