<template>
  <article class="article-wrapper">
    <h3 class="article-title">API</h3>
    <div
      v-highlight
      class="markdown"
      v-html="compiledMd"
    />
  </article>
</template>

<script>
import { computed } from 'vue';
import marked from 'marked';
import highlight from 'docs/directives/highlight';

export default {
  name: 'Example',
  directives: {
    highlight,
  },
  components: {
  },
  props: {
    source: {
      type: String,
      default: '',
    },
  },
  setup(props) {
    const compiledMd = computed(() => marked(props.source));

    return {
      compiledMd,
    };
  },
};
/* eslint-disable max-len */
