export default "<template>\n  <ev-chart\n    :data=\"chartData\"\n    :options=\"chartOptions\"\n  />\n</template>\n\n<script>\n  import dayjs from 'dayjs';\n\n  export default {\n    setup() {\n      const currentTime = dayjs();\n\n      const chartData = {\n        series: {\n          series1: { name: 'series#1' },\n          series2: { name: 'series#2' },\n          series3: { name: 'series#3' },\n        },\n        data: {\n          series1: [\n            { x: currentTime, y: 1 },\n            { x: currentTime.subtract(1, 'second'), y: 20 },\n            { x: currentTime.subtract(1, 'second'), y: 30 },\n            { x: currentTime.subtract(2, 'second'), y: 10 },\n          ],\n          series2: [\n            { x: currentTime, y: 20 },\n            { x: currentTime.subtract(2, 'second'), y: 20 },\n            { x: currentTime.subtract(3, 'second'), y: 13 },\n            { x: currentTime.subtract(4, 'second'), y: 1 },\n          ],\n          series3: [\n            { x: currentTime, y: 30 },\n            { x: currentTime.subtract(2, 'second'), y: 5 },\n            { x: currentTime.subtract(3, 'second'), y: 23 },\n            { x: currentTime.subtract(5, 'second'), y: 40 },\n            { x: currentTime.subtract(6, 'second'), y: 20 },\n          ],\n        },\n      };\n\n      const chartOptions = {\n        type: 'scatter',\n        width: '100%',\n        padding: {\n          right: 50,\n        },\n        title: {\n          text: 'Chart Title',\n          show: true,\n        },\n        legend: {\n          show: true,\n          position: 'right',\n        },\n        axesX: [{\n          type: 'time',\n          timeFormat: 'HH:mm:ss',\n          interval: 'second',\n          plotLines: [{\n            color: '#FF0000',\n            value: currentTime.subtract(5, 'second'),\n            segments: [6, 2],\n            label: {\n              show: true,\n              fillColor: '#E0E0E0',\n              text: 'X Plot Line',\n              maxWidth: 40,\n              textOverflow: 'ellipsis',\n            },\n          }],\n          plotBands: [{\n            color: 'rgba(250, 222, 76, 0.8)',\n            from: currentTime.subtract(2, 'second'),\n            to: currentTime.subtract(3, 'second'),\n            label: {\n              show: true,\n              text: 'X Plot Band Zone',\n              fontColor: '#FFA500',\n              textOverflow: 'ellipsis',\n            },\n          }],\n        }],\n        axesY: [{\n          type: 'linear',\n          showGrid: true,\n          startToZero: true,\n          autoScaleRatio: 0.1,\n          plotLines: [{\n            color: '#FF0000',\n            value: 40,\n            segments: [6, 2],\n            label: {\n              show: true,\n              text: 'Y Plot Line',\n            },\n          }],\n          plotBands: [{\n            color: 'rgba(250, 222, 76, 0.8)',\n            from: 10,\n            to: 15,\n            label: {\n              show: true,\n              fillColor: '#E0E0E0',\n              text: 'Y Plot Band Zone',\n              fontColor: '#FFA500',\n              textOverflow: 'ellipsis',\n              verticalAlign: 'top',\n            },\n          }],\n        }],\n      };\n\n      return {\n        chartData,\n        chartOptions,\n      };\n    },\n  };\n</script>\n";