export default "<template>\n  <div class=\"case\">\n    <ev-chart\n      :data=\"chartData\"\n      :options=\"chartOptions\"\n    />\n    <div class=\"description\">\n      <div class=\"row\">\n        <div class=\"row-item\">\n          <span class=\"item-title\">\n                \n          </span>\n          <ev-toggle v-model=\"sortByValue\"/>\n        </div>\n        <div class=\"row-item\">\n          <span class=\"item-title\">\n             \n          </span>\n          <ev-toggle v-model=\"useScrollbar\"/>\n        </div>\n      </div>\n\n      <div class=\"row\">\n        <div class=\"row-item\">\n          <span class=\"item-title\">\n              \n          </span>\n          <ev-select\n            v-model=\"textOverflow\"\n            :items=\"[{\n              name: 'wrap',\n              value: 'wrap',\n            }, {\n              name: 'ellipsis',\n              value: 'ellipsis',\n            }]\"\n          />\n        </div>\n      </div>\n\n      <div class=\"row\">\n        <div class=\"row-item\">\n          <span class=\"item-title\">\n            maxWidth\n          </span>\n          <ev-input-number\n            v-model=\"maxWidth\"\n            :step=\"10\"\n            :min=\"100\"\n            :max=\"500\"\n          />\n        </div>\n        <div class=\"row-item\">\n          <span class=\"item-title\">\n            maxHeight\n          </span>\n          <ev-input-number\n            v-model=\"maxHeight\"\n            :step=\"10\"\n            :min=\"100\"\n            :max=\"500\"\n          />\n        </div>\n      </div>\n\n      <div class=\"row\">\n        <div class=\"row-item\">\n          <span class=\"item-title\">\n             \n          </span>\n          <ev-toggle v-model=\"useShadow\"/>\n        </div>\n        <div class=\"row-item\">\n          <span class=\"item-title\">\n             \n          </span>\n          <ev-input-number\n            v-model=\"shadowOpacity\"\n            :step=\"0.05\"\n            :precision=\"2\"\n            :min=\"0\"\n            :max=\"1\"\n          />\n        </div>\n      </div>\n\n      <div class=\"row\">\n        <div class=\"row-item\">\n          <span class=\"item-title\">\n             \n          </span>\n          <ev-text-field v-model=\"fontColor\"/>\n        </div>\n        <div class=\"row-item\">\n          <span class=\"item-title\">\n             \n          </span>\n          <ev-text-field v-model=\"backgroundColor\"/>\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\n  import { ref, onMounted, reactive } from 'vue';\n  import dayjs from 'dayjs';\n\n  export default {\n    setup() {\n      const sortByValue = ref(true);\n      const useScrollbar = ref(true);\n      const maxWidth = ref(300);\n      const maxHeight = ref(300);\n      const useShadow = ref(false);\n      const shadowOpacity = ref(0.25);\n      const fontColor = ref('#000000');\n      const backgroundColor = ref('rgb(210, 234, 227, 0.7)');\n      const textOverflow = ref('wrap');\n\n      const chartData = reactive({\n        series: {\n          series1: { name: 'series#1', point: false },\n          series2: { name: 'series#2', point: false },\n          series3: { name: 'series#3', point: false },\n          series4: { name: 'series#4', point: false },\n          series5: { name: 'series#5', point: false },\n          series6: { name: 'series#6', point: false },\n          series7: { name: 'series#7', point: false },\n          series8: { name: 'series#8', point: false },\n          series9: { name: 'series#9', point: false },\n          series10: { name: 'series#10', point: false },\n          series11: { name: 'series#11', point: false },\n          series12: { name: 'series#12', point: false },\n          series13: { name: 'series#13', point: false },\n          series14: { name: 'series#14', point: false },\n          series15: { name: 'series#15', point: false },\n          series16: { name: 'series#16', point: false },\n          series17: { name: 'series#17', point: false },\n          series18: { name: 'series#18', point: false },\n          series19: { name: 'series#19', point: false },\n          series20: { name: 'series#20', point: false },\n          series21: { name: 'series#21__long____long____long____long____long____long____long____long____long____long____long____long____long____long____long____long____long____long__(end)', point: false },\n        },\n        labels: [],\n        data: {\n          series1: [],\n          series2: [],\n          series3: [],\n          series4: [],\n          series5: [],\n          series6: [],\n          series7: [],\n          series8: [],\n          series9: [],\n          series10: [],\n          series11: [],\n          series12: [],\n          series13: [],\n          series14: [],\n          series15: [],\n          series16: [],\n          series17: [],\n          series18: [],\n          series19: [],\n          series20: [],\n          series21: [],\n        },\n      });\n\n      const chartOptions = reactive({\n        type: 'line',\n        width: '100%',\n        height: '80%',\n        title: {\n          text: 'Chart Title',\n          show: true,\n        },\n        legend: {\n          show: true,\n          position: 'right',\n        },\n        axesX: [{\n          type: 'time',\n          timeFormat: 'HH:mm:ss',\n          interval: 'second',\n        }],\n        axesY: [{\n          type: 'linear',\n          showGrid: true,\n          startToZero: true,\n          autoScaleRatio: 0.1,\n        }],\n        tooltip: {\n          use: true,\n          sortByValue,\n          backgroundColor,\n          fontColor,\n          shadowOpacity,\n          useShadow,\n          useScrollbar,\n          maxWidth,\n          maxHeight,\n          textOverflow,\n          formatter: {\n            title: ({ x }) => dayjs(x).format('YYYY-MM-DD HH:mm:ss'),\n            value: ({ y }) => `${y.toFixed(2)}`,\n          },\n        },\n      });\n\n      let timeValue = dayjs().format('YYYY-MM-DD HH:mm:ss');\n      const addRandomChartData = () => {\n        timeValue = dayjs(timeValue).add(1, 'second');\n        chartData.labels.push(dayjs(timeValue));\n\n        Object.values(chartData.data).forEach((seriesData) => {\n          seriesData.push(Math.random());\n        });\n      };\n\n      onMounted(() => {\n        for (let ix = 0; ix < 60; ix++) {\n          addRandomChartData();\n        }\n      });\n\n      return {\n        chartData,\n        chartOptions,\n        sortByValue,\n        useScrollbar,\n        maxWidth,\n        maxHeight,\n        useShadow,\n        shadowOpacity,\n        fontColor,\n        backgroundColor,\n        textOverflow,\n      };\n    },\n  };\n</script>\n\n<style lang=\"scss\" scoped>\n  .description-label {\n    vertical-align: top;\n    margin-right: 3px;\n  }\n\n  .row {\n    display: flex;\n    margin-top: 15px;\n    .row-item {\n      display: flex;\n      margin-right: 30px;\n      .item-title {\n        line-height: 33px;\n        margin-right: 3px;\n        min-width: 50px;\n      }\n      .ev-text-field, .ev-input-number, .ev-select {\n        width: auto;\n      }\n    }\n  }\n</style>\n";