export default "<template>\n  <ev-chart\n    :data=\"chartData\"\n    :options=\"chartOptions\"\n  />\n</template>\n\n<script>\n  export default {\n    setup() {\n      const chartData = {\n        series: {\n          series1: { name: 'series#1' },\n          series2: { name: 'series#2' },\n          series3: { name: 'series#3' },\n        },\n        data: {\n          series1: [10],\n          series2: [20],\n          series3: [70],\n        },\n      };\n\n      const chartOptions = {\n        type: 'pie',\n        width: '100%',\n        title: {\n          text: 'Chart Title',\n          show: true,\n        },\n        legend: {\n          show: true,\n          position: 'right',\n          width: 200,\n          table: {\n            use: true,\n            columns: {\n              name: {\n                title: 'Series name',\n                style: {\n                  width: '140px',\n                },\n              },\n              last: {\n                use: true,\n                title: 'Value',\n                unit: '%',\n                decimalPoint: 0,\n                style: {\n                  color: '#219EBC',\n                },\n              },\n            },\n          },\n        },\n        tooltip: {\n          formatter: ({ value }) => `${value}%`,\n        },\n      };\n\n      return {\n        chartData,\n        chartOptions,\n      };\n    },\n  };\n</script>\n";