export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Common</p>\n    <ev-menu\n      v-model=\"menu\"\n      :items=\"menuItems\"\n      @change=\"menuChange\"\n    />\n    <div class=\"description\">\n      <span class=\"badge\">Selected Menu Name</span>\n      {{ menu }}\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref } from 'vue';\n\nexport default {\n  setup() {\n    const menu = ref('menu01');\n    const menuItems = ref([\n      {\n        text: 'Menu Text 01',\n        value: 'menu01',\n        iconClass: 'ev-icon-s-panel-out',\n        disabled: true,\n      },\n      {\n        text: 'Menu Text 02',\n        value: 'menu02',\n        iconClass: 'ev-icon-s-pause',\n        children: [\n          {\n            text: 'Child Text 02-1',\n            value: 'menu02-1',\n            iconClass: 'ev-icon-server2',\n            expand: false,\n            children: [\n              {\n                text: 'Child Text 02-1-1',\n                value: 'menu02-1-1',\n              },\n              {\n                text: 'Child Text 02-1-2',\n                value: 'menu02-1-2',\n              },\n              {\n                text: 'Child Text 02-1-3',\n                value: 'menu02-1-3',\n              },\n              {\n                text: 'Child Text 02-1-4',\n                value: 'menu02-1-4',\n                disabled: true,\n              },\n            ],\n          },\n          {\n            text: 'Child Text 02-2',\n            value: 'menu02-2',\n            iconClass: 'ev-icon-server',\n          },\n          {\n            text: 'Child Text 02-3',\n            value: 'menu02-3',\n            iconClass: 'ev-icon-compress',\n            children: [\n              {\n                text: 'Child Text 02-3-1',\n                value: 'menu02-3-1',\n                iconClass: 'ev-icon-bell-warning',\n              },\n              {\n                text: 'Child Text 02-3-2',\n                value: 'menu02-3-2',\n                iconClass: 'ev-icon-expand2',\n              },\n            ],\n          },\n        ],\n      },\n      {\n        text: 'Menu Text 03',\n        value: 'menu03',\n        hidden: true,\n        children: [\n          {\n            text: 'Child Text 03-1',\n            value: 'menu03-1',\n          },\n          {\n            text: 'Child Text 03-2',\n            value: 'menu03-2',\n          },\n          {\n            text: 'Child Text 03-3',\n            value: 'menu03-3',\n          },\n          {\n            text: 'Child Text 03-4',\n            value: 'menu03-4',\n          },\n        ],\n      },\n      {\n        text: 'Menu Text 04',\n        value: 'menu04',\n      },\n      {\n        text: 'Menu Text 05',\n        value: 'menu05',\n        children: [\n          {\n            text: 'Child Text 05-1',\n            value: 'menu05-1',\n          },\n          {\n            text: 'Child Text 05-2',\n            value: 'menu05-2',\n            iconClass: 'ev-icon-expand',\n          },\n        ],\n      },\n    ]);\n    const menuChange = (newVal, prevVal) => {\n      console.log(newVal, prevVal);\n    };\n\n    return {\n      menu,\n      menuItems,\n      menuChange,\n    };\n  },\n};\n</script>\n";