export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Common</p>\n    <ev-button>Default</ev-button>\n    <ev-button\n      type=\"primary\"\n    >\n      Primary\n    </ev-button>\n    <ev-button\n      type=\"info\"\n    >\n      Info\n    </ev-button>\n    <ev-button\n      type=\"warning\"\n    >\n      Warning\n    </ev-button>\n    <ev-button\n      type=\"error\"\n    >\n      error\n    </ev-button>\n    <ev-button\n      type=\"ghost\"\n    >\n      ghost\n    </ev-button>\n    <ev-button\n      type=\"dashed\"\n    >\n      dashed\n    </ev-button>\n    <ev-button\n      type=\"text\"\n    >\n      Text\n    </ev-button>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Shape</p>\n    <ev-button\n      shape=\"radius\"\n    >\n      Default\n    </ev-button>\n    <ev-button\n      type=\"primary\"\n      shape=\"radius\"\n    >\n      Primary\n    </ev-button>\n    <ev-button\n      type=\"info\"\n      shape=\"radius\"\n    >\n      Info\n    </ev-button>\n    <ev-button\n      type=\"warning\"\n      shape=\"radius\"\n    >\n      Warning\n    </ev-button>\n    <ev-button\n      type=\"error\"\n      shape=\"radius\"\n    >\n      error\n    </ev-button>\n    <ev-button\n      type=\"ghost\"\n      shape=\"radius\"\n    >\n      ghost\n    </ev-button>\n    <ev-button\n      type=\"dashed\"\n      shape=\"radius\"\n    >\n      dashed\n    </ev-button>\n    <div class=\"description\" style=\"margin-bottom: 20px;\">\n      <div class=\"badge yellow\">\n        shape=radius\n      </div>\n    </div>\n    <div style=\"margin-bottom: 20px;\">\n\n      <ev-button\n        shape=\"circle\"\n      >\n        Default\n      </ev-button>\n      <ev-button\n        type=\"primary\"\n        shape=\"circle\"\n      >\n        Primary\n      </ev-button>\n      <ev-button\n        type=\"info\"\n        shape=\"circle\"\n      >\n        Info\n      </ev-button>\n      <ev-button\n        type=\"warning\"\n        shape=\"circle\"\n      >\n        Warning\n      </ev-button>\n      <ev-button\n        type=\"error\"\n        shape=\"circle\"\n      >\n        error\n      </ev-button>\n      <ev-button\n        type=\"ghost\"\n        shape=\"circle\"\n      >\n        ghost\n      </ev-button>\n      <ev-button\n        type=\"dashed\"\n        shape=\"circle\"\n      >\n        dashed\n      </ev-button>\n    </div>\n    <div>\n      <ev-button\n        shape=\"circle\"\n      >\n        <ev-icon icon=\"ev-icon-db2\" />\n      </ev-button>\n      <ev-button\n        type=\"primary\"\n        shape=\"circle\"\n      >\n        <ev-icon icon=\"ev-icon-calendar\" />\n      </ev-button>\n      <ev-button\n        type=\"info\"\n        shape=\"circle\"\n      >\n        <ev-icon icon=\"ev-icon-dolphin\" />\n      </ev-button>\n      <ev-button\n        type=\"warning\"\n        shape=\"circle\"\n      >\n        <ev-icon icon=\"ev-icon-bell-warning\" />\n      </ev-button>\n      <ev-button\n        type=\"error\"\n        shape=\"circle\"\n      >\n        <ev-icon icon=\"ev-icon-close2\" />\n      </ev-button>\n      <ev-button\n        type=\"ghost\"\n        shape=\"circle\"\n      >\n        <ev-icon icon=\"ev-icon-connection\" />\n      </ev-button>\n      <ev-button\n        type=\"dashed\"\n        shape=\"circle\"\n      >\n        <ev-icon icon=\"ev-icon-pig\" />\n      </ev-button>\n    </div>\n    <div class=\"description\">\n      <div class=\"badge yellow\">\n        shape=circle\n      </div>\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Disabled</p>\n    <ev-button\n      disabled\n    >\n      Default\n    </ev-button>\n    <ev-button\n      type=\"primary\"\n      disabled\n    >\n      Primary\n    </ev-button>\n    <ev-button\n      type=\"info\"\n      disabled\n    >\n      Info\n    </ev-button>\n    <ev-button\n      type=\"warning\"\n      disabled\n    >\n      Warning\n    </ev-button>\n    <ev-button\n      type=\"error\"\n      disabled\n    >\n      error\n    </ev-button>\n    <ev-button\n      type=\"ghost\"\n      disabled\n    >\n      ghost\n    </ev-button>\n    <ev-button\n      type=\"dashed\"\n      disabled\n    >\n      dashed\n    </ev-button>\n    <ev-button\n      type=\"text\"\n      disabled\n    >\n      Text\n    </ev-button>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Size</p>\n    <ev-button\n      size=\"large\"\n    >\n      Large\n    </ev-button>\n    <ev-button\n      size=\"medium\"\n    >\n      Medium\n    </ev-button>\n    <ev-button\n      size=\"small\"\n    >\n      Small\n    </ev-button>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">HTML Type</p>\n    <form>\n      <ev-button\n        html-type=\"button\"\n      >\n        Button\n      </ev-button>\n      <ev-button\n        html-type=\"submit\"\n      >\n        Submit\n      </ev-button>\n      <ev-button\n        html-type=\"reset\"\n      >\n        Reset\n      </ev-button>\n    </form>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Auto Focus</p>\n    <ev-button\n      :auto-focus=\"true\"\n    >\n      Auto Focus\n    </ev-button>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Event</p>\n    <ev-button\n      type=\"info\"\n      @click=\"clickBtn\"\n    >\n      <ev-icon icon=\"ev-icon-login\" />\n       Click!\n    </ev-button>\n    <div class=\"description\">\n      <span class=\"badge\">Click Event</span>\n      {{ clickMsg }}\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref } from 'vue';\n\nexport default {\n  setup() {\n    const clickMsg = ref('No event occurred');\n    const clickBtn = () => {\n      clickMsg.value = 'Button was clicked!';\n    };\n    return {\n      clickMsg,\n      clickBtn,\n    };\n  },\n};\n</script>\n<style lang=\"scss\" scoped>\n.case .ev-button {\n  margin-right: 10px;\n}\n</style>\n";