export default "<template>\n  <div class=\"case\">\n    <ev-radio-group\n      v-model=\"radioGroup1\"\n    >\n      <ev-radio label=\"Option A\" />\n      <ev-radio label=\"Option B\" />\n      <ev-radio label=\"Option C\" />\n    </ev-radio-group>\n    <div class=\"description\">\n      <span class=\"badge\">\n        Selected Value\n      </span>\n      {{ radioGroup1 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Event</p>\n    <ev-radio-group\n      v-model=\"radioGroup2\"\n      @change=\"changeRadioGroup\"\n    >\n      <ev-radio label=\"Option A\" />\n      <ev-radio label=\"Option B\" />\n      <ev-radio label=\"Option C\" />\n    </ev-radio-group>\n    <div class=\"description\">\n      <span class=\"badge\">\n        Change Event\n      </span>\n      {{ eventText }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Button Type</p>\n    <ev-radio-group\n      v-model=\"radioGroup2\"\n      type=\"button\"\n    >\n      <ev-radio label=\"Option A\" />\n      <ev-radio label=\"Option B\" />\n      <ev-radio label=\"Option C\" />\n    </ev-radio-group>\n    <ev-radio-group\n      v-model=\"radioGroup2\"\n      type=\"button\"\n    >\n      <ev-radio\n        label=\"Option A\"\n        disabled\n      />\n      <ev-radio label=\"Option B\" />\n      <ev-radio\n        label=\"Option C\"\n        disabled\n      />\n    </ev-radio-group>\n    <ev-radio-group\n      v-model=\"radioGroup2\"\n      type=\"button\"\n    >\n      <ev-radio\n        label=\"Option A\"\n        disabled\n      />\n      <ev-radio\n        label=\"Option B\"\n        disabled\n      />\n      <ev-radio\n        label=\"Option C\"\n        disabled\n      />\n    </ev-radio-group>\n  </div>\n</template>\n\n<script>\n  import { ref } from 'vue';\n\n  export default {\n    components: {},\n    setup() {\n      const radioGroup1 = ref('Option A');\n      const radioGroup2 = ref('Option A');\n      const eventText = ref('No event occurred');\n      const changeRadioGroup = (newValue) => {\n        eventText.value = `Click radio and change value '${newValue}'`;\n      };\n\n      return {\n        radioGroup1,\n        radioGroup2,\n        eventText,\n        changeRadioGroup,\n      };\n    },\n  };\n</script>\n\n<style lang=\"scss\">\n.case .ev-radio-group.type-button {\n  margin-bottom: 10px;\n}\n</style>\n";