export default "<template>\n  <div class=\"case\">\n    <ev-chart\n      ref=\"chart\"\n      v-model:selectedLabel=\"defaultSelectLabel\"\n      :data=\"chartData1\"\n      :options=\"chartOptions1\"\n      @click=\"onClick\"\n    />\n    <ev-chart\n      v-model:selectedLabel=\"defaultSelectLabel\"\n      :data=\"chartData2\"\n      :options=\"chartOptions2\"\n      @click=\"onClick\"\n    />\n    <div class=\"description\">\n      <ev-toggle v-model=\"isUseClick\" />\n      <span>\n          enable ( false  v-model   )\n      </span>\n      <br>\n      <br>\n      <ev-toggle v-model=\"isFixedPosTop\" />\n      <span>\n        tip   \n      </span>\n      <br>\n      <br>\n      <ev-toggle v-model=\"isLive\" />\n      <span>\n          \n      </span>\n      <br>\n      <br>\n      <div>\n        <div class=\"badge yellow\">\n          v-model:selectedLabel\n        </div>\n        {{ defaultSelectLabel }}\n        <br>\n        <br>\n        <div class=\"badge yellow\">\n             (selected)\n        </div>\n        {{ clickedLabel }}\n        <br>\n        <br>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { onBeforeUnmount, onMounted, reactive, ref, watch } from 'vue';\nimport dayjs from 'dayjs';\n\nexport default {\n  components: {},\n\n  setup() {\n    const chart = ref(null);\n\n    const chartData1 = reactive({\n      series: {\n        series1: { name: 'series#1' },\n        series2: { name: 'series#2' },\n        series3: { name: 'series#3' },\n        series4: { name: 'series#4' },\n        series5: { name: 'series#5' },\n      },\n      labels: [],\n      data: {\n        series1: [],\n        series2: [],\n        series3: [],\n        series4: [],\n        series5: [],\n      },\n    });\n    const chartData2 = reactive({\n      series: {\n        series1: { name: 'series1', fill: true, point: false },\n        series2: { name: 'series2', fill: true, point: false },\n        series3: { name: 'series3', fill: true, point: false },\n        series4: { name: 'series4', fill: true, point: false },\n        series5: { name: 'series5', fill: true, point: false },\n      },\n      labels: [],\n      groups: [['series1', 'series2', 'series3', 'series4', 'series5']],\n      data: {\n        series1: [],\n        series2: [],\n        series3: [],\n        series4: [],\n        series5: [],\n      },\n    });\n    const isUseClick = ref(true);\n    const isFixedPosTop = ref(false);\n\n    const chartOptions1 = ref({\n      type: 'line',\n      width: '100%',\n      height: '80%',\n      title: {\n        show: false,\n      },\n      legend: {\n        show: true,\n        position: 'right',\n      },\n      axesX: [{\n        type: 'time',\n        timeFormat: 'HH:mm:ss',\n        interval: 'second',\n      }],\n      axesY: [{\n        type: 'linear',\n        showGrid: true,\n        startToZero: true,\n        autoScaleRatio: 0.1,\n      }],\n      selectLabel: {\n        use: true,\n        useClick: isUseClick,\n        limit: 2,\n        useDeselectOverflow: true,\n        showTip: true,\n        fixedPosTop: isFixedPosTop,\n        useApproximateValue: true,\n        tipBackground: '#FF0000',\n        useSeriesOpacity: true,\n        useLabelOpacity: true,\n      },\n    });\n    const chartOptions2 = ref({\n      type: 'line',\n      width: '100%',\n      height: '80%',\n      title: {\n        show: false,\n      },\n      legend: {\n        show: true,\n        position: 'right',\n      },\n        axesX: [{\n          type: 'time',\n          timeFormat: 'HH:mm:ss',\n          interval: 'second',\n        }],\n      axesY: [{\n        type: 'linear',\n        showGrid: true,\n        startToZero: true,\n        autoScaleRatio: 0.1,\n      }],\n      selectLabel: {\n        use: true,\n        useClick: isUseClick,\n        limit: 2,\n        useDeselectOverflow: true,\n        showTip: true,\n        fixedPosTop: isFixedPosTop,\n        useApproximateValue: true,\n        tipBackground: '#FF0000',\n        useSeriesOpacity: true,\n        useLabelOpacity: true,\n      },\n    });\n\n    const clickedLabel = ref();\n    const onClick = ({ selected }) => {\n      clickedLabel.value = selected;\n    };\n\n    const defaultSelectLabel = ref({\n      dataIndex: [1],\n    });\n\n    const isLive = ref(false);\n    const liveInterval = ref();\n    let timeValue = dayjs().format('YYYY-MM-DD HH:mm:ss');\n\n    const addRandomChartData = () => {\n      if (isLive.value) {\n        chartData1.labels.shift();\n        chartData2.labels.shift();\n      }\n\n      timeValue = dayjs(timeValue).add(1, 'second');\n      chartData1.labels.push(dayjs(timeValue));\n      chartData2.labels.push(dayjs(timeValue));\n\n      Object.values(chartData1.data).forEach((seriesData) => {\n        if (isLive.value) {\n          seriesData.shift();\n        }\n        seriesData.push(Math.floor(Math.random() * ((5000 - 5) + 1)) + 5);\n      });\n      Object.values(chartData2.data).forEach((seriesData) => {\n        if (isLive.value) {\n          seriesData.shift();\n        }\n        seriesData.push(Math.floor(Math.random() * ((5000 - 5) + 1)) + 5);\n      });\n    };\n\n    onMounted(() => {\n      for (let ix = 0; ix < 60; ix++) {\n        addRandomChartData();\n      }\n    });\n\n    watch(isLive, (newValue) => {\n      if (newValue) {\n        addRandomChartData();\n        liveInterval.value = setInterval(addRandomChartData, 1000);\n      } else {\n        clearInterval(liveInterval.value);\n      }\n    });\n\n    onBeforeUnmount(() => {\n      clearInterval(liveInterval.value);\n    });\n\n    return {\n      chart,\n      chartData1,\n      chartData2,\n      chartOptions1,\n      chartOptions2,\n      clickedLabel,\n      defaultSelectLabel,\n      isUseClick,\n      isFixedPosTop,\n      isLive,\n      onClick,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\" scoped>\n  .description {\n    position: relative;\n  }\n</style>\n";