export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Common - Value</p>\n    <ev-slider\n      v-model=\"modelValue1\"\n      :mark=\"{\n        10: 'Custom Label 10',\n        30: 'Custom Label 30',\n        70: 'Custom Label 70',\n      }\"\n    />\n    <div class=\"description\" style=\"margin-bottom: 20px;\">\n      <span class=\"badge\">\n        min=0\n      </span>\n      <span class=\"badge\">\n        max=100\n      </span>\n      <span class=\"badge\">\n        step=1\n      </span>\n    </div>\n    <ev-slider\n      v-model=\"modelValue2\"\n      :mark=\"{\n        value: {\n          10: '10 Value',\n          50: '50 Value',\n          80: '80 Value',\n        },\n      }\"\n    />\n    <div class=\"description\" style=\"margin-bottom: 20px;\">\n      <span class=\"badge\">\n        min=0\n      </span>\n      <span class=\"badge\">\n        max=100\n      </span>\n      <span class=\"badge\">\n        step=1\n      </span>\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Percent</p>\n    <ev-slider\n      v-model=\"modelValue2\"\n      :max=\"80\"\n      :mark=\"{\n        percent: {\n          10: '10%',\n          50: '50%',\n          80: '80%',\n        },\n        value: {\n          50: '50 Value',\n        },\n      }\"\n    />\n    <ev-slider\n      v-model=\"modelValue3\"\n      range\n      readonly\n      :mark=\"{\n        33.33: 'Warning Value',\n        66.66: 'Critical Value',\n      }\"\n      :color=\"['#3C81F6', '#FADE4C', '#FF470E']\"\n      :show-tooltip=\"false\"\n    />\n    <div class=\"description\">\n      <span class=\"badge\">\n        min=0\n      </span>\n      <span class=\"badge\">\n        max=80\n      </span>\n      <span class=\"badge\">\n        step=1\n      </span>\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Style</p>\n    <ev-slider\n      v-model=\"modelValue2\"\n      :max=\"80\"\n      :mark=\"{\n        value: {\n          10: '10 Value',\n          35: '35 Value',\n          75: '75 Value',\n        },\n        style: {\n          color: '#FF470E',\n          fontSize: '14px',\n          backgroundColor: '#E8E8E8',\n          borderBottom: '1px solid #BBBBBB',\n        },\n      }\"\n    />\n    <div class=\"description\">\n      <span class=\"badge\">\n        min=0\n      </span>\n      <span class=\"badge\">\n        max=80\n      </span>\n      <span class=\"badge\">\n        step=1\n      </span>\n    </div>\n  </div>\n</template>\n\n<script>\n  import { ref } from 'vue';\n\n  export default {\n    setup() {\n      const modelValue1 = ref();\n      const modelValue2 = ref();\n      const modelValue3 = ref([33.33, 66.66]);\n      return {\n        modelValue1,\n        modelValue2,\n        modelValue3,\n      };\n    },\n  };\n</script>\n";