export default "<template>\n  <ev-chart\n    :data=\"chartData\"\n    :options=\"chartOptions\"\n  />\n</template>\n\n<script>\nimport { onMounted, reactive } from 'vue';\n\n  export default {\n    setup() {\n      const chartData = reactive({\n        series: {\n          series1: {\n            name: 'series#1',\n          },\n        },\n        labels: {\n          x: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],\n          y: [],\n        },\n        data: {\n          series1: [],\n        },\n      });\n\n      const chartOptions = {\n        type: 'heatMap',\n        width: '100%',\n        title: {\n          text: 'Chart Title',\n          show: true,\n        },\n        indicator: {\n          use: true,\n        },\n        axesX: [{\n          type: 'step',\n          showGrid: false,\n        }],\n        axesY: [{\n          type: 'step',\n          showGrid: false,\n        }],\n        heatMapColor: {\n          colorsByRange: [\n            { color: '#4FD4B6', label: 'Low' },\n            { color: '#6DB9E3', label: 'Medium' },\n            { color: '#5A4BE1', label: 'High' },\n          ],\n          error: '#D46C4F',\n          stroke: {\n            show: true,\n            color: '#FFFFFF',\n            lineWidth: 2,\n            radius: 3,\n          },\n        },\n        tooltip: {\n          use: true,\n        },\n      };\n\n      const crateYAxisLabels = () => {\n        const currentYear = new Date().getFullYear();\n        for (let i = 0; i < 7; i++) {\n          chartData.labels.y.push(currentYear - i);\n        }\n      };\n\n      const createChartData = () => {\n        const labelX = chartData.labels.x;\n        const labelY = chartData.labels.y;\n        for (let ix = 0; ix < labelX.length; ix++) {\n          for (let iy = 0; iy < labelY.length; iy++) {\n            let randomCount = Math.floor(Math.random() * 100) + 1;\n            if (randomCount > 90) {\n              randomCount = -1;\n            }\n            chartData.data.series1.push({\n              x: labelX[ix],\n              y: labelY[iy],\n              value: randomCount,\n            });\n          }\n        }\n      };\n\n      onMounted(() => {\n        crateYAxisLabels();\n        createChartData();\n      });\n\n      return {\n        chartData,\n        chartOptions,\n      };\n    },\n  };\n</script>\n";