export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">TreeGrid</p>\n    <ev-tree-grid\n      v-model:selected=\"selected\"\n      v-model:checked=\"checked\"\n      :columns=\"columns\"\n      :rows=\"tableData\"\n      :width=\"widthMV\"\n      :height=\"heightMV\"\n      :option=\"{\n        adjust: true,\n        showHeader: showHeaderMV,\n        rowHeight: rowHeightMV,\n        columnWidth: columnWidthMV,\n        useCheckbox: {\n          use: useCheckboxMV,\n          mode: checkboxModeMV,\n          headerCheck: headerCheckMV,\n        },\n        useSelection: useSelection,\n        customContextMenu: menuItems,\n        style: {\n          stripe: stripeMV,\n          border: borderMV,\n          highlight: highlightMV,\n        },\n        expandIcon: expandIconMV,\n        collapseIcon: collapseIconMV,\n        parentIcon: parentIconMV,\n        childIcon: childIconMV,\n        page: pageInfo,\n        useSummary: true,\n      }\"\n      @check-row=\"onCheckedRow\"\n      @check-all=\"onCheckedRow\"\n      @click-row=\"onClickRow\"\n      @dblclick-row=\"onDoubleClickRow\"\n    >\n    </ev-tree-grid>\n    <div class=\"description\">\n      <div class=\"form-rows\">\n        <span class=\"badge yellow\">\n          Show Header\n        </span>\n        <ev-toggle\n          v-model=\"showHeaderMV\"\n        />\n        <span class=\"badge yellow\">\n          Use Checkbox\n        </span>\n        <ev-toggle\n          v-model=\"useCheckboxMV\"\n        />\n        <span class=\"badge yellow\">\n          Stripe Style\n        </span>\n        <ev-toggle\n          v-model=\"stripeMV\"\n        />\n        <span class=\"badge yellow\">\n          Use Selection\n        </span>\n        <ev-toggle\n          v-model=\"useSelection.use\"\n        />\n        <span class=\"badge yellow\">\n          Multiple Selection\n        </span>\n        <ev-toggle\n          v-model=\"useSelection.multiple\"\n        />\n      </div>\n      <div class=\"form-rows\">\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">\n            Expand Column Index\n          </span>\n          <ev-input-number\n            v-model=\"expandColumnMV\"\n            :step=\"1\"\n            :max=\"columns.length - 1\"\n            :min=\"0\"\n            @change=\"changeExpandTreeColumn\"\n          />\n        </div>\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">\n            Limit Count\n          </span>\n          <ev-select\n            v-model=\"useSelection.limitCount\"\n            :items=\"limitItems\"\n            :style=\"{ width: '200px' }\"\n            clearable\n            placeholder=\"Please select value.\"\n          />\n        </div>\n      </div>\n      <div class=\"form-rows\">\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">\n            Width\n          </span>\n          <ev-text-field\n            v-model=\"widthMV\"\n          />\n        </div>\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">\n            Height\n          </span>\n          <ev-text-field\n            v-model=\"heightMV\"\n          />\n        </div>\n      </div>\n      <div class=\"form-rows\">\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">\n            Row Height\n          </span>\n          <ev-input-number\n            v-model=\"rowHeightMV\"\n            :step=\"10\"\n            :max=\"150\"\n            :min=\"10\"\n          />\n        </div>\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">\n            Column Width\n          </span>\n          <ev-input-number\n            v-model=\"columnWidthMV\"\n            :step=\"20\"\n            :max=\"300\"\n            :min=\"40\"\n          />\n        </div>\n      </div>\n      <div class=\"form-rows\">\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">\n            Checkbox\n          </span>\n          <ev-radio-group\n            v-model=\"checkboxModeMV\"\n            type=\"button\"\n            @change=\"changeMode\"\n          >\n            <ev-radio label=\"single\" />\n            <ev-radio label=\"multi\" />\n          </ev-radio-group>\n          <span class=\"badge\">\n            Mode\n          </span>{{ checkboxModeMV }}\n          <span class=\"badge\">\n            Count\n          </span>{{ checked.length }}\n        </div>\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">\n            Checked Row\n          </span>\n          <ev-text-field\n            v-model=\"checkedRowsMV\"\n            type=\"textarea\"\n            readonly\n          />\n        </div>\n      </div>\n      <div class=\"form-rows\">\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">\n            Clicked Row\n          </span>\n          <ev-text-field\n            v-model=\"clickedRowMV\"\n            type=\"textarea\"\n            readonly\n          />\n        </div>\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">\n            Double Clicked Row\n          </span>\n          <ev-text-field\n            v-model=\"DbClickedRowsMV\"\n            type=\"textarea\"\n            readonly\n          />\n        </div>\n      </div>\n      <div class=\"form-rows\">\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">\n            Highlight\n          </span>\n          <ev-input-number\n            v-model=\"highlightMV\"\n            :step=\"1\"\n            :max=\"100\"\n          />\n        </div>\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">\n            Border Style\n          </span>\n          <ev-select\n            v-model=\"borderMV\"\n            :items=\"borderItems\"\n            placeholder=\"Please select value.\"\n          />\n          <button\n            class=\"btn\"\n            @click=\"onReset('border')\"\n          >\n            <ev-icon\n              icon=\"ev-icon-trash3\"\n              size=\"small\"\n            />Reset\n          </button>\n        </div>\n      </div>\n      <div class=\"form-rows\">\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">\n            Expand/Collapse Icon\n          </span>\n          <ev-select\n            v-model=\"iconMV\"\n            :items=\"iconItems\"\n            placeholder=\"Please select value.\"\n          />\n          <button\n            class=\"btn\"\n            @click=\"onReset('treeIcon')\"\n          >\n            <ev-icon\n              icon=\"ev-icon-trash3\"\n              size=\"small\"\n            />Reset\n          </button>\n        </div>\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">\n            Data Icon\n          </span>\n          <ev-select\n            v-model=\"dataIconMV\"\n            :items=\"dataIconItems\"\n            placeholder=\"Please select value.\"\n          />\n          <button\n            class=\"btn\"\n            @click=\"onReset('dataIcon')\"\n          >\n            <ev-icon\n              icon=\"ev-icon-trash3\"\n              size=\"small\"\n            />Reset\n          </button>\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref, computed, reactive } from 'vue';\n\nexport default {\n  setup() {\n    const tableData = ref([]);\n    const selected = ref([]);\n    const checked = ref([]);\n    const widthMV = ref('100%');\n    const heightMV = ref(300);\n    const showHeaderMV = ref(true);\n    const stripeMV = ref(false);\n    const rowHeightMV = ref(35);\n    const columnWidthMV = ref(80);\n    const useCheckboxMV = ref(true);\n    const checkboxModeMV = ref('multi');\n    const headerCheckMV = ref(true);\n    const checkedRowsMV = ref();\n    const clickedRowMV = ref();\n    const DbClickedRowsMV = ref();\n    const expandColumnMV = ref(0);\n    const menuItems = ref([\n      {\n        text: 'Menu1',\n        click: (param) => {\n          console.log(`[Menu1] Selected Row Data: ${JSON.stringify(param?.selectedRow?.[0]?.data)}`);\n        },\n      }, {\n        text: 'Menu2',\n        click: param => console.log('[Menu2]', param),\n      },\n    ]);\n    const highlightMV = ref(-1);\n    const borderMV = ref('');\n    const iconMV = ref('');\n    const dataIconMV = ref('');\n    const borderItems = ref([\n      {\n        name: 'none',\n        value: 'none',\n      },\n      {\n        name: 'rows',\n        value: 'rows',\n      },\n    ]);\n    const iconItems = ref([\n      {\n        name: 'plus',\n        value: { expand: 'ev-icon-circle-plus', collapse: 'ev-icon-circle-minus' },\n      },\n      {\n        name: 'arrow',\n        value: { expand: 'ev-icon-s-arrow-down', collapse: 'ev-icon-s-arrow-right' },\n      },\n    ]);\n    const dataIconItems = ref([\n      {\n        name: 'folder',\n        value: { parent: 'ev-icon-folder', child: 'ev-icon-document' },\n      },\n      {\n        name: 'database',\n        value: { parent: 'ev-icon-db', child: 'ev-icon-connection' },\n      },\n      {\n        name: 'none',\n        value: { parent: 'none', child: 'none' },\n      },\n    ]);\n    const expandIconMV = computed(() => (iconMV.value ? iconMV.value.expand : ''));\n    const collapseIconMV = computed(() => (iconMV.value ? iconMV.value.collapse : ''));\n    const parentIconMV = computed(() => (dataIconMV.value ? dataIconMV.value.parent : ''));\n    const childIconMV = computed(() => (dataIconMV.value ? dataIconMV.value.child : ''));\n    const resetBorderStyle = () => {\n      borderMV.value = '';\n    };\n    const resetTreeIcon = () => {\n      iconMV.value = '';\n    };\n    const resetDataIcon = () => {\n      dataIconMV.value = '';\n    };\n    const onReset = (type) => {\n        switch (type) {\n          case 'border':\n            borderMV.value = '';\n            break;\n          case 'treeIcon':\n            iconMV.value = '';\n            break;\n          case 'dataIcon':\n            dataIconMV.value = '';\n            break;\n          default:\n            break;\n        }\n    };\n    const onClickCheckbox = (e) => {\n      console.log(`checkbox component click: ${e}`);\n    };\n    const onClickButton = (e) => {\n      console.log(`button component click: ${e}`);\n    };\n    const changeMode = (mode) => {\n      checkboxModeMV.value = mode;\n      checked.value = [];\n    };\n    const onCheckedRow = () => {\n      let checkedRow = '';\n      for (let i = 0; i < checked.value.length; i++) {\n        checkedRow += JSON.stringify(checked.value[i].data);\n      }\n      checkedRowsMV.value = checkedRow;\n    };\n    const onDoubleClickRow = (e) => {\n      const rowData = e.rowData.data;\n      DbClickedRowsMV.value = JSON.stringify(rowData);\n    };\n    const onClickRow = (e) => {\n      const rowData = e.rowData.data;\n      clickedRowMV.value = JSON.stringify(rowData);\n    };\n    const getData = () => {\n      tableData.value = [{\n        id: 'Exem 1',\n        date: '2016-05-01',\n        name: '1',\n        expand: true,\n        children: [{\n          id: 'Exem 2',\n          date: '2016-05-02',\n          name: '2',\n          expand: false,\n          children: [{\n            id: 'Exem 3',\n            date: '2016-05-02',\n            name: '3',\n          }, {\n            id: 'Exem 4',\n            date: '2016-05-02',\n            name: '4',\n            expand: false,\n            children: [{\n              id: 'Exem 5',\n              date: '2016-05-02',\n              name: '5',\n              children: [{\n                id: 'Exem 51',\n                date: '2016-05-02',\n                name: '1251',\n                children: [{\n                  id: 'Exem 52',\n                  date: '2016-05-02',\n                  name: '20000',\n                }],\n              }],\n            }, {\n              id: 'Exem 6',\n              date: '2016-05-02',\n              name: '6',\n            }],\n          }],\n        }, {\n          id: 'Exem 7',\n          date: '2016-05-03',\n          name: '7',\n          children: [{\n            id: 'Exem 8',\n            date: '2016-05-03',\n            name: '8',\n          }, {\n            id: 'Exem 9',\n            date: '2016-05-03',\n            name: '9',\n          }, {\n            id: 'Exem 10',\n            date: '2016-05-03',\n            name: '10',\n          }],\n        }, {\n          id: 'Exem 11',\n          date: '2016-05-04',\n          name: '11',\n        }],\n      }];\n    };\n    const columns = ref([\n      { caption: 'ID', field: 'id', type: 'number' },\n      { caption: 'Date', field: 'date', type: 'string' },\n      {\n        caption: 'Name',\n        field: 'name',\n        type: 'float',\n        summaryType: 'sum',\n        summaryRenderer: 'Sum: {0}',\n        decimal: 1,\n      },\n    ]);\n    const changeExpandTreeColumn = (idx) => {\n      columns.value.forEach((val) => {\n        val.expandColumn = false;\n      });\n      columns.value[idx] = { ...columns.value[idx], expandColumn: true };\n    };\n    const limitItems = ref([\n      {\n        name: '2',\n        value: 2,\n      },\n      {\n        name: '4',\n        value: 4,\n      },\n    ]);\n    const useSelection = reactive({\n      use: true,\n      multiple: true,\n      limitCount: 2,\n    });\n    const pageInfo = reactive({\n      use: true,\n      // isInfinite: true,\n      perPage: 5,\n      total: computed(() => tableData.value.length),\n      useClient: true,\n      showPageInfo: true,\n    });\n\n    getData();\n    return {\n      pageInfo,\n      columns,\n      tableData,\n      selected,\n      checked,\n      widthMV,\n      heightMV,\n      showHeaderMV,\n      stripeMV,\n      rowHeightMV,\n      columnWidthMV,\n      useCheckboxMV,\n      checkboxModeMV,\n      headerCheckMV,\n      checkedRowsMV,\n      clickedRowMV,\n      DbClickedRowsMV,\n      menuItems,\n      highlightMV,\n      borderMV,\n      borderItems,\n      iconMV,\n      iconItems,\n      expandIconMV,\n      collapseIconMV,\n      dataIconMV,\n      dataIconItems,\n      parentIconMV,\n      childIconMV,\n      limitItems,\n      useSelection,\n      expandColumnMV,\n      onClickCheckbox,\n      onClickButton,\n      changeMode,\n      onCheckedRow,\n      onDoubleClickRow,\n      onClickRow,\n      resetBorderStyle,\n      resetTreeIcon,\n      resetDataIcon,\n      onReset,\n      changeExpandTreeColumn,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\" scoped>\n.description {\n  min-width: 200px;\n}\n.form-rows {\n  display: flex;\n  margin-bottom: 5px;\n}\n.form-row {\n  width: 50%;\n}\n.ev-text-field, .ev-input-number, .ev-select {\n  width: 80%;\n}\n.badge {\n  margin-bottom: 2px;\n  margin-right: 5px !important;\n}\n.ev-toggle {\n  margin-right: 10px;\n}\n</style>\n";