export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Common</p>\n    <ev-button\n      type=\"info\"\n      @click=\"showInfo\"\n    >\n      Show Info\n    </ev-button>\n    <ev-button\n      type=\"primary\"\n      @click=\"showSuccess\"\n    >\n      Show Success\n    </ev-button>\n    <ev-button\n      type=\"warning\"\n      @click=\"showWarning\"\n    >\n      Show Warning\n    </ev-button>\n    <ev-button\n      type=\"error\"\n      @click=\"showError\"\n    >\n      Show Error\n    </ev-button>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Position</p>\n    <ev-button\n      @click=\"showTopRight\"\n    >\n      Show Top-Right\n    </ev-button>\n    <ev-button\n      @click=\"showTopLeft\"\n    >\n      Show Top-Left\n    </ev-button>\n    <ev-button\n      @click=\"showBottomRight\"\n    >\n      Show Bottom-Right\n    </ev-button>\n    <ev-button\n      @click=\"showBottomLeft\"\n    >\n      Show Bottom-Left\n    </ev-button>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Title</p>\n    <ev-button\n      @click=\"showTitle\"\n    >\n      Show Title\n    </ev-button>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Hide Close Button</p>\n    <ev-button\n      @click=\"showClose\"\n    >\n      Show Close\n    </ev-button>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Custom Duration</p>\n    <ev-button\n      @click=\"showDuration\"\n    >\n      Show Duration\n    </ev-button>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Icon</p>\n    <ev-button\n      @click=\"showIcon\"\n    >\n      Show Icon\n    </ev-button>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Click Event</p>\n    <ev-button\n      @click=\"showOnClick\"\n    >\n      Show onClick\n    </ev-button>\n    <div class=\"description\">\n      <span class=\"badge\">\n        After click\n      </span>\n      {{ onClickMsg }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Close Event</p>\n    <ev-button\n      @click=\"showOnClose\"\n    >\n      Show onClose\n    </ev-button>\n    <div class=\"description\">\n      <span class=\"badge\">\n        After close\n      </span>\n      {{ onCloseMsg }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">HTML</p>\n    <ev-button\n      @click=\"showHTML\"\n    >\n      Show HTML\n    </ev-button>\n  </div>\n</template>\n\n<script>\nimport { ref, getCurrentInstance } from 'vue';\n\nexport default {\n  setup() {\n    const { ctx } = getCurrentInstance();\n    const showInfo = () => {\n      ctx.$notify('Infomation. This is an Info type notification.');\n    };\n    const showSuccess = () => {\n      ctx.$notify({\n        message: 'Success! This is an Success type notification.',\n        type: 'success',\n      });\n    };\n    const showWarning = () => {\n      ctx.$notify({\n        message: 'Warning. This is an Warning type notification.',\n        type: 'warning',\n      });\n    };\n    const showError = () => {\n      ctx.$notify({\n        message: 'Error. This is an Error type notification.',\n        type: 'error',\n      });\n    };\n\n    const showTopRight = () => {\n      ctx.$notify({\n        title: 'Position: Top-Right',\n        message: 'Position Top Right',\n      });\n    };\n    const showTopLeft = () => {\n      ctx.$notify({\n        title: 'Position: Top-Left',\n        message: 'Position Top Right',\n        position: 'top-left',\n      });\n    };\n    const showBottomRight = () => {\n      ctx.$notify({\n        title: 'Position: Bottom-Right',\n        message: 'Position Bottom Right',\n        position: 'bottom-right',\n      });\n    };\n    const showBottomLeft = () => {\n      ctx.$notify({\n        title: 'Position: Bottom-Left',\n        message: 'Position Bottom Left',\n        position: 'bottom-left',\n      });\n    };\n    const showTitle = () => {\n      ctx.$notify({\n        message: 'You can enter the title.',\n        title: 'Notice!',\n      });\n    };\n    const showClose = () => {\n      ctx.$notify({\n        message: 'You can choose to close properties',\n        showClose: false,\n      });\n    };\n    const showDuration = () => {\n      ctx.$notify({\n        message: 'You can change the duration.',\n        duration: 1500,\n      });\n    };\n    const showIcon = () => {\n      ctx.$notify({\n        message: 'You can add an icon.',\n        title: 'Notice!',\n        iconClass: 'ev-icon-getmore',\n      });\n    };\n    const onClickMsg = ref();\n    const showOnClick = () => {\n      ctx.$notify({\n        message: 'You can set the action after clicking!',\n        showClose: true,\n        onClick: () => {\n          onClickMsg.value = 'You can set the action after clicking!';\n        },\n      });\n    };\n    const onCloseMsg = ref();\n    const showOnClose = () => {\n      ctx.$notify({\n        message: 'You can set the behavior after closing.',\n        showClose: true,\n        onClose: () => {\n          onCloseMsg.value = 'You can set the behavior after closing!';\n        },\n      });\n    };\n    const showHTML = () => {\n      ctx.$notify({\n        message: 'You <i>can enter</i> your message in <strong>html</strong>.',\n        useHTML: true,\n      });\n    };\n    return {\n      showInfo,\n      showSuccess,\n      showWarning,\n      showError,\n      showTopRight,\n      showTopLeft,\n      showBottomRight,\n      showBottomLeft,\n      showTitle,\n      showClose,\n      showDuration,\n      showIcon,\n      onClickMsg,\n      showOnClick,\n      onCloseMsg,\n      showOnClose,\n      showHTML,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\" scoped>\n.case .ev-button {\n  margin-right: 10px;\n}\n</style>\n";