export default "<template>\n  <div class=\"case\">\n    <ev-chart\n      ref=\"chart\"\n      v-model:selectedLabel=\"defaultSelectLabel\"\n      :data=\"chartData1\"\n      :options=\"chartOptions1\"\n      @click=\"onClick\"\n    />\n    <ev-chart\n      v-model:selectedLabel=\"defaultSelectLabel\"\n      :data=\"chartData2\"\n      :options=\"chartOptions2\"\n      @click=\"onClick\"\n    />\n    <ev-chart\n      v-model:selectedLabel=\"defaultSelectLabel\"\n      :data=\"chartData3\"\n      :options=\"chartOptions3\"\n      @click=\"onClick\"\n    />\n    <ev-chart\n      v-model:selectedLabel=\"defaultSelectLabel\"\n      :data=\"chartData4\"\n      :options=\"chartOptions4\"\n      @click=\"onClick\"\n    />\n    <div class=\"description\">\n      <ev-button @click=\"toggleSelectData\">\n        select by v-model\n      </ev-button>\n      <span class=\"left\">\n           v-model:selectedLabel   dataIndex    \n      </span>\n      <br>\n      <br>\n      <ev-toggle v-model=\"isFixedPosTop\" />\n      <span class=\"left\">\n        tip   \n      </span>\n      <br>\n      <br>\n      <ev-button @click=\"toggleOverflow\">\n        Deselect Overflow\n      </ev-button>\n      <span class=\"left\">\n         limit     deselect     \n      </span>\n      <br>\n      <br>\n      <ev-button @click=\"updateData\">\n        Update Data\n      </ev-button>\n      <span class=\"left\">\n             ,  \n      </span>\n      <br>\n      <br>\n      <div>\n        <div class=\"badge yellow\">\n          v-model:selectedLabel\n        </div>\n        {{ defaultSelectLabel }}\n        <br>\n        <br>\n        <div class=\"badge yellow\">\n             (selected)\n        </div>\n        {{ clickedLabel }}\n        <br>\n        <br>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref } from 'vue';\n\nexport default {\n  components: {},\n\n  setup() {\n    const chart = ref(null);\n\n    const chartData1 = ref({\n      series: {\n        series1: { name: 'series#1' },\n        series2: { name: 'series#2' },\n      },\n      labels: ['value1', 'value2', 'value3', 'value4', 'value5'],\n      data: {\n        series1: [100, 150, 51, 150, 350],\n        series2: [100, 150, 51, 150, 450],\n      },\n    });\n    const chartData2 = ref({\n      series: {\n        series1: { name: 'series#1' },\n        series2: { name: 'series#2' },\n      },\n      labels: ['value1', 'value2', 'value3', 'value4', 'value5'],\n      groups: [['series1', 'series2']],\n      data: {\n        series1: [100, 150, 51, 150, 350],\n        series2: [100, 150, 51, 150, 450],\n      },\n    });\n    const chartData3 = ref({\n      series: {\n        series1: { name: 'series#1' },\n        series2: { name: 'series#2' },\n      },\n      labels: ['value1', 'value2', 'value3', 'value4', 'value5'],\n      data: {\n        series1: [100, 150, 51, 150, 350],\n        series2: [100, 150, 51, 150, 450],\n      },\n    });\n    const chartData4 = ref({\n      series: {\n        series1: { name: 'series#1' },\n        series2: { name: 'series#2' },\n      },\n      labels: ['value1', 'value2', 'value3', 'value4', 'value5'],\n      groups: [['series1', 'series2']],\n      data: {\n        series1: [100, 150, 51, 150, 350],\n        series2: [100, 150, 51, 150, 450],\n      },\n    });\n\n    const isFixedPosTop = ref(false);\n\n    const chartOptions1 = ref({\n      type: 'bar',\n      thickness: 0.8,\n      width: '100%',\n      horizontal: false,\n      title: {\n        show: false,\n      },\n      legend: {\n        show: true,\n        position: 'right',\n      },\n      axesX: [{\n        type: 'step',\n        showGrid: false,\n        labelStyle: {\n          fitWidth: true,\n          fitDir: 'left',\n        },\n      }],\n      axesY: [{\n        showAxis: true,\n        type: 'linear',\n        startToZero: true,\n        autoScaleRatio: 0.1,\n        showGrid: false,\n      }],\n      selectLabel: {\n        use: true,\n        limit: 2,\n        useDeselectOverflow: true,\n        showTip: true,\n        fixedPosTop: isFixedPosTop,\n        useApproximateValue: true,\n        tipBackground: '#FF0000',\n        useSeriesOpacity: true,\n        useLabelOpacity: true,\n      },\n      maxTip: {\n        use: true,\n        showTextTip: true,\n        tipStyle: {\n          background: '#FF0000',\n        },\n      },\n    });\n    const chartOptions2 = ref({\n      type: 'bar',\n      thickness: 0.8,\n      width: '100%',\n      horizontal: false,\n      title: {\n        show: false,\n      },\n      legend: {\n        show: true,\n        position: 'right',\n      },\n      axesX: [{\n        type: 'step',\n        showGrid: false,\n        labelStyle: {\n          fitWidth: true,\n          fitDir: 'left',\n        },\n      }],\n      axesY: [{\n        showAxis: true,\n        type: 'linear',\n        startToZero: true,\n        autoScaleRatio: 0.1,\n        showGrid: false,\n      }],\n      selectLabel: {\n        use: true,\n        limit: 2,\n        useDeselectOverflow: true,\n        showTip: true,\n        fixedPosTop: isFixedPosTop,\n        useApproximateValue: true,\n        tipBackground: '#FF0000',\n        useSeriesOpacity: true,\n        useLabelOpacity: true,\n      },\n      maxTip: {\n        use: true,\n        showTextTip: true,\n        tipStyle: {\n          background: '#FF0000',\n        },\n      },\n    });\n    const chartOptions3 = ref({\n      type: 'bar',\n      thickness: 0.8,\n      width: '100%',\n      horizontal: true,\n      title: {\n        show: false,\n      },\n      legend: {\n        show: true,\n        position: 'right',\n      },\n      axesY: [{\n        type: 'step',\n        showGrid: false,\n        labelStyle: {\n          fitWidth: true,\n          fitDir: 'left',\n        },\n      }],\n      axesX: [{\n        showAxis: true,\n        type: 'linear',\n        startToZero: true,\n        autoScaleRatio: 0.1,\n        showGrid: false,\n      }],\n      selectLabel: {\n        use: true,\n        limit: 2,\n        useDeselectOverflow: true,\n        showTip: true,\n        fixedPosTop: isFixedPosTop,\n        useApproximateValue: true,\n        tipBackground: '#FF0000',\n        useSeriesOpacity: true,\n        useLabelOpacity: true,\n      },\n      maxTip: {\n        use: true,\n        showTextTip: true,\n        tipStyle: {\n          background: '#FF0000',\n        },\n      },\n    });\n    const chartOptions4 = ref({\n      type: 'bar',\n      thickness: 0.8,\n      width: '100%',\n      horizontal: true,\n      title: {\n        show: false,\n      },\n      legend: {\n        show: true,\n        position: 'right',\n      },\n      axesY: [{\n        type: 'step',\n        showGrid: false,\n        labelStyle: {\n          fitWidth: true,\n          fitDir: 'left',\n        },\n      }],\n      axesX: [{\n        showAxis: true,\n        type: 'linear',\n        startToZero: true,\n        autoScaleRatio: 0.1,\n        showGrid: false,\n      }],\n      selectLabel: {\n        use: true,\n        limit: 2,\n        useDeselectOverflow: true,\n        showTip: true,\n        fixedPosTop: isFixedPosTop,\n        useApproximateValue: true,\n        tipBackground: '#FF0000',\n        useSeriesOpacity: true,\n        useLabelOpacity: true,\n      },\n      maxTip: {\n        use: true,\n        showTextTip: true,\n        tipStyle: {\n          background: '#FF0000',\n        },\n      },\n    });\n\n    const clickedLabel = ref();\n    const onClick = ({ selected }) => {\n      clickedLabel.value = selected;\n    };\n\n    const defaultSelectItem = ref({\n      seriesID: 'series1',\n      dataIndex: 1,\n    });\n    const defaultSelectLabel = ref({\n      dataIndex: [0],\n    });\n\n    const toggleSelectData = () => {\n      const arr = defaultSelectLabel.value.dataIndex;\n      const newIndex = (arr.pop() + 1) % 9;\n      if (!arr.includes(newIndex)) {\n        arr.push(newIndex);\n      }\n    };\n\n    const toggleOverflow = () => {\n      const b = chartOptions1.value.selectLabel.useDeselectOverflow;\n      chartOptions1.value.selectLabel.useDeselectOverflow = !b;\n      chartOptions2.value.selectLabel.useDeselectOverflow = !b;\n      chartOptions3.value.selectLabel.useDeselectOverflow = !b;\n      chartOptions4.value.selectLabel.useDeselectOverflow = !b;\n    };\n\n    const updateData = () => {\n      const getRandArr = count => Array(count)\n        .fill(0).map(() => Math.ceil(Math.random() * 100));\n\n      const chartList = [chartData1, chartData2, chartData3, chartData4];\n      chartList.forEach((c) => {\n        const seriesList = ['series1', 'series2'];\n        seriesList.forEach((sId) => {\n          c.value.data[sId] = getRandArr(5);\n        });\n      });\n    };\n\n\n    return {\n      chart,\n      chartData1,\n      chartData2,\n      chartData3,\n      chartData4,\n      isFixedPosTop,\n      chartOptions1,\n      chartOptions2,\n      chartOptions3,\n      chartOptions4,\n      clickedLabel,\n      defaultSelectItem,\n      defaultSelectLabel,\n      onClick,\n      toggleSelectData,\n      toggleOverflow,\n      updateData,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\" scoped>\n  .description {\n    position: relative;\n  }\n  .left {\n    position: absolute;\n    left: 160px;\n    padding-top: 10px;\n  }\n</style>\n";