export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Common</p>\n    <ev-checkbox\n      v-model=\"checkVal1\"\n    >\n      Checkbox\n    </ev-checkbox>\n    <div class=\"description\">\n      <button\n        class=\"btn\"\n        @click=\"clickButton\"\n      >\n        click to change the check value\n      </button>\n      &nbsp; {{ checkVal1 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Use Change Event</p>\n    <ev-checkbox\n      v-model=\"checkVal2\"\n      @change=\"changeCheck\"\n    >\n      Single Checkbox\n    </ev-checkbox>\n    <div class=\"description\">\n      <span class=\"badge\">\n        Value in changeEvent\n      </span>\n      {{ checkResult2.value }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Disabled</p>\n    <ev-checkbox\n      v-model=\"checkVal1\"\n      :disabled=\"isDisable\"\n    >\n      DISABLED\n    </ev-checkbox>\n    <div class=\"description\">\n      <button\n        class=\"btn\"\n        @click=\"toggleDisable\"\n      >\n        toggle to disable\n      </button>\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Indeterminate</p>\n    <ev-checkbox\n      v-model=\"checkVal3\"\n      :indeterminate=\"isIndeterminate\"\n    >\n      INDETERMINATE\n    </ev-checkbox>\n    <div class=\"description\">\n      <button\n        class=\"btn\"\n        @click=\"toggleIndeterminate\"\n      >\n        toggle to indeterminate\n      </button>\n      &nbsp; {{ isIndeterminate }}\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref, reactive } from 'vue';\n\nexport default {\n  setup() {\n    const checkVal1 = ref(false);\n    const clickButton = () => {\n      checkVal1.value = !checkVal1.value;\n    };\n\n    const checkVal2 = ref(true);\n    const checkResult2 = reactive({\n      value: '',\n    });\n    const changeCheck = (value, e) => {\n      checkResult2.value = value;\n      checkResult2.e = e;\n    };\n\n    const isDisable = ref(true);\n    const toggleDisable = () => {\n      isDisable.value = !isDisable.value;\n    };\n\n    const checkVal3 = ref(false);\n    const isIndeterminate = ref(false);\n    const toggleIndeterminate = () => {\n      isIndeterminate.value = !isIndeterminate.value;\n    };\n\n    return {\n      checkVal1,\n      clickButton,\n\n      checkVal2,\n      checkResult2,\n      changeCheck,\n\n      isDisable,\n      toggleDisable,\n\n      checkVal3,\n      isIndeterminate,\n      toggleIndeterminate,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\">\n</style>\n";