export default "<template>\n  <div class=\"case\">\n    <ev-chart\n      v-model:selectedItem=\"defaultSelectItem\"\n      :data=\"chartData\"\n      :options=\"chartOptions\"\n      @click=\"onClick\"\n      @dbl-click=\"onDblClick\"\n    />\n    <div class=\"description\">\n      <ev-button\n          @click=\"toggleSelectData\">\n        select toggle 1 - 2\n      </ev-button>\n      <br><br>\n      <div>\n        <div class=\"badge yellow\">\n            v-model\n        </div>\n        {{ defaultSelectItem }}\n        <br><br>\n        <div class=\"badge yellow\">\n           \n        </div>\n        {{ clickedInfo }}\n        <br><br>\n        <div class=\"badge yellow\">\n            \n        </div>\n        {{ dblClickedInfo }}\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { reactive, ref } from 'vue';\n\n  export default {\n    setup() {\n      const chartData = reactive({\n        series: {\n          series1: { name: 'series#1' },\n          series2: { name: 'series#2' },\n          series3: { name: 'series#3' },\n        },\n        data: {\n          series1: [10],\n          series2: [20],\n          series3: [70],\n        },\n      });\n\n      const chartOptions = {\n        type: 'pie',\n        width: '100%',\n        height: '50%',\n        title: {\n          text: 'Chart Title',\n          show: true,\n        },\n        legend: {\n          show: true,\n          position: 'right',\n        },\n        selectItem: {\n          use: true,\n        },\n      };\n\n      const clickedInfo = ref(\"''\");\n      const onClick = (target) => {\n        clickedInfo.value = target;\n      };\n\n      const dblClickedInfo = ref(\"''\");\n      const onDblClick = (target) => {\n        dblClickedInfo.value = target;\n      };\n\n      const defaultSelectItem = ref({\n        seriesID: 'series1',\n      });\n\n      const toggleSelectData = () => {\n        const seriesID = defaultSelectItem.value.seriesID;\n        defaultSelectItem.value.seriesID = seriesID === 'series1' ? 'series2' : 'series1';\n      };\n\n      return {\n        chartData,\n        chartOptions,\n        clickedInfo,\n        dblClickedInfo,\n        defaultSelectItem,\n        onClick,\n        onDblClick,\n        toggleSelectData,\n      };\n    },\n  };\n</script>\n\n<style lang=\"scss\" scoped>\n  .case {\n    height: 100%;\n  }\n</style>\n";