export default "<template>\n  <div class=\"case\">\n    <ev-grid\n      v-model:selected=\"selected\"\n      v-model:checked=\"checked\"\n      :columns=\"columns\"\n      :rows=\"tableData\"\n      :width=\"widthMV\"\n      :height=\"heightMV\"\n      :option=\"{\n        adjust: adjustMV,\n        showHeader: showHeaderMV,\n        rowHeight: rowHeightMV,\n        columnWidth: columnWidthMV,\n        useFilter: false,\n        useCheckbox: {\n          use: useCheckboxMV,\n          mode: checkboxModeMV,\n          headerCheck: headerCheckMV,\n        },\n        customContextMenu: menuItems,\n        style: {\n          stripe: stripeMV,\n          border: borderMV,\n        },\n      }\"\n      @check-row=\"onCheckedRow\"\n      @check-all=\"onCheckedRow\"\n      @click-row=\"onClickRow\"\n      @dblclick-row=\"onDoubleClickRow\"\n    >\n      <!-- renderer start -->\n      <template #gridButton>\n        <ev-button\n          type=\"ghost\"\n          size=\"small\"\n        >\n          Delete\n        </ev-button>\n        <ev-button size=\"small\">\n          Edit\n        </ev-button>\n      </template>\n      <template #check=\"{ item }\">\n        <ev-checkbox\n          v-model=\"item.row[2][item.column.index]\"\n          label=\"check\"\n          @click.stop=\"\"\n          @dblclick.stop=\"\"\n        />\n      </template>\n      <template #select=\"{ item }\">\n        <ev-select\n          v-model=\"item.row[2][item.column.index]\"\n          :items=\"[\n              {\n                name: 'a',\n                value: 'a',\n              }, {\n                name: 'b',\n                value: 'b',\n              }, {\n                name: 'c',\n                value: 'c',\n              },\n            ]\"\n          placeholder=\"Please select value.\"\n          @click.stop=\"\"\n          @dblclick.stop=\"\"\n        />\n      </template>\n      <template #slide=\"{ item }\">\n        <ev-slider\n          v-model=\"item.row[2][item.column.index]\"\n          range\n          readonly\n          :mark=\"{\n            33.33: 'W',\n            66.66: 'C',\n          }\"\n          :color=\"['#3C81F6', '#FADE4C', '#FF470E']\"\n          :show-tooltip=\"false\"\n        />\n      </template>\n      <template #custom=\"{ item }\">\n        <span\n          :class=\"getStateClass(item.row[2][item.column.index])\"\n        >\n          <span class=\"v-chip__content\"> {{ item.row[2][item.column.index] }} </span>\n        </span>\n      </template>\n      <template #inputNumber=\"{ item }\">\n        <ev-input-number\n          v-model=\"item.row[2][item.column.index]\"\n          :max=\"100\"\n          :min=\"0\"\n          @click.stop=\"onInputNumberClick\"\n          @dblclick.stop=\"\"\n        />\n      </template>\n    </ev-grid>\n  </div>\n</template>\n\n<script>\nimport { ref } from 'vue';\n\nexport default {\n  setup() {\n    const tableData = ref([]);\n    const selected = ref([]);\n    const checked = ref([]);\n    const widthMV = ref('100%');\n    const heightMV = ref(300);\n    const adjustMV = ref(true);\n    const showHeaderMV = ref(true);\n    const stripeMV = ref(false);\n    const rowHeightMV = ref(35);\n    const columnWidthMV = ref(80);\n    const useCheckboxMV = ref(true);\n    const checkboxModeMV = ref('multi');\n    const headerCheckMV = ref(true);\n    const checkedRowsMV = ref();\n    const clickedRowMV = ref();\n    const DbClickedRowsMV = ref();\n    const menuItems = ref([\n      {\n        text: 'Menu1',\n        click: param => console.log(`[Menu1] Selected Row Data: ${param?.selectedRow}`),\n      }, {\n        text: 'Menu2',\n        click: param => console.log('[Menu2]', param),\n      },\n    ]);\n    const borderMV = ref('');\n    const columns = ref([\n      {\n        caption: 'Check',\n        field: 'check',\n        type: 'boolean',\n      },\n      {\n        caption: 'Select',\n        field: 'select',\n        type: 'string',\n      },\n      {\n        caption: 'Slide',\n        field: 'slide',\n        type: 'string',\n        sortable: false,\n      },\n      {\n        caption: 'InputNumber',\n        field: 'inputNumber',\n        type: 'number',\n        width: 140,\n      },\n      {\n        caption: 'Custom',\n        field: 'custom',\n        type: 'number',\n        align: 'center',\n      },\n      {\n        caption: '',\n        field: 'gridButton',\n        type: 'boolean',\n        width: 120,\n      },\n    ]);\n    const onCheckedRow = () => {\n      let checkedRow = '';\n      for (let i = 0; i < checked.value.length; i++) {\n        checkedRow += JSON.stringify(checked.value[i]);\n      }\n      checkedRowsMV.value = checkedRow;\n    };\n    const onDoubleClickRow = (e) => {\n      DbClickedRowsMV.value = `${e.rowData}`;\n    };\n    const onClickRow = (e) => {\n      clickedRowMV.value = `${e.rowData}`;\n    };\n    const getData = (count, startIndex) => {\n      const state = ['a', 'b', 'c'];\n      const temp = [];\n      for (let ix = startIndex; ix < startIndex + count; ix++) {\n        temp.push([\n          true, // check\n          state[ix % 3], // select\n          [33.33, 66.66], // slide\n          10, // inputNumber\n          Math.floor(Math.random() * (99 - 10 + 1)) + 10, // custom\n          '',\n        ]);\n      }\n      tableData.value = temp;\n    };\n    const onInputNumberClick = () => {\n      console.log('On click InputNumber');\n    };\n    const getStateClass = (value) => {\n      let stateColor = 'green';\n      if (value >= 70) {\n        stateColor = 'red';\n      } else if (value >= 50) {\n        stateColor = 'yellow';\n      }\n      return {\n        'v-chip': true,\n        'v-size--default': true,\n        [stateColor]: true,\n      };\n    };\n\n    getData(7, 0);\n    return {\n      columns,\n      tableData,\n      selected,\n      checked,\n      widthMV,\n      heightMV,\n      adjustMV,\n      showHeaderMV,\n      stripeMV,\n      rowHeightMV,\n      columnWidthMV,\n      useCheckboxMV,\n      checkboxModeMV,\n      headerCheckMV,\n      checkedRowsMV,\n      clickedRowMV,\n      DbClickedRowsMV,\n      menuItems,\n      borderMV,\n      onCheckedRow,\n      onDoubleClickRow,\n      onClickRow,\n      onInputNumberClick,\n      getStateClass,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\" scoped>\n.description {\n  min-width: 200px;\n}\n.form-rows {\n  display: flex;\n  margin-bottom: 5px;\n}\n.form-row {\n  width: 50%;\n}\n.ev-text-field, .ev-input-number, .ev-select {\n  width: 80%;\n}\n.badge {\n  margin-bottom: 2px;\n  margin-right: 5px !important;\n}\n.ev-toggle {\n  margin-right: 10px;\n}\n.v-chip {\n  display: inline-flex;\n  position: relative;\n  height: 32px;\n  padding: 0 12px;\n  align-items: center;\n  cursor: default;\n  color: #FFFFFF;\n  line-height: 20px;\n  max-width: 100%;\n  outline: none;\n  overflow: hidden;\n  text-decoration: none;\n  transition-duration: .28s;\n  transition-property: box-shadow, opacity;\n  transition-timing-function: cubic-bezier(.4,0,.2,1);\n  vertical-align: middle;\n  white-space: nowrap;\n  border-radius: 16px;\n  font-size: 14px;\n\n  &.green {\n    background-color: #4CAF50;\n    border-color: #4CAF50;\n  }\n  &.yellow {\n    background-color: #F7DF6A;\n    border-color: #F7DF6A;\n  }\n  &.red {\n    background-color: #FF4949;\n    border-color: #FF4949;\n  }\n}\n.v-chip .v-chip__content {\n  display: inline-flex;\n  height: 100%;\n  align-items: center;\n  max-width: 100%;\n}\n</style>\n";