export default "<template>\n  <div\n    ref=\"zoomRef\"\n    class=\"case\"\n  >\n    <ev-chart\n      v-model:zoomStartIdx=\"zoomStartIdx\"\n      v-model:zoomEndIdx=\"zoomEndIdx\"\n      :data=\"chartData\"\n      :options=\"chartOptions\"\n    />\n\n    <div class=\"description\">\n      <p class=\"case-title\"> Start / End   (   )</p>\n      <div class=\"input-wrapper\">\n        <ev-input-number\n          v-model=\"zoomStartIdx\"\n        />\n        <ev-input-number\n          v-model=\"zoomEndIdx\"\n        />\n      </div>\n      <p class=\"case-title\">  </p>\n      <ev-input-number\n        v-model=\"bufferMemoryCnt\"\n        :min=\"1\"\n        :max=\"1000\"\n      />\n      <br/>\n      <br/>\n      <ev-button @click=\"onUpdateChartData\"> </ev-button>\n      <br/>\n      <br/>\n      <span class=\"toggle-label\"> </span>\n      <ev-toggle v-model=\"isShowToolbar\"/>\n      <br/>\n      <br/>\n      <span class=\"toggle-label\"> </span>\n      <ev-toggle v-model=\"isShowToggleLegend\"/>\n      <br/>\n      <br/>\n      <span class=\"toggle-label\"> </span>\n      <ev-toggle v-model=\"isExpandChartArea\"/>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { onMounted, reactive, ref, watch } from 'vue';\nimport dayjs from 'dayjs';\n\nexport default {\n  setup() {\n    const isShowToggleLegend = ref(false);\n    const isExpandChartArea = ref(false);\n    const isShowToolbar = ref(true);\n    const bufferMemoryCnt = ref(10);\n    const zoomStartIdx = ref(0);\n    const zoomEndIdx = ref(0);\n    const zoomRef = ref();\n    let timeValue = dayjs().format('YYYY-MM-DD HH:mm:ss');\n\n    const chartData = reactive({\n      series: {\n        series11: { name: 'series#11' },\n        series22: { name: 'series#22' },\n      },\n      labels: [],\n      data: {\n        series11: [],\n        series22: [],\n      },\n    });\n\n    const chartOptions = reactive({\n      type: 'line',\n      width: '100%',\n      title: {\n        text: 'Chart Title1',\n        show: true,\n      },\n      legend: {\n        show: false,\n        position: 'right',\n      },\n      axesX: [{\n        type: 'time',\n        showGrid: false,\n        timeFormat: 'HH:mm:ss',\n        interval: 'second',\n      }],\n      axesY: [{\n        type: 'linear',\n        showGrid: false,\n        startToZero: true,\n        autoScaleRatio: 0.1,\n      }],\n      zoom: {\n        bufferMemoryCnt: 5,\n        keepZoomStatus: true,\n        toolbar: {\n          show: isShowToolbar,\n          items: {\n            previous: {\n              icon: 'ev-icon-allow2-left',\n              size: 'medium',\n              title: 'Previous',\n            },\n            latest: {\n              icon: 'ev-icon-allow2-right',\n              size: 'medium',\n              title: 'Latest',\n            },\n            reset: {\n              icon: 'ev-icon-redo',\n              size: 'medium',\n              title: 'Reset',\n            },\n            dragZoom: {\n              icon: 'ev-icon-zoomin',\n              size: 'medium',\n              title: 'Drag Zoom',\n            },\n          },\n        },\n      },\n    });\n\n    const addRandomChartData = () => {\n      timeValue = dayjs(timeValue).add(1, 'second');\n      chartData.labels.push(dayjs(timeValue));\n\n      Object.values(chartData.data).forEach((seriesData) => {\n        seriesData.push(Math.floor(Math.random() * ((5000 - 5) + 1)) + 5);\n      });\n    };\n\n    onMounted(() => {\n      for (let ix = 0; ix < 300; ix++) {\n        addRandomChartData();\n      }\n    });\n\n    const onUpdateChartData = () => {\n      const init = (data) => {\n        data.labels.length = 0;\n        const seriesKeyArr = Object.keys(data.data);\n\n        for (let i = 0; i < seriesKeyArr.length; i++) {\n          const series = seriesKeyArr[i];\n\n          data.data[series].length = 0;\n        }\n      };\n\n      init(chartData);\n\n      for (let ix = 0; ix < 300; ix++) {\n        addRandomChartData();\n      }\n    };\n\n    watch(isShowToggleLegend, (isShow) => {\n      chartOptions.legend.show = isShow;\n    });\n\n    watch(isExpandChartArea, (isExpand) => {\n      const viewElement = zoomRef.value.parentElement;\n\n      if (isExpand) {\n        viewElement.style.width = '100%';\n        viewElement.nextSibling.style.display = 'none';\n      } else {\n        viewElement.style.width = '50%';\n        viewElement.nextSibling.style.display = 'initial';\n      }\n    });\n\n    watch(bufferMemoryCnt, (cnt) => {\n      chartOptions.zoom.bufferMemoryCnt = cnt;\n    }, { immediate: true });\n\n    return {\n      chartData,\n      chartOptions,\n      isShowToggleLegend,\n      isExpandChartArea,\n      zoomRef,\n      zoomStartIdx,\n      zoomEndIdx,\n      bufferMemoryCnt,\n      isShowToolbar,\n      onUpdateChartData,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\" scoped>\n.input-wrapper {\n  display: flex;\n\n  .ev-input-number {\n    width: 100%;\n  }\n}\n</style>\n";