export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Common</p>\n    <ev-button\n      type=\"info\"\n      @click=\"showInfo\"\n    >\n      Show Info\n    </ev-button>\n    <ev-button\n      type=\"primary\"\n      @click=\"showSuccess\"\n    >\n      Show Success\n    </ev-button>\n    <ev-button\n      type=\"warning\"\n      @click=\"showWarning\"\n    >\n      Show Warning\n    </ev-button>\n    <ev-button\n      type=\"error\"\n      @click=\"showError\"\n    >\n      Show Error\n    </ev-button>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Title</p>\n    <ev-button\n      @click=\"showTitle\"\n    >\n      Show Title\n    </ev-button>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Icon</p>\n    <ev-button\n      @click=\"showIcon\"\n    >\n      Show Icon\n    </ev-button>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">On/Off Element And Focusable</p>\n    <ev-button\n      @click=\"showConfirm\"\n    >\n      Hide Cancel\n    </ev-button>\n    <ev-button\n      @click=\"showCancel\"\n    >\n      Hide Confirm\n    </ev-button>\n    <ev-button\n      @click=\"showClose\"\n    >\n      Hide Close\n    </ev-button>\n    <ev-button\n      @click=\"showPlain\"\n    >\n      Hide Cancel, Confirm\n    </ev-button>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Custom Button Text</p>\n    <ev-button\n      @click=\"showCustomText\"\n    >\n      Custom Text\n    </ev-button>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">After Closing</p>\n    <ev-button\n      @click=\"showOnClose\"\n    >\n      Show onClose\n    </ev-button>\n    <div class=\"description\">\n      <span class=\"badge\">\n        After close behavior\n      </span>\n      {{ onCloseMsg }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">HTML</p>\n    <ev-button\n      @click=\"showHTML\"\n    >\n      Show HTML\n    </ev-button>\n  </div>\n</template>\n\n<script>\nimport { ref, getCurrentInstance } from 'vue';\n\nexport default {\n  setup() {\n    const { ctx } = getCurrentInstance();\n    const showInfo = () => {\n      ctx.$messagebox({\n        message: 'Success! This is an Success type message.',\n        type: 'info',\n      });\n    };\n    const showSuccess = () => {\n      ctx.$messagebox({\n        message: 'Success! This is an Success type message.',\n        type: 'success',\n      });\n    };\n    const showWarning = () => {\n      ctx.$messagebox({\n        message: 'Warning. This is an Warning type message.',\n        type: 'warning',\n      });\n    };\n    const showError = () => {\n      ctx.$messagebox({\n        message: 'Error. This is an Error type message.',\n        type: 'error',\n      });\n    };\n    const showTitle = () => {\n      ctx.$messagebox({\n        title: 'Title!',\n        message: 'You can enter the title.',\n        iconClass: 'ev-icon-getmore',\n      });\n    };\n    const showIcon = () => {\n      ctx.$messagebox({\n        message: 'You can add an icon.',\n        iconClass: 'ev-icon-getmore',\n      });\n    };\n    const showConfirm = () => {\n      ctx.$messagebox({\n        title: 'On/Off Element And Focusable',\n        message: 'Hide cancel button.',\n        iconClass: 'ev-icon-getmore',\n        showCancelBtn: false,\n        focusable: true,\n        onClose: type => console.log(`[ type = '${type}' ] Hide Cancel`),\n      });\n    };\n    const showCancel = () => {\n      ctx.$messagebox({\n        title: 'On/Off Element And Focusable',\n        message: 'Hide confirm button.',\n        iconClass: 'ev-icon-getmore',\n        showConfirmBtn: false,\n        focusable: true,\n        onClose: type => console.log(`[ type = '${type}' ] Hide Confirm`),\n      });\n    };\n    const showClose = () => {\n      ctx.$messagebox({\n        title: 'On/Off Element And Focusable',\n        message: 'Hide close button.',\n        iconClass: 'ev-icon-getmore',\n        showClose: false,\n        focusable: true,\n        onClose: type => console.log(`[ type = '${type}' ] Hide Close`),\n      });\n    };\n    const showPlain = () => {\n      ctx.$messagebox({\n        title: 'On/Off Element And Focusable',\n        message: 'Hide cancel, confirm button.',\n        iconClass: 'ev-icon-getmore',\n        showCancelBtn: false,\n        showConfirmBtn: false,\n        focusable: true,\n        onClose: type => console.log(`[ type = '${type}' ] Hide Cancel, Confirm`),\n      });\n    };\n    const showCustomText = () => {\n      ctx.$messagebox({\n        title: 'Custom Button Text',\n        message: 'You can modify the name of the button.',\n        iconClass: 'ev-icon-getmore',\n        confirmBtnText: 'Custom Confirm',\n        cancelBtnText: 'Custom Cancel',\n      });\n    };\n    const onCloseMsg = ref();\n    const showOnClose = () => {\n      ctx.$messagebox({\n        title: 'After Close Event',\n        message: 'You can set the behavior after closing.',\n        onClose: (type) => {\n          onCloseMsg.value = `[ type = '${type}' ] You can set the behavior after closing!`;\n        },\n      });\n    };\n    const showHTML = () => {\n      ctx.$messagebox({\n        title: 'Use HTML',\n        message: 'You <i>can enter</i> your message in <strong>html</strong>.',\n        useHTML: true,\n      });\n    };\n    return {\n      showInfo,\n      showSuccess,\n      showWarning,\n      showError,\n      showTitle,\n      showIcon,\n      showConfirm,\n      showCancel,\n      showClose,\n      showPlain,\n      showCustomText,\n      onCloseMsg,\n      showOnClose,\n      showHTML,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\" scoped>\n.case .ev-button {\n  margin-right: 10px;\n}\n</style>\n";