export default "<template>\n  <div class=\"case\">\n    <ev-chart\n      :data=\"chartData\"\n      :options=\"chartOptions\"\n    />\n    <div class=\"description\">\n      <div class=\"row\">\n        <div class=\"row-item\">\n          <span class=\"item-title\">\n            doughnutHoleSize\n          </span>\n          <ev-input-number\n            v-model=\"doughnutHoleSize\"\n            :step=\"0.1\"\n            :precision=\"1\"\n            :min=\"0\"\n            :max=\"0.8\"\n          />\n        </div>\n        <div class=\"row-item\">\n          <span class=\"item-title\">\n            fontSize\n          </span>\n          <ev-input-number\n              v-model=\"fontSize\"\n              :step=\"1\"\n              :precision=\"0\"\n              :min=\"1\"\n              :max=\"30\"\n          />\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { reactive, ref } from 'vue';\n\n  export default {\n    setup() {\n      const doughnutHoleSize = ref(0);\n      const fontSize = ref(12);\n\n      const formatter = ({ value, percentage }) => `${(+percentage).toFixed(0)}% (${value})`;\n\n      const chartData = reactive({\n        series: {\n          series1: { name: 'series#1', showValue: { use: true, fontSize, formatter } },\n          series2: { name: 'series#2', showValue: { use: true, fontSize, formatter } },\n          series3: { name: 'series#3', showValue: { use: true, fontSize, formatter } },\n        },\n        data: {\n          series1: [1],\n          series2: [4],\n          series3: [9],\n        },\n      });\n\n      const chartOptions = reactive({\n        type: 'pie',\n        width: '100%',\n        title: {\n          text: 'Chart Title',\n          show: true,\n        },\n        legend: {\n          show: true,\n          position: 'right',\n        },\n        doughnutHoleSize,\n        tooltip: {\n          use: true,\n          formatter,\n        },\n      });\n\n      return {\n        chartData,\n        chartOptions,\n        doughnutHoleSize,\n        fontSize,\n      };\n    },\n  };\n</script>\n\n<style lang=\"scss\" scoped>\n  .row {\n    display: flex;\n    margin-top: 15px;\n\n    .row-item {\n      display: flex;\n      margin-right: 30px;\n\n      .item-title {\n        line-height: 33px;\n        margin-right: 3px;\n        min-width: 50px;\n      }\n\n      .ev-text-field, .ev-input-number, .ev-select {\n        width: auto;\n      }\n    }\n  }\n</style>\n";