export default "<template>\n  <div class=\"case\">\n    <ev-chart\n      :data=\"chartData\"\n      :options=\"chartOptions\"\n    />\n    <div class=\"description\">\n      <div class=\"row\">\n        <div class=\"row-item\">\n          <span class=\"item-title\">\n            Max Value\n          </span>\n            <ev-input-number\n              v-model=\"maxValue\"\n              class=\"component\"\n              :min=\"1\"\n            />\n        </div>\n        <div class=\"row-item\">\n          <span class=\"item-title\">\n            Display Overflow\n          </span>\n          <ev-checkbox\n            v-model=\"displayOverflow\"\n            class=\"check-box\"\n          />\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref, reactive, computed } from 'vue';\nimport EvCheckbox from '@/components/checkbox/Checkbox';\n\n  export default {\n    components: { EvCheckbox },\n    setup() {\n      const chartData = reactive({\n        series: {\n          series1: { name: 'series#1' },\n          series2: { name: 'series#2' },\n        },\n        data: {\n          series1: [\n            { x: 134, y: 51 }, { x: 67, y: 59 }, { x: 19, y: 49 },\n            { x: 15, y: 63 }, { x: 55, y: 53 }, { x: 161, y: 51 },\n            { x: 167, y: 59 }, { x: 159, y: 49 }, { x: 157, y: 63 },\n            { x: 155, y: 53 }, { x: 170, y: 59 }, { x: 159, y: 47 },\n            { x: 166, y: 69 }, { x: 176, y: 66 }, { x: 160, y: 75 },\n            { x: 172, y: 55 }, { x: 170, y: 54 }, { x: 172, y: 62 },\n            { x: 153, y: 42 }, { x: 160, y: 50 }, { x: 147, y: 49 },\n            { x: 168, y: 49 }, { x: 175, y: 73 }, { x: 157, y: 47 },\n            { x: 167, y: 68 }, { x: 159, y: 50 }, { x: 175, y: 82 },\n            { x: 166, y: 57 }, { x: 176, y: 87 }, { x: 170, y: 72 },\n          ],\n          series2: [\n            { x: 9, y: 51 }, { x: 72, y: 59 }, { x: 0, y: 49 },\n            { x: 57, y: 63 }, { x: 15, y: 53 }, { x: 174, y: 65 },\n            { x: 175, y: 71 }, { x: 200, y: 80, color: '#FF0000' }, { x: 186, y: 72 },\n            { x: 187, y: 78 }, { x: 181, y: 74 }, { x: 184, y: 86 },\n            { x: 184, y: 78 }, { x: 175, y: 62 }, { x: 184, y: 81 },\n            { x: 180, y: 76 }, { x: 177, y: 83 }, { x: 192, y: 90, color: '#FF0000' },\n            { x: 176, y: 74 }, { x: 174, y: 71 }, { x: 184, y: 79 },\n            { x: 192, y: 93, color: '#FF0000' }, { x: 171, y: 70 }, { x: 173, y: 72 },\n            { x: 176, y: 85 }, { x: 176, y: 78 }, { x: 180, y: 77 },\n            { x: 172, y: 66 }, { x: 176, y: 86 }, { x: 173, y: 81 },\n          ],\n        },\n      });\n\n      const maxValue = ref(60);\n      const displayOverflow = ref(true);\n\n      const chartOptions = computed(() => ({\n        type: 'scatter',\n        width: '100%',\n        height: '100%',\n        padding: { top: 20, right: 2, bottom: 4, left: 2 },\n        axesX: [{\n          type: 'linear',\n          showAxis: true,\n          startToZero: true,\n          autoScaleRatio: null,\n          showGrid: true,\n          axisLineColor: '#C9CFDC',\n          gridLineColor: '#C9CFDC',\n          interval: null,\n          labelStyle: {\n            show: true,\n            fontSize: 12,\n            color: '#25262E',\n            fontFamily: 'Roboto',\n            fitWidth: false,\n            fitDir: 'right',\n          },\n          plotLines: [],\n          plotBands: [],\n          formatter: null,\n        }],\n        axesY: [{\n          type: 'linear',\n          showAxis: true,\n          startToZero: false,\n          autoScaleRatio: null,\n          showGrid: true,\n          axisLineColor: '#C9CFDC',\n          gridLineColor: '#C9CFDC',\n          interval: 1,\n          labelStyle: {\n            show: true,\n            fontSize: 12,\n            color: '#25262E',\n            fontFamily: 'Roboto',\n            fitWidth: false,\n            fitDir: 'right',\n          },\n          plotLines: [],