export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Common</p>\n    <ev-slider\n      v-model=\"modelValue1\"\n    />\n    <div class=\"description\">\n      <span class=\"badge dark\">\n        Slider Value\n        {{ modelValue1 }}\n      </span>\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Readonly</p>\n    <ev-slider\n      v-model=\"modelValue1\"\n      readonly\n    />\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Disabled</p>\n    <ev-slider\n      v-model=\"modelValue1\"\n      disabled\n    />\n    <ev-slider\n      v-model=\"modelValue1\"\n      disabled\n      show-step\n    />\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Maximum & Minimum Value</p>\n    <ev-slider\n      v-model=\"modelValue2\"\n      :min=\"2\"\n      :max=\"10\"\n    />\n    <div class=\"description\" style=\"margin-bottom: 20px;\">\n      <span class=\"badge yellow\">\n        min=2\n      </span>\n      <span class=\"badge yellow\">\n        max=10\n      </span>\n      <span class=\"badge dark\">\n        Slider Value\n        {{ modelValue2 }}\n      </span>\n    </div>\n    <ev-slider\n      v-model=\"modelValue3\"\n      :min=\"-20\"\n      :max=\"10\"\n    />\n    <div class=\"description\">\n      <span class=\"badge yellow\">\n        min=-20\n      </span>\n      <span class=\"badge yellow\">\n        max=10\n      </span>\n      <span class=\"badge dark\">\n        Slider Value\n        {{ modelValue3 }}\n      </span>\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Step</p>\n    <ev-slider\n      v-model=\"modelValue4\"\n      :max=\"45\"\n      :min=\"1\"\n      :step=\"9\"\n      show-step\n    />\n    <div class=\"description\">\n      <span class=\"badge yellow\">\n        step=9\n      </span>\n      <span class=\"badge\">\n        max=45\n      </span>\n      <span class=\"badge\">\n        min=1\n      </span>\n      <span class=\"badge dark\">\n        Slider Value\n        {{ modelValue4 }}\n      </span>\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Hide Tooltip</p>\n    <ev-slider\n      v-model=\"modelValue4\"\n      :max=\"45\"\n      :min=\"1\"\n      :step=\"9\"\n      :show-tooltip=\"false\"\n    />\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Tooltip Format</p>\n    <ev-slider\n      v-model=\"modelValue4\"\n      :max=\"45\"\n      :min=\"1\"\n      :step=\"9\"\n      :tooltip-format=\"format1\"\n    />\n    <ev-slider\n      v-model=\"modelValue4\"\n      :max=\"45\"\n      :min=\"1\"\n      :step=\"9\"\n      :tooltip-format=\"format2\"\n    />\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Fill Color</p>\n    <ev-slider\n      v-model=\"modelValue4\"\n      :max=\"45\"\n      :min=\"1\"\n      :step=\"9\"\n      color=\"#FADE4C\"\n      show-step\n    />\n    <div class=\"description\">\n      <span class=\"badge yellow\">\n        color=\"#FADE4C\"\n      </span>\n    </div>\n    <ev-slider\n      v-model=\"modelValue4\"\n      :max=\"45\"\n      :min=\"1\"\n      :step=\"9\"\n      :color=\"['#3C81F6', '#FADE4C']\"\n    />\n    <div class=\"description\">\n      <span class=\"badge yellow\">\n        color=['#3C81F6', '#FADE4C']\n      </span>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref } from 'vue';\n\nexport default {\n  setup() {\n    const modelValue1 = ref(50);\n    const modelValue2 = ref(4);\n    const modelValue3 = ref(-6);\n    const modelValue4 = ref(12);\n    const format1 = val => `Custom ${val}`;\n    const format2 = val => `<p>Custom</p> <strong>${val}</strong>`;\n    return {\n      modelValue1,\n      modelValue2,\n      modelValue3,\n      modelValue4,\n      format1,\n      format2,\n    };\n  },\n};\n</script>\n";