export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Common</p>\n    <ev-scheduler\n      v-model=\"checkVal1\"\n    />\n    <div class=\"description\">\n      <span\n        class=\"badge\"\n        @click=\"clearVal1\"\n      >\n        Clear\n      </span>\n      {{ checkVal1 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Custom</p>\n    <ev-scheduler\n      v-model=\"checkVal2\"\n      :col-labels=\"widthLabels()\"\n      :row-labels=\"heightLabels()\"\n    />\n    <div class=\"description\">\n      {{ checkVal2 }}\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref } from 'vue';\n\n/**\n * ,    \n * @param num\n * @returns {string|*}\n */\nconst lpadToTwoDigits = (num) => {\n  if (num === null) {\n    return '00';\n  } else if (+num < 10) {\n    return `0${num}`;\n  }\n  return num;\n};\n\nexport default {\n  setup() {\n    const checkVal1 = ref([]);\n    const clearVal1 = () => { checkVal1.value = []; };\n\n    const checkVal2 = ref([]);\n    const widthLabels = () => {\n      const result = [];\n      for (let i = 0; i < 15; i++) {\n        result.push(`+${i + 1}`);\n      }\n      return result;\n    };\n    const heightLabels = () => {\n      const result = [];\n      for (let i = 0; i < 48; i++) {\n        if (i % 2) {\n          result.push(`${lpadToTwoDigits(Math.floor(i / 2))}:30`);\n        } else {\n          result.push(`${lpadToTwoDigits(i / 2)}:00`);\n        }\n      }\n      return result;\n    };\n\n    return {\n      checkVal1,\n      clearVal1,\n      checkVal2,\n      widthLabels,\n      heightLabels,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\">\n</style>\n";