export default "<template>\n  <div class=\"case\">\n    <ev-chart\n      :data=\"chartData\"\n      :options=\"chartOptions\"\n    />\n    <div class=\"description\">\n      <b>  4500    </b>\n      <br>\n      <span class=\"toggle-label\">  </span>\n      <ev-toggle\n        v-model=\"isLive\"\n      />\n    </div>\n  </div>\n</template>\n\n<script>\n  import { watch, ref, onBeforeUnmount, onMounted, reactive } from 'vue';\n  import dayjs from 'dayjs';\n\n  export default {\n    setup() {\n      const chartData = reactive({\n        series: {\n          series1: { name: 'series#1', show: true, type: 'bar', showValue: { use: true } },\n          series3: { name: 'series#2', show: true, type: 'line', combo: true },\n        },\n        labels: [],\n        data: {\n          series1: [],\n          series3: [],\n        },\n      });\n\n      const chartOptions = {\n        width: '100%',\n        height: '80%',\n        thickness: 0.8,\n        title: {\n          text: 'Chart Title',\n          show: true,\n        },\n        legend: {\n          show: true,\n          position: 'right',\n        },\n        axesX: [{\n          type: 'time',\n          timeFormat: 'mm:ss',\n          interval: 'second',\n          categoryMode: true,\n        }],\n        axesY: [{\n          type: 'linear',\n          showGrid: true,\n          startToZero: true,\n          autoScaleRatio: 0.1,\n        }],\n      };\n\n      const isLive = ref(false);\n      const liveInterval = ref();\n      let timeValue = dayjs().format('YYYY-MM-DD HH:mm:ss');\n\n      const addRandomChartData = () => {\n        if (isLive.value) {\n          chartData.labels.shift();\n        }\n\n        timeValue = dayjs(timeValue).add(1, 'second');\n        chartData.labels.push(dayjs(timeValue));\n\n        Object.values(chartData.data).forEach((seriesData) => {\n          if (isLive.value) {\n            seriesData.shift();\n          }\n\n          const randomValue = Math.floor(Math.random() * ((5000 - 5) + 1)) + 5;\n          if (randomValue > 4500) {\n            seriesData.push({ value: randomValue, color: '#FF0000' });\n          } else {\n            seriesData.push(randomValue);\n          }\n        });\n      };\n\n      onMounted(() => {\n        for (let ix = 0; ix < 10; ix++) {\n          addRandomChartData();\n        }\n      });\n\n      watch(isLive, (newValue) => {\n        if (newValue) {\n          addRandomChartData();\n          liveInterval.value = setInterval(addRandomChartData, 1000);\n        } else {\n          clearInterval(liveInterval.value);\n        }\n      });\n\n      onBeforeUnmount(() => {\n        clearInterval(liveInterval.value);\n      });\n\n      return {\n        chartData,\n        chartOptions,\n        isLive,\n      };\n    },\n  };\n</script>\n\n<style lang=\"scss\" scoped>\n  .case {\n    height: 100%;\n  }\n  .toggle-label {\n    vertical-align: top;\n    margin-right: 7px;\n  }\n</style>\n";