export default "<template>\n  <div\n    ref=\"zoomRef\"\n    class=\"case\"\n  >\n    <ev-chart-group\n      v-model:zoomStartIdx=\"zoomStartIdx\"\n      v-model:zoomEndIdx=\"zoomEndIdx\"\n      :options=\"chartGroupOptions\"\n    >\n      <ev-chart\n        :data=\"chartData\"\n        :options=\"chartOptions\"\n      />\n      <ev-chart\n        :data=\"chartData2\"\n        :options=\"chartOptions2\"\n      />\n      <ev-chart\n        :data=\"chartData3\"\n        :options=\"chartOptions3\"\n      />\n    </ev-chart-group>\n\n    <div class=\"description\">\n      <p class=\"case-title\"> Start / End   (   )</p>\n      <div class=\"input-wrapper\">\n        <ev-input-number\n          v-model=\"zoomStartIdx\"\n        />\n        <ev-input-number\n          v-model=\"zoomEndIdx\"\n        />\n      </div>\n      <br/>\n      <br/>\n      <p class=\"case-title\">  </p>\n      <ev-input-number\n        v-model=\"bufferMemoryCnt\"\n        :min=\"1\"\n        :max=\"1000\"\n      />\n      <br/>\n      <br/>\n      <ev-button @click=\"onUpdateChartData\"> </ev-button>\n      <br/>\n      <br/>\n      <span class=\"toggle-label\"> </span>\n      <ev-toggle v-model=\"isShowToolbar\"/>\n      <br/>\n      <br/>\n      <span class=\"toggle-label\"> </span>\n      <ev-toggle v-model=\"isShowToggleLegend\"/>\n      <br/>\n      <br/>\n      <span class=\"toggle-label\"> </span>\n      <ev-toggle v-model=\"isExpandChartArea\"/>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { onMounted, reactive, ref, watch } from 'vue';\nimport dayjs from 'dayjs';\n\nexport default {\n  setup() {\n    const isShowToggleLegend = ref(false);\n    const isExpandChartArea = ref(false);\n    const isShowToolbar = ref(true);\n    const bufferMemoryCnt = ref(10);\n    const zoomRef = ref();\n    const zoomStartIdx = ref(0);\n    const zoomEndIdx = ref(0);\n    let timeValue = dayjs().format('YYYY-MM-DD HH:mm:ss');\n\n    const chartGroupOptions = reactive({\n      zoom: {\n        bufferMemoryCnt: 5,\n        useAnimation: false,\n        useWheelMove: false,\n        toolbar: {\n          show: isShowToolbar,\n          items: {\n            previous: {\n              icon: 'ev-icon-allow2-left',\n              size: 'medium',\n              title: 'Previous',\n            },\n            latest: {\n              icon: 'ev-icon-allow2-right',\n              size: 'medium',\n              title: 'Latest',\n            },\n            reset: {\n              icon: 'ev-icon-redo',\n              size: 'medium',\n              title: 'Reset',\n            },\n            dragZoom: {\n              icon: 'ev-icon-zoomin',\n              size: 'medium',\n              title: 'Drag Zoom',\n            },\n          },\n        },\n      },\n    });\n\n    const chartData = reactive({\n      series: {\n        series1: { name: 'series#1' },\n        series2: { name: 'series#2' },\n        series3: { name: 'series#3' },\n        series4: { name: 'series#4' },\n        series5: { name: 'series#5' },\n      },\n      labels: [],\n      data: {\n        series1: [],\n        series2: [],\n        series3: [],\n        series4: [],\n        series5: [],\n      },\n    });\n\n    const chartData2 = reactive({\n      series: {\n        series111: { name: 'series#111', fill: true, point: false },\n        series222: { name: 'series#222', fill: true, point: false },\n        series333: { name: 'series#333', fill: true, point: false },\n      },\n      labels: [],\n      data: {\n        series111: [],\n        series222: [],\n        series333: [],\n      },\n    });\n\n    const chartData3 = reactive({\n      series: {\n        series1: { name: 'series#1', fill: false, point: true },\n        series2: { name: 'series#2', fill: false, point: true },\n        series3: { name: 'series#3', fill: false, point: true },\n        series4: { name: 'series#4', fill: false, point: true },\n        series5: { name: 'series#5', fill: false, point: true },\n      },\n      labels: [],\n      data: {\n        series1: [],\n        series2: [],\n        series3: [],\n        series4: [],\n        series5: [],\n      },\n    });\n\n    const chartOptions = reactive({\n      type: 'line',\n      width: '100%',\n      title: {\n        text: '   1',\n        show: true,\n      },\n      legend: {\n        show: false,\n        position: 'right',\n      },\n      axesX: [{\n        type: 'time',\n        timeFormat: 'HH:mm:ss',\n        interval: 'second',\n      }],\n      axesY: [{\n        type: 'linear',\n        showGrid: true,\n        startToZero: true,\n        autoScaleRatio: 0.1,\n      }],\n    });\n\n    const chartOptions2 = reactive({\n      type: 'line',\n      width: '100%',\n      title: {\n        text: '   2',\n        show: true,\n      },\n      legend: {\n        show: false,\n        position: 'right',\n      },\n      axesX: [{\n        type: 'time',\n        showGrid: false,\n        timeFormat: 'HH:mm:ss',\n        interval: 'second',\n      }],\n      axesY: [{\n        type: 'linear',\n        showGrid: true,\n        startToZero: true,\n        autoScaleRatio: 0.1,\n      }],\n      maxTip: {\n        use: true,\n        showIndicator: true,\n        indicatorColor: '#FF0000',\n        tipBackground: '#000000',\n        tipTextColor: '#FFFFFF',\n      },\n    });\n\n    const chartOptions3 = reactive({\n      type: 'line',\n      width: '100%',\n      title: {\n        text: '   3',\n        show: true,\n      },\n      legend: {\n        show: false,\n        position: 'right',\n      },\n      axesX: [{\n        type: 'time',\n        showGrid: false,\n        timeFormat: 'HH:mm:ss',\n        interval: 'second',\n      }],\n      axesY: [{\n        type: 'linear',\n        showGrid: true,\n        startToZero: true,\n        autoScaleRatio: 0.1,\n      }],\n      maxTip: {\n        use: true,\n        showIndicator: true,\n        indicatorColor: '#FF0000',\n        tipBackground: '#000000',\n        tipTextColor: '#FFFFFF',\n      },\n    });\n\n    const addRandomChartData = () => {\n      timeValue = dayjs(timeValue).add(1, 'second');\n      chartData.labels.push(dayjs(timeValue));\n      chartData2.labels.push(dayjs(timeValue));\n      chartData3.labels.push(dayjs(timeValue));\n\n      Object.values(chartData.data).forEach((seriesData) => {\n        seriesData.push(Math.floor(Math.random() * ((5000 - 5) + 1)) + 5);\n      });\n\n      Object.values(chartData2.data).forEach((seriesData) => {\n        seriesData.push(Math.floor(Math.random() * ((5000 - 5) + 1)) + 5);\n      });\n\n      Object.values(chartData3.data).forEach((seriesData) => {\n        seriesData.push(Math.floor(Math.random() * ((5000 - 5) + 1)) + 5);\n      });\n    };\n\n    onMounted(() => {\n      for (let ix = 0; ix < Math.ceil(Math.random() * 100); ix++) {\n        addRandomChartData();\n      }\n    });\n\n    const onUpdateChartData = () => {\n      const init = (data) => {\n        data.labels.length = 0;\n        const seriesKeyArr = Object.keys(data.data);\n\n        for (let i = 0; i < seriesKeyArr.length; i++) {\n          const series = seriesKeyArr[i];\n\n          data.data[series].length = 0;\n        }\n      };\n\n      init(chartData);\n      init(chartData2);\n      init(chartData3);\n\n      for (let ix = 0; ix < Math.ceil(Math.random() * 100); ix++) {\n        addRandomChartData();\n      }\n    };\n\n    watch(isShowToggleLegend, (isShow) => {\n      chartOptions.legend.show = isShow;\n      chartOptions2.legend.show = isShow;\n      chartOptions3.legend.show = isShow;\n    });\n\n    watch(isExpandChartArea, (isExpand) => {\n      const viewElement = zoomRef.value.parentElement;\n\n      if (isExpand) {\n        viewElement.style.width = '100%';\n        viewElement.nextSibling.style.display = 'none';\n      } else {\n        viewElement.style.width = '50%';\n        viewElement.nextSibling.style.display = 'initial';\n      }\n    });\n\n    watch(bufferMemoryCnt, (cnt) => {\n      chartGroupOptions.zoom.bufferMemoryCnt = cnt;\n    }, { immediate: true });\n\n    return {\n      chartGroupOptions,\n      chartData,\n      chartData2,\n      chartData3,\n      chartOptions,\n      chartOptions2,\n      chartOptions3,\n      isShowToggleLegend,\n      isExpandChartArea,\n      isShowToolbar,\n      zoomRef,\n      zoomStartIdx,\n      zoomEndIdx,\n      bufferMemoryCnt,\n      onUpdateChartData,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\" scoped>\n.input-wrapper {\n  display: flex;\n\n  .ev-input-number {\n    width: 100%;\n  }\n}\n</style>\n";