export default "<template>\n  <ev-chart\n    :data=\"chartData\"\n    :options=\"chartOptions\"\n  />\n</template>\n\n<script>\n  import dayjs from 'dayjs';\n\n  export default {\n    setup() {\n      const time = dayjs().format('YYYY-MM-DD HH:mm:ss');\n      const chartData = {\n        series: {\n          series1: { name: 'series#1', fill: true, point: true },\n          series2: { name: 'series#2', fill: true, point: true },\n          series3: { name: 'series#3', fill: true, point: true },\n        },\n        groups: [\n          ['series1', 'series2', 'series3'],\n        ],\n        labels: [\n          dayjs(time),\n          dayjs(time).add(1, 'day'),\n          dayjs(time).add(2, 'day'),\n          dayjs(time).add(3, 'day'),\n          dayjs(time).add(4, 'day'),\n          dayjs(time).add(5, 'day'),\n          dayjs(time).add(6, 'day'),\n        ],\n        data: {\n          series1: [100, 25, 47, 47, 40, 50, 100],\n          series2: [100, 25, 47, 47, null, null, null],\n          series3: [100, 50, 50, 50, null, 50, 100],\n        },\n      };\n\n      const chartOptions = {\n        type: 'line',\n        width: '100%',\n        title: {\n          text: 'Chart Title',\n          show: true,\n        },\n        legend: {\n          show: true,\n          position: 'right',\n        },\n        tooltip: {\n          use: true,\n        },\n        axesX: [{\n          type: 'time',\n          showGrid: false,\n          timeFormat: 'MM/DD',\n          interval: 'day',\n          labelStyle: {\n            color: '#A4A4A4',\n            fontSize: '11px',\n            fontFamily: 'Roboto',\n          },\n        }],\n        axesY: [{\n          type: 'linear',\n          showGrid: true,\n          startToZero: true,\n          autoScaleRatio: 0.1,\n          labelStyle: {\n            color: '#A4A4A4',\n            fontSize: '11px',\n            fontFamily: 'Roboto',\n          },\n        }],\n      };\n\n      return {\n        chartData,\n        chartOptions,\n      };\n    },\n  };\n</script>\n\n<style lang=\"scss\">\n</style>\n";