export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Common</p>\n    <ev-input-number\n      v-model=\"inputNumber1\"\n    />\n    <div class=\"description\">\n      <span class=\"badge\">\n        InputNumber Value\n      </span>\n      {{ inputNumber1 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Readonly</p>\n    <ev-input-number\n      v-model=\"inputNumber1\"\n      readonly\n    />\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Disabled</p>\n    <ev-input-number\n      v-model=\"inputNumber1\"\n      disabled\n    />\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Maximum & Minimum Value</p>\n    <ev-input-number\n      v-model=\"inputNumber2\"\n      :max=\"10\"\n      :min=\"2\"\n    />\n    <div class=\"description\">\n      <span class=\"badge yellow\">\n        max=\"10\"\n      </span>\n      <span class=\"badge yellow\">\n        min=\"2\"\n      </span>\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Step</p>\n    <ev-input-number\n      v-model=\"inputNumber3\"\n      :step=\"3\"\n      :min=\"2\"\n      :max=\"10\"\n    />\n    <div class=\"description\">\n      <span class=\"badge yellow\">\n        step=\"3\"\n      </span>\n      <span class=\"badge\">\n        max=\"10\"\n      </span>\n      <span class=\"badge\">\n        min=\"2\"\n      </span>\n      {{ inputNumber3 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Step Strictly</p>\n    <ev-input-number\n      v-model=\"inputNumber4\"\n      :step=\"3\"\n      :min=\"2\"\n      :max=\"10\"\n      step-strictly\n    />\n    <div class=\"description\">\n      <span class=\"badge yellow\">\n        step=\"3\"\n      </span>\n      <span class=\"badge yellow\">\n        min=\"2\"\n      </span>\n      <span class=\"badge\">\n        max=\"10\"\n      </span>\n      {{ inputNumber4 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Precision</p>\n    <ev-input-number\n      v-model=\"inputNumber5\"\n      :precision=\"3\"\n      :step=\"0.02\"\n    />\n    <div class=\"description\">\n      <span class=\"badge yellow\">\n        precision=\"3\"\n      </span>\n      <span class=\"badge\">\n        step=\"0.02\"\n      </span>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref } from 'vue';\n\nexport default {\n  setup() {\n    const inputNumber1 = ref();\n    const inputNumber2 = ref();\n    const inputNumber3 = ref();\n    const inputNumber4 = ref();\n    const inputNumber5 = ref();\n    return {\n      inputNumber1,\n      inputNumber2,\n      inputNumber3,\n      inputNumber4,\n      inputNumber5,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\" scoped>\n.case .ev-input-number {\n  width: 300px;\n  margin-right: 15px;\n}\n</style>\n";