export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Common</p>\n    <div class=\"tab-wrapper\">\n      <ev-tabs\n        v-model=\"selectedValue1\"\n        v-model:panels=\"tabPanels1\"\n        :closable=\"true\"\n      >\n        <ev-tab-panel\n          v-for=\"(item, idx) in tabPanels1\"\n          :key=\"`${item.name}_${idx}`\"\n          :text=\"item.text\"\n          :value=\"item.value\"\n        >\n          <div v-html=\"item.content\"/>\n        </ev-tab-panel>\n      </ev-tabs>\n    </div>\n    <div class=\"description\">\n      <button\n        class=\"btn\"\n        @click=\"addItem1\"\n      >\n        addItem\n      </button>\n      &nbsp;&nbsp;&nbsp;\n      <button\n        class=\"btn\"\n        @click=\"popItem1\"\n      >\n        popItem\n      </button>\n      &nbsp;&nbsp;&nbsp;\n      <button\n        class=\"btn\"\n        @click=\"spliceItem1\"\n      >\n        spliceItem1\n      </button>\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Draggable</p>\n    <div class=\"tab-wrapper\">\n      <ev-tabs\n        v-model=\"selectedValue1\"\n        v-model:panels=\"tabPanels1\"\n        :draggable=\"true\"\n      >\n        <ev-tab-panel\n          v-for=\"(item, idx) in tabPanels1\"\n          :key=\"`${item.name}_${idx}`\"\n          :text=\"item.text\"\n          :value=\"item.value\"\n        >\n          <div v-html=\"item.content\"/>\n        </ev-tab-panel>\n      </ev-tabs>\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">With Component</p>\n    <div class=\"tab-wrapper\">\n      <ev-tabs\n        v-model=\"selectedValue2\"\n        v-model:panels=\"tabPanels2\"\n        class=\"example2\"\n      >\n        <ev-tab-panel\n          v-for=\"(item, idx) in tabPanels2\"\n          :key=\"`${item.name}_${idx}`\"\n          :text=\"item.text\"\n          :value=\"item.value\"\n        >\n          <component\n            :is=\"item.component\"\n            v-if=\"item.component\"\n            class=\"componentCls\"\n          />\n        </ev-tab-panel>\n      </ev-tabs>\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Stretch Tab Name</p>\n    <div class=\"tab-wrapper\">\n      <ev-tabs\n        v-model=\"selectedValue2\"\n        v-model:panels=\"tabPanels2\"\n        :stretch=\"true\"\n        :closable=\"true\"\n      >\n        <ev-tab-panel\n          v-for=\"(item, idx) in tabPanels2\"\n          :key=\"`${item.name}_${idx}`\"\n          :text=\"item.text\"\n          :value=\"item.value\"\n        >\n          <component\n            :is=\"item.component\"\n            v-if=\"item.component\"\n            class=\"componentCls\"\n          />\n        </ev-tab-panel>\n      </ev-tabs>\n    </div>\n    <div class=\"description\">\n      <button\n        class=\"btn\"\n        @click=\"toggleComp4\"\n      >\n        Toggle Component 4\n      </button>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref, shallowRef, triggerRef, defineAsyncComponent } from 'vue';\n\nexport default {\n  setup() {\n    const selectedValue1 = ref('tabName2');\n    const tabPanels1 = ref([\n      {\n        text: 'LABEL1LABEL1LABEL1LABEL1LABEL1',\n        value: 'tabName1',\n        content: 'content1',\n      },\n      {\n        text: 'LABEL2LABEL2',\n        value: 'tabName2',\n        content: '<div class=\"example1-label\"><h3>HEADER</h3><p>123123123423423423</p></div>',\n      },\n      {\n        text: 'LABEL3',\n        value: 'tabName3',\n        content: 'content3',\n      },\n      {\n        text: 'LABEL4',\n        value: 'tabName4',\n        content: 'content4',\n      },\n      {\n        text: 'LABEL5',\n        value: 'tabName5',\n        content: 'content5',\n      },\n      {\n        text: 'LABEL6',\n        value: 'tabName6',\n        content: 'content6',\n      },\n    ]);\n    const idx = ref(tabPanels1.value.length + 1);\n\n    const addItem1 = () => {\n      tabPanels1.value.push({\n        text: 'NEW TEXT',\n        value: `value${idx.value}`,\n        content: `content${idx.value}`,\n      });\n      idx.value++;\n    };\n\n    const popItem1 = () => {\n      if (tabPanels1.value.length > 1) {\n        tabPanels1.value.pop();\n      }\n    };\n    const spliceItem1 = () => {\n      if (tabPanels1.value.length > 1) {\n        tabPanels1.value.splice(0, 1);\n      }\n    };\n\n    const selectedValue2 = ref('comp1');\n    const tabPanels2 = shallowRef([\n      {\n        text: 'LABEL1LABEL1LABEL1LABEL1LABEL1',\n        value: 'comp1',\n        component: defineAsyncComponent(() => import('./Comp1.vue')),\n        iconClass: 'ev-icon-question',\n      },\n      {\n        text: 'LABEL2LABEL2',\n        value: 'comp2',\n        component: defineAsyncComponent(() => import('./Comp2.vue')),\n        iconClass: 'ev-icon-shard',\n      },\n      {\n        text: 'TEXT3TEXT3TEXT3EXT3TEXT3TEX3',\n        value: 'comp3',\n        component: defineAsyncComponent(() => import('./Comp3.vue')),\n        iconClass: 'ev-icon-bell',\n      },\n\n    ]);\n\n    const toggleComp4 = () => {\n      const comp4Idx = tabPanels2.value.findIndex(v => v.value === 'comp4');\n      if (comp4Idx < 0) {\n        tabPanels2.value.push({\n          text: 'LABEL4LABEL4LABEL4LABEL4LABEL4',\n          value: 'comp4',\n          component: defineAsyncComponent(() => import('./Comp4.vue')),\n          iconClass: 'ev-icon-binder',\n        });\n      } else {\n        tabPanels2.value.splice(comp4Idx, 1);\n      }\n      triggerRef(tabPanels2);\n    };\n\n    return {\n      selectedValue1,\n      tabPanels1,\n      addItem1,\n      popItem1,\n      spliceItem1,\n\n      selectedValue2,\n      tabPanels2,\n      toggleComp4,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\">\n.tab-wrapper {\n  width: 100%;\n  height: 100%;\n}\n\n.componentCls {\n  padding: 20px;\n  border: 1px solid #B2B2B2;\n  border-top: none;\n  border-radius: 0 0 4px 4px;\n}\n\n.example1-label {\n  padding: 20px;\n}\n\n.example2 {\n  .ev-tabs-title {\n    width: 150px;\n  }\n}\n</style>\n";