export default "<template>\n  <ev-chart\n    :data=\"chartData\"\n    :options=\"chartOptions\"\n  />\n</template>\n\n<script>\n  export default {\n    setup() {\n      const addUnit = (value) => {\n        const bytes = +value;\n\n        if (bytes === 0) {\n          return '0 Bytes';\n        }\n\n        const decimalPoint = 2;\n        const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB', 'PB', 'EB', 'ZB', 'YB'];\n        const k = 1024;\n        const i = Math.floor(Math.log(bytes) / Math.log(k));\n\n        return `${parseFloat((bytes / k ** i).toFixed(decimalPoint))} ${sizes[i]}`;\n      };\n\n      const chartData = {\n        series: {\n          series1: { name: 'series#1', showValue: { use: true, fontSize: 12, textColor: '#000000', align: 'out', formatter: addUnit } },\n        },\n        labels: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri'],\n        data: {\n          series1: [1500.123456, 180.123456, 30.123456, 10, 0],\n        },\n      };\n\n      const chartOptions = {\n        type: 'bar',\n        width: '100%',\n        height: '100%',\n        thickness: 0.8,\n        title: {\n          text: 'Chart Title',\n          show: true,\n        },\n        legend: {\n          show: true,\n          position: 'right',\n        },\n        horizontal: true,\n        borderRadius: 15,\n        axesX: [{\n          type: 'log',\n          startToZero: true,\n          autoScaleRatio: 0.1,\n          showGrid: true,\n          formatter: addUnit,\n        }],\n        axesY: [{\n          type: 'step',\n          showGrid: false,\n        }],\n        indicator: {\n          use: false,\n        },\n        tooltip: {\n          use: true,\n        },\n      };\n\n      return {\n        chartData,\n        chartOptions,\n      };\n    },\n  };\n</script>\n\n<style lang=\"scss\">\n</style>\n";