<template>
  <label
    class="ev-checkbox"
    :class="[
      { disabled, },
      { checked, },
    ]"
  >
    <input
      ref="checkbox"
      v-model="mv"
      type="checkbox"
      class="ev-checkbox-input"
      :disabled="disabled"
      :value="label"
      :readonly="readonly"
      @change="changeMv"
    />
    <span class="ev-checkbox-label">
      <template v-if="$slots.default">
        <slot />
      </template>
      <template v-else>
        {{ label }}
      </template>
    </span>
  </label>
</template>

<script>
import { ref, computed, watch, nextTick, inject } from 'vue';

export default {
  name: 'EvCheckbox',
  props: {
    modelValue: {
      type: [String, Number, Boolean, Symbol, Array],
      default: false,
    },
    label: {
      type: [String, Number, Boolean, Symbol],
      default: null,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    indeterminate: {
      type: Boolean,
      default: false,
    },
  },
  emits: {
    'update:modelValue': null,
    'update:indeterminate': [Boolean],
    change: null,
  },
  setup(props, { emit }) {
    /**
     * checkbox Ref
     */
    const checkbox = ref(null);

    /**
     * checkbox modelView 
     * checkbox group    Provide('EvCheckboxGroupMv') 
     * checkbox group    2nd argument 
     *   - checkbox html5 indeterminate attribute   v-model:indeterminate update
     *   - Vue3 v-model:attr  Vue2 :attr.sync  ( )
     */
    const mv = inject(
      'EvCheckboxGroupMv',
      computed({
        get: () => props.modelValue,
        set: (val) => {
          if (val && !checkbox.value.indeterminate) {
            emit('update:indeterminate', false);
          }
          emit('update:modelValue', val);
        },
      }),
    );

    /**
     * mv       check 
     * return {Boolean}
     */
    const refLabel = computed(() => props.label);
    const checked = computed(() => {
      if (Array.isArray(mv.value)) {
        return mv.value.includes(refLabel.value);
      }
      return mv.value;
    });

    /**
     *   ''     
     * checkbox group   Provide('EvCheckboxGroupChange') 
     * checkbox group    2nd argument 
     */
    const changeMv = inject(
      'EvCheckboxGroupChange',
      async (e) => {
        await nextTick();
        emit('change', mv.value, e);
      },
    );

    /**
     * props indeterminate  
     * checkbox element indeterminate attribute  
     */
    watch(
      () => props.indeterminate,
      (val) => { checkbox.value.indeterminate = val; },
    );

    return {
      mv,
      checkbox,
      checked,
      changeMv,
    };
  },
};
</script>

<style lang="scss">
@import '../../style/index.scss';

.ev-checkbox {
  margin-right: 30px;
  cursor: pointer;
  user-select: none;
  &-label {
    padding-left: 5px;
  }
  &-input {
    cursor: pointer;
  }
}

@include state('disabled') {
  .ev-checkbox-label {
    @include evThemify() {
      color: evThemed('disabled');
    }
  }
  .ev-checkbox-input,
  .ev-checkbox-label {
    cursor: not-allowed !important;
  }
}
@include state('type-button') {
  .ev-checkbox {
    display: inline-block;
    padding: 0;
    margin: 0;
    text-align: center;

    @include evThemify() {
      border: 1px solid evThemed('border-base');
      border-left: 0;
    }
    &:first-child {
      border-radius: $default-radius 0 0 $default-radius;

      @include evThemify() {
        border-left: 1px solid evThemed('border-base');
      }
    }
    &:last-child {
      border-radius: 0 $default-radius $default-radius 0;
    }
    &.checked {
      color: $color-white;

      @include evThemify() {
        background-color: evThemed('primary');
      }
    }
    &.disabled.checked {
      @include evThemify() {
        background-color: rgba(evThemed('border-base'), 0.5);
      }
    }
  }
  .ev-checkbox-input {
    @include visible-hide();
  }
  .ev-checkbox-label {
    display: inline-block;
    padding: 7px 12px;
  }
}
</style>
