export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Common</p>\n    <ev-text-field\n      v-model=\"modelValue1\"\n      placeholder=\"Please enter the content\"\n      type=\"textarea\"\n    />\n    <div class=\"description\">\n      <span class=\"badge yellow\">\n        type=\"textarea\"\n      </span>\n      <span class=\"badge\">\n        Text Field Value\n      </span>\n      {{ modelValue1 }}\n    </div>\n  </div>\n\n  <div class=\"case\">\n    <p class=\"case-title\">Readonly</p>\n    <ev-text-field\n      v-model=\"modelValue2\"\n      type=\"textarea\"\n      readonly\n    />\n  </div>\n\n  <div class=\"case\">\n    <p class=\"case-title\">Disabled</p>\n    <ev-text-field\n      v-model=\"modelValue3\"\n      type=\"textarea\"\n      disabled\n    />\n  </div>\n\n  <div class=\"case\">\n    <p class=\"case-title\">Max Length</p>\n    <div style=\"display: flex;\">\n      <ev-text-field\n        v-model=\"modelValue4\"\n        placeholder=\"Please enter the content\"\n        type=\"textarea\"\n        :max-length=\"10\"\n      />\n      <ev-text-field\n        v-model=\"modelValue4\"\n        placeholder=\"Please enter the content\"\n        type=\"textarea\"\n        :max-length=\"10\"\n        show-max-length\n      />\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref } from 'vue';\n\nexport default {\n  setup() {\n    const modelValue1 = ref();\n    const modelValue2 = ref('Read Only Content');\n    const modelValue3 = ref('Disabled Content');\n    const modelValue4 = ref();\n\n    return {\n      modelValue1,\n      modelValue2,\n      modelValue3,\n      modelValue4,\n    };\n  },\n};\n</script>\n";