export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Common</p>\n    <ev-select\n      v-model=\"selectVal1\"\n      :items=\"items1\"\n      placeholder=\"Please select value.\"\n      @change=\"changeSelect1\"\n    />\n    <div class=\"description\">\n      <button\n        @click=\"addItem\"\n      >\n        addItem\n      </button>\n      first selectbox value : {{ selectVal1 }}\n    </div>\n    <div class=\"description\">\n      change val : {{ selectEventVal.val }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Disabled Item</p>\n    <ev-select\n      v-model=\"selectVal3\"\n      :items=\"items3\"\n    />\n    <div class=\"description\">\n      <button\n        @click=\"disableIdx1\"\n      >\n        Disable Items[1]\n      </button>\n      selected value : {{ selectVal3 }}\n    </div>\n    <div class=\"description\">\n      items : {{ items3 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Disabled Select</p>\n    <ev-select\n      v-model=\"selectVal4\"\n      :items=\"items4\"\n      :disabled=\"isDisabled\"\n    />\n    <br>\n    <div class=\"description\">\n      <button\n        @click=\"disableSelect\"\n      >\n        Disable Selectbox\n      </button>\n      selected value : {{ selectVal4 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Clearable Select</p>\n    <ev-select\n      v-model=\"selectVal4\"\n      :items=\"items4\"\n      placeholder=\"Please Select Item.\"\n      clearable\n    />\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Filterable Select</p>\n    <ev-select\n      v-model=\"selectVal4\"\n      :items=\"items4\"\n      placeholder=\"Please Select Item.\"\n      filterable\n    />\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Icon</p>\n    <ev-select\n      v-model=\"selectVal5\"\n      :items=\"items5\"\n      placeholder=\"Please select value.\"\n      @change=\"changeSelect1\"\n    />\n    <div class=\"description\">\n      val : {{ selectVal5 }}\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref, reactive } from 'vue';\n\nexport default {\n  setup() {\n    const selectVal1 = ref('');\n    const items1 = ref([\n      {\n        name: 'name0name0name0name0name0',\n        value: 'value0',\n      },\n      {\n        name: 'name11111111111111111111111111111111111111',\n        value: 'value1',\n      },\n      {\n        name: '22222222',\n        value: 'value2',\n      },\n      {\n        name: 'name3',\n        value: 'value3',\n      },\n      {\n        name: 'name4',\n        value: 'value4',\n      },\n      {\n        name: 'name5',\n        value: 'value5',\n      },\n      {\n        name: 'name6',\n        value: 'value6',\n      },\n      {\n        name: 'name7',\n        value: 'value7',\n      },\n      {\n        name: 'name8',\n        value: 'value8',\n      },\n    ]);\n    const selectEventVal = reactive({\n      e: null,\n      val: null,\n    });\n\n    const addItem = () => {\n      const count = items1.value.length;\n      items1.value.push({\n        name: `name${count}`,\n        value: `value${count}`,\n      });\n    };\n    const changeSelect1 = (val) => {\n      selectEventVal.val = val;\n    };\n\n    const selectVal3 = ref('value1');\n    const items3 = ref([\n      {\n        name: 'name1',\n        value: 'value1',\n      },\n      {\n        name: 'name2',\n        value: 'value2',\n        disabled: true,\n      },\n      {\n        name: 'name3',\n        value: 'value3',\n      },\n    ]);\n    const disableIdx1 = () => {\n      items3.value[1].disabled = !items3.value[1].disabled;\n    };\n\n    const selectVal4 = ref('value2');\n    const items4 = ref([\n      {\n        name: 'name1',\n        value: 'value1',\n      },\n      {\n        name: 'name2',\n        value: 'value2',\n      },\n      {\n        name: 'name3',\n        value: 'value3',\n      },\n      {\n        name: 'name13',\n        value: 'value13',\n      },\n      {\n        name: 'name23',\n        value: 'value23',\n      },\n      {\n        name: 'name33',\n        value: 'value33',\n      },\n      {\n        name: ' A',\n        value: 'instance A',\n      },\n      {\n        name: ' B',\n        value: 'instance B',\n      },\n      {\n        name: ' ',\n        value: 'instant grocery',\n      },\n      {\n        name: 'A ',\n        value: 'A instance',\n      },\n      {\n        name: 'B ',\n        value: 'B instance',\n      },\n    ]);\n    const isDisabled = ref(true);\n    const disableSelect = () => { isDisabled.value = !isDisabled.value; };\n\n    const selectVal5 = ref();\n    const items5 = ref([\n      {\n        name: 'name0',\n        value: 'value0',\n        iconClass: 'ev-icon-query',\n      },\n      {\n        name: 'name1',\n        value: 'value1',\n      },\n      {\n        name: 'name2',\n        value: 'value2',\n        iconClass: '',\n      },\n      {\n        name: 'name3',\n        value: 'value3',\n        iconClass: 'ev-icon-bell',\n      },\n      {\n        name: 'name4',\n        value: 'value4',\n        iconClass: 'ev-icon-close2',\n      },\n    ]);\n\n    return {\n      selectVal1,\n      items1,\n      selectEventVal,\n      addItem,\n      changeSelect1,\n\n      selectVal3,\n      items3,\n      disableIdx1,\n\n      selectVal4,\n      items4,\n      isDisabled,\n      disableSelect,\n\n      selectVal5,\n      items5,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\">\n</style>\n";