export default "<template>\n  <div\n    ref=\"zoomRef\"\n    class=\"case\"\n  >\n    <ev-chart-group :options=\"chartGroupOptions\">\n      <ev-chart\n        :data=\"chartData\"\n        :options=\"chartOptions\"\n      />\n      <ev-chart-brush :options=\"brushOptions\"/>\n    </ev-chart-group>\n\n    <div class=\"description\">\n      <ev-button @click=\"onUpdateChartData\"> </ev-button>\n      <br/>\n      <br/>\n      <span class=\"toggle-label\"> useDebounce  </span>\n      <ev-toggle v-model=\"isUseDebounce\"/>\n      <br/>\n      <br/>\n      <span class=\"toggle-label\">Brush </span>\n      <ev-toggle v-model=\"isShowBrush\"/>\n      <br/>\n      <br/>\n      <span class=\"toggle-label\"> </span>\n      <ev-toggle v-model=\"isShowToolbar\"/>\n      <br/>\n      <br/>\n      <span class=\"toggle-label\"> </span>\n      <ev-toggle v-model=\"isExpandChartArea\"/>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { onMounted, reactive, ref, watch } from 'vue';\nimport dayjs from 'dayjs';\n\nexport default {\n  setup() {\n    const isExpandChartArea = ref(false);\n    const isShowBrush = ref(true);\n    const isShowToolbar = ref(true);\n    const isUseDebounce = ref(true);\n    const zoomRef = ref();\n    let timeValue = dayjs().format('YYYY-MM-DD HH:mm:ss');\n\n    const chartGroupOptions = reactive({\n      zoom: {\n        keepZoomStatus: true,\n        toolbar: {\n          show: isShowToolbar,\n        },\n      },\n    });\n\n    const chartData = reactive({\n      series: {\n        series1: { name: 'series#1' },\n        series2: { name: 'series#2' },\n        series3: { name: 'series#3' },\n        series4: { name: 'series#4' },\n        series5: { name: 'series#5' },\n      },\n      labels: [],\n      data: {\n        series1: [],\n        series2: [],\n        series3: [],\n        series4: [],\n        series5: [],\n      },\n    });\n\n    const chartOptions = reactive({\n      type: 'line',\n      width: '100%',\n      legend: {\n        show: true,\n        position: 'right',\n      },\n      axesX: [{\n        type: 'time',\n        timeFormat: 'HH:mm:ss',\n        interval: 'second',\n      }],\n      axesY: [{\n        type: 'linear',\n        showGrid: true,\n        startToZero: true,\n        autoScaleRatio: 0.1,\n      }],\n    });\n\n    const brushOptions = reactive({\n      show: isShowBrush,\n      useDebounce: isUseDebounce,\n      chartIdx: 0,\n      height: 80,\n    });\n\n    const addRandomChartData = () => {\n      timeValue = dayjs(timeValue).add(1, 'second');\n      chartData.labels.push(dayjs(timeValue));\n\n      Object.values(chartData.data).forEach((seriesData) => {\n        seriesData.push(Math.floor(Math.random() * ((5000 - 5) + 1)) + 5);\n      });\n    };\n\n    onMounted(() => {\n      for (let ix = 0; ix < 100; ix++) {\n        addRandomChartData();\n      }\n    });\n\n    const onUpdateChartData = () => {\n      const init = (data) => {\n        data.labels.length = 0;\n        const seriesKeyArr = Object.keys(data.data);\n\n        for (let i = 0; i < seriesKeyArr.length; i++) {\n          const series = seriesKeyArr[i];\n\n          data.data[series].length = 0;\n        }\n      };\n\n      init(chartData);\n\n      for (let ix = 0; ix < 100; ix++) {\n        addRandomChartData();\n      }\n    };\n\n    watch(isExpandChartArea, (isExpand) => {\n      const viewElement = zoomRef.value.parentElement;\n\n      if (isExpand) {\n        viewElement.style.width = '100%';\n        viewElement.nextSibling.style.display = 'none';\n      } else {\n        viewElement.style.width = '50%';\n        viewElement.nextSibling.style.display = 'initial';\n      }\n    });\n\n    return {\n      chartGroupOptions,\n      chartData,\n      chartOptions,\n      brushOptions,\n      isShowToolbar,\n      isShowBrush,\n      isExpandChartArea,\n      isUseDebounce,\n      zoomRef,\n      onUpdateChartData,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\" scoped>\n.input-wrapper {\n  display: flex;\n\n  .ev-input-number {\n    width: 100%;\n  }\n}\n</style>\n";