export default "<template>\n  <div class=\"case\">\n    <ev-chart\n      :data=\"chartData\"\n      :options=\"chartOptions\"\n    />\n  </div>\n</template>\n\n<script>\n  export default {\n    setup() {\n      const chartData = {\n        series: {\n          series1: { name: 'series#1' },\n          series2: { name: 'series#2' },\n          series3: { name: 'series#3' },\n          series4: { name: 'series#4' },\n          series5: { name: 'series#5' },\n          series6: { name: 'series#6' },\n          series7: { name: 'series#7' },\n          series8: { name: 'series#8' },\n          series9: { name: 'series#9' },\n        },\n        data: [{\n          id: 'series1',\n          value: 15,\n          children: [{\n            id: 'series2',\n            value: 30,\n          }, {\n            id: 'series5',\n            value: 10,\n            children: [{\n              id: 'series7',\n              value: 10,\n            }],\n          }],\n        }, {\n          id: 'series3',\n          value: 20,\n          children: [{\n            id: 'series6',\n            value: 5,\n            children: [{\n              id: 'series8',\n              value: 10,\n              children: [{\n                id: 'series9',\n                value: 10,\n              }],\n            }],\n          }],\n        }],\n      };\n\n      const chartOptions = {\n        type: 'pie',\n        width: '100%',\n        height: '100%',\n        title: {\n          text: 'Chart Title',\n          show: true,\n        },\n        legend: {\n          show: true,\n          position: 'right',\n        },\n        sunburst: true,\n        reverse: false,\n        doughnutHoleSize: 0.4,\n      };\n\n      return {\n        chartData,\n        chartOptions,\n      };\n    },\n  };\n</script>\n\n<style lang=\"scss\" scoped>\n  .case {\n    height: 100%;\n  }\n</style>\n";