export default "<template>\n  <div class=\"case\">\n    <ev-grid\n      v-model:selected=\"selected\"\n      v-model:checked=\"checked\"\n      :columns=\"columns\"\n      :rows=\"tableData\"\n      :width=\"widthMV\"\n      :height=\"heightMV\"\n      :option=\"{\n        adjust: true,\n        showHeader: showHeaderMV,\n        rowHeight: rowHeightMV,\n        columnWidth: columnWidthMV,\n        useFilter: useFilterMV,\n        customContextMenu: menuItems,\n        useCheckbox: {\n          use: useCheckboxMV,\n          mode: checkboxModeMV,\n          headerCheck: headerCheckMV,\n        },\n        useSelection: {\n          use: useSelectionMV,\n          multiple: isSelectionMultiple,\n          limitCount: limitMV,\n        },\n        style: {\n          stripe: stripeMV,\n          border: borderMV,\n          highlight: highlightMV,\n        },\n        page: pageInfo,\n      }\"\n      @check-row=\"onCheckedRow\"\n      @check-all=\"onCheckedRow\"\n      @click-row=\"onClickRow\"\n      @dblclick-row=\"onDoubleClickRow\"\n      @page-change=\"onRequestData\"\n    >\n    </ev-grid>\n    <!-- description -->\n    <div class=\"description\">\n      <div class=\"form-rows\">\n        <span class=\"badge yellow\">\n          Show Header\n        </span>\n        <ev-toggle\n          v-model=\"showHeaderMV\"\n        />\n        <span class=\"badge yellow\">\n          Use Filter\n        </span>\n        <ev-toggle\n          v-model=\"useFilterMV\"\n          readonly\n        />\n        <span class=\"badge yellow\">\n          Use Checkbox\n        </span>\n        <ev-toggle\n          v-model=\"useCheckboxMV\"\n        />\n        <span class=\"badge yellow\">\n          Stripe Style\n        </span>\n        <ev-toggle\n          v-model=\"stripeMV\"\n        />\n      </div>\n      <div class=\"form-rows\">\n        <span class=\"badge yellow\">\n          Use Selection\n        </span>\n        <ev-toggle\n          v-model=\"useSelectionMV\"\n        />\n        <span class=\"badge yellow\">\n          Multiple Selection\n        </span>\n        <ev-toggle\n          v-model=\"isSelectionMultiple\"\n        />\n        <span class=\"badge yellow\">\n          Limit Count\n        </span>\n        <ev-select\n          v-model=\"limitMV\"\n          :items=\"limitItems\"\n          :style=\"{ width: '200px' }\"\n          clearable\n          placeholder=\"Please select value.\"\n        />\n      </div>\n      <div class=\"form-rows\">\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">\n            Width\n          </span>\n          <ev-text-field\n            v-model=\"widthMV\"\n          />\n        </div>\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">\n            Height\n          </span>\n          <ev-text-field\n            v-model=\"heightMV\"\n          />\n        </div>\n      </div>\n      <div class=\"form-rows\">\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">\n            Row Height\n          </span>\n          <ev-input-number\n            v-model=\"rowHeightMV\"\n            :step=\"10\"\n            :max=\"150\"\n            :min=\"35\"\n          />\n        </div>\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">\n            Column Width\n          </span>\n          <ev-input-number\n            v-model=\"columnWidthMV\"\n            :step=\"20\"\n            :max=\"300\"\n            :min=\"40\"\n          />\n        </div>\n      </div>\n      <div class=\"form-rows\">\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">\n            Checkbox\n          </span>\n          <ev-radio-group\n            v-model=\"checkboxModeMV\"\n            type=\"button\"\n            @change=\"changeMode\"\n          >\n            <ev-radio label=\"single\" />\n            <ev-radio label=\"multi\" />\n          </ev-radio-group>\n          <span class=\"badge\">\n            Mode\n          </span>{{ checkboxModeMV }}\n          <span class=\"badge\">\n            Count\n          </span>{{ checked.length }}\n        </div>\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">\n            Checked Row\n          </span>\n          <ev-text-field\n            v-model=\"checkedRowsMV\"\n            type=\"textarea\"\n            readonly\n          />\n        </div>\n      </div>\n      <div class=\"form-rows\">\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">\n            Clicked Row\n          </span>\n          <ev-text-field\n            v-model=\"clickedRowMV\"\n            type=\"textarea\"\n            readonly\n          />\n        </div>\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">\n            Double Clicked Row\n          </span>\n          <ev-text-field\n            v-model=\"DbClickedRowsMV\"\n            type=\"textarea\"\n            readonly\n          />\n        </div>\n      </div>\n      <div class=\"form-rows\">\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">\n            Highlight\n          </span>\n          <ev-input-number\n            v-model=\"highlightMV\"\n            :step=\"1\"\n            :max=\"100\"\n            :min=\"-1\"\n          />\n        </div>\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">\n            Border Style\n          </span>\n          <ev-select\n            v-model=\"borderMV\"\n            :items=\"items\"\n            clearable\n            placeholder=\"Please select value.\"\n          />\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref, reactive, computed } from 'vue';\n\nexport default {\n  setup() {\n    const tableData = ref([]);\n    const selected = ref([]);\n    const checked = ref([]);\n    const widthMV = ref('100%');\n    const heightMV = ref(300);\n    const showHeaderMV = ref(true);\n    const stripeMV = ref(false);\n    const rowHeightMV = ref(45);\n    const columnWidthMV = ref(80);\n    const useFilterMV = ref(false);\n    const useCheckboxMV = ref(true);\n    const checkboxModeMV = ref('multi');\n    const headerCheckMV = ref(true);\n    const checkedRowsMV = ref();\n    const clickedRowMV = ref();\n    const DbClickedRowsMV = ref();\n    const useSelectionMV = ref(true);\n    const isSelectionMultiple = ref(false);\n    const menuItems = ref([\n      {\n        text: 'Menu1',\n        click: param => console.log(`[Menu1] Selected Row Data: ${param?.selectedRow}`),\n      }, {\n        text: 'Menu2',\n        click: param => console.log('[Menu2]', param),\n      },\n    ]);\n    const highlightMV = ref(-1);\n    const borderMV = ref('rows');\n    const items = ref([\n      {\n        name: 'none',\n        value: 'none',\n      },\n      {\n        name: 'rows',\n        value: 'rows',\n      },\n    ]);\n    const limitMV = ref(2);\n    const limitItems = ref([\n      {\n        name: '2',\n        value: 2,\n      },\n      {\n        name: '4',\n        value: 4,\n      },\n    ]);\n    const columns = ref([\n      { caption: 'Name', field: 'userName', type: 'string', width: 80 },\n      { caption: 'Role', field: 'role', type: 'string', width: 100 },\n      { caption: 'Phone', field: 'phone', type: 'string', sortable: false },\n      { caption: 'Email', field: 'email', type: 'string', width: 200 },\n      { caption: 'Last Login', field: 'lastLogin', type: 'string' },\n    ]);\n    const resetBorderStyle = () => {\n      borderMV.value = '';\n    };\n    const changeMode = (mode) => {\n      checkboxModeMV.value = mode;\n      checked.value = [];\n    };\n    const onCheckedRow = () => {\n      let checkedRow = '';\n      checked.value.forEach((row) => {\n        checkedRow += JSON.stringify(row);\n      });\n      checkedRowsMV.value = checkedRow;\n    };\n    const onDoubleClickRow = (e) => {\n      DbClickedRowsMV.value = `${e.rowData}`;\n    };\n    const onClickRow = () => {\n      let clickedRow = '';\n      selected.value.forEach((row) => {\n        clickedRow += JSON.stringify(row);\n      });\n      clickedRowMV.value = clickedRow;\n    };\n    const getData = (count, startIndex) => {\n      const temp = [];\n      const roles = ['Common', 'Admin'];\n      for (let ix = startIndex; ix < startIndex + count; ix++) {\n        temp.push([\n          `user_${ix + 1}`,\n          roles[ix % 2],\n          '010-0000-0000',\n          'kmn0827@ex-em.com',\n          '2020.08.04 14:15',\n        ]);\n      }\n      return temp;\n    };\n    const onRequestData = (e) => {\n      if (e.eventName?.onSort || e.eventName?.onSearch) {\n        tableData.value = getData(50, 0);\n      } else if (e.eventName?.onScrollEnd && tableData.value.length < 1000) {\n        const newData = getData(50, tableData.value.length);\n        tableData.value = [\n          ...tableData.value,\n          ...newData,\n        ];\n      }\n    };\n    const pageInfo = reactive({\n      use: true,\n      isInfinite: true,\n      perPage: 50,\n      total: computed(() => tableData.value.length),\n      useClient: true,\n    });\n\n    tableData.value = getData(50, 0);\n    return {\n      columns,\n      tableData,\n      selected,\n      checked,\n      widthMV,\n      heightMV,\n      showHeaderMV,\n      stripeMV,\n      rowHeightMV,\n      columnWidthMV,\n      useFilterMV,\n      useCheckboxMV,\n      checkboxModeMV,\n      headerCheckMV,\n      checkedRowsMV,\n      clickedRowMV,\n      DbClickedRowsMV,\n      menuItems,\n      highlightMV,\n      borderMV,\n      items,\n      pageInfo,\n      isSelectionMultiple,\n      useSelectionMV,\n      limitMV,\n      limitItems,\n      changeMode,\n      onCheckedRow,\n      onDoubleClickRow,\n      onClickRow,\n      resetBorderStyle,\n      onRequestData,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\" scoped>\n.description {\n  min-width: 200px;\n}\n.form-rows {\n  display: flex;\n  margin-bottom: 5px;\n}\n.form-row {\n  width: 50%;\n}\n.ev-text-field, .ev-input-number, .ev-select {\n  width: 80%;\n}\n.badge {\n  margin-bottom: 2px;\n  margin-right: 5px !important;\n}\n.ev-toggle {\n  margin-right: 10px;\n}\n</style>\n";