export default "<template>\n  <div class=\"case\">\n    <ev-chart\n      v-model:selectedLabel=\"defaultSelectLabel\"\n      :data=\"chartData\"\n      :options=\"chartOptions\"\n      @click=\"onClick\"\n    />\n    <div class=\"description\">\n      <div class=\"badge yellow\">\n        v-model:selectedLabel\n      </div>\n      {{ defaultSelectLabel }}\n      <br>\n      <br>\n      <div class=\"badge yellow\">\n           (selected)\n      </div>\n      {{ clickedLabel }}\n    </div>\n  </div>\n</template>\n\n<script>\n  import { ref, onMounted, reactive } from 'vue';\n  import dayjs from 'dayjs';\n\n  export default {\n    setup() {\n      const chartData = reactive({\n        series: {\n          series1: { name: 'series#1', show: true, type: 'bar', showValue: { use: true } },\n          series3: { name: 'series#2', show: true, type: 'line', combo: true },\n        },\n        labels: [],\n        data: {\n          series1: [],\n          series3: [],\n        },\n      });\n\n      const chartOptions = {\n        width: '100%',\n        height: '80%',\n        thickness: 0.8,\n        title: {\n          text: 'Chart Title',\n          show: true,\n        },\n        legend: {\n          show: true,\n          position: 'right',\n        },\n        axesX: [{\n          type: 'time',\n          timeFormat: 'mm:ss',\n          interval: 'second',\n          categoryMode: true,\n        }],\n        axesY: [{\n          type: 'linear',\n          showGrid: true,\n          startToZero: true,\n          autoScaleRatio: 0.1,\n        }],\n        selectLabel: {\n          use: true,\n          limit: 1,\n          useDeselectOverflow: true,\n          showTip: true,\n          fixedPosTop: true,\n          useApproximateValue: true,\n          tipBackground: '#FF0000',\n          useSeriesOpacity: true,\n          useLabelOpacity: true,\n        },\n      };\n\n      const defaultSelectLabel = ref({\n        dataIndex: [2],\n      });\n\n      const clickedLabel = ref();\n      const onClick = ({ selected }) => {\n        clickedLabel.value = selected;\n      };\n\n      let timeValue = dayjs().format('YYYY-MM-DD HH:mm:ss');\n\n      const addRandomChartData = () => {\n        timeValue = dayjs(timeValue).add(1, 'second');\n        chartData.labels.push(dayjs(timeValue));\n\n        Object.values(chartData.data).forEach((seriesData) => {\n          const randomValue = Math.floor(Math.random() * ((5000 - 5) + 1)) + 5;\n          seriesData.push(randomValue);\n        });\n      };\n\n      onMounted(() => {\n        for (let ix = 0; ix < 10; ix++) {\n          addRandomChartData();\n        }\n      });\n\n      return {\n        chartData,\n        chartOptions,\n        defaultSelectLabel,\n        clickedLabel,\n        onClick,\n      };\n    },\n  };\n</script>\n\n<style lang=\"scss\" scoped>\n.description {\n  position: relative;\n}\n</style>\n";