export default "<template>\n  <ev-chart\n      :data=\"chartData\"\n      :options=\"chartOptions\"\n  />\n</template>\n\n<script>\nimport { onMounted, reactive } from 'vue';\n\n  export default {\n    setup() {\n      const chartData = reactive({\n        series: {\n          series1: {\n            name: 'series#1',\n          },\n        },\n        labels: {\n          x: ['12a', '1a', '2a', '3a', '4a', '5a', '6a', '7a', '8a', '9a', '10a', '11a',\n              '12p', '1p', '2p', '3p', '4p', '5p', '6p', '7p', '8p', '9p', '10p', '11p'],\n          y: ['SUN', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'SAT'],\n        },\n        data: {\n          series1: [],\n        },\n      });\n\n\n      const chartOptions = {\n        type: 'heatMap',\n        width: '100%',\n        title: {\n          text: 'Chart Title',\n          show: true,\n        },\n        indicator: {\n          use: true,\n        },\n        axesX: [{\n          type: 'step',\n          showGrid: false,\n        }],\n        axesY: [{\n          type: 'step',\n          showGrid: false,\n        }],\n        heatMapColor: {\n          min: '#FFC19E',\n          max: '#CC3D3D',\n          rangeCount: 5,\n        },\n        tooltip: {\n          use: true,\n        },\n      };\n\n      const createChartData = () => {\n        const labelX = chartData.labels.x;\n        const labelY = chartData.labels.y;\n        for (let ix = 0; ix < labelX.length; ix++) {\n          for (let iy = 0; iy < labelY.length; iy++) {\n            const randomCount = Math.floor(Math.random() * 500) + 1;\n            chartData.data.series1.push({\n              x: labelX[ix],\n              y: labelY[iy],\n              value: randomCount,\n            });\n          }\n        }\n      };\n\n      onMounted(() => {\n        createChartData();\n      });\n\n      return {\n        chartData,\n        chartOptions,\n      };\n    },\n  };\n</script>\n";