export default "<template>\n  <div class=\"case\">\n    <ev-chart\n      ref=\"chart\"\n      v-model:selectedItem=\"defaultSelectItem\"\n      :data=\"chartData\"\n      :options=\"chartOptions\"\n      @click=\"onClick\"\n      @dbl-click=\"onDblClick\"\n    />\n    <div class=\"description\">\n      <ev-button\n          @click=\"toggleSelectData\">\n        select toggle 1 - 2\n      </ev-button>\n      <br><br>\n      <div class=\"badge yellow\">\n        useSeriesOpacity\n      </div>\n      <ev-toggle v-model=\"useSeriesOpacity\"/>\n      <br><br>\n      <div class=\"badge yellow\">\n        showTextTip\n      </div>\n      <ev-toggle v-model=\"showTextTip\"/>\n      <br><br>\n      <div class=\"badge yellow\">\n        useMaxTip\n      </div>\n      <ev-toggle v-model=\"useMaxTip\"/>\n      <br><br>\n      <div>\n        <div class=\"badge yellow\">\n            v-model\n        </div>\n        {{ defaultSelectItem }}\n        <br><br>\n        <div class=\"badge yellow\">\n           \n        </div>\n        {{ clickedLabel }}\n        <br><br>\n        <div class=\"badge yellow\">\n            \n        </div>\n        {{ dblClickedLabel }}\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { reactive, ref } from 'vue';\n\n  export default {\n    setup() {\n      const chart = ref(null);\n\n      const chartData = {\n        series: {\n          series1: { name: 'series#1' },\n          series2: { name: 'series#2' },\n        },\n        labels: ['value1', 'value2', 'value3', 'value4', 'value5'],\n        data: {\n          series1: [100, 150, 51, 150, 350],\n          series2: [100, 150, 51, 150, 450],\n        },\n      };\n\n      const useSeriesOpacity = ref(true);\n      const showTextTip = ref(false);\n      const useMaxTip = ref(false);\n      const chartOptions = reactive({\n        type: 'bar',\n        thickness: 0.8,\n        width: '100%',\n        title: {\n          text: 'Chart Title',\n          show: true,\n        },\n        legend: {\n          show: true,\n          position: 'right',\n        },\n        axesX: [{\n          type: 'step',\n          showGrid: false,\n          labelStyle: {\n            fitWidth: true,\n            fitDir: 'left',\n          },\n        }],\n        axesY: [{\n          showAxis: true,\n          type: 'linear',\n          startToZero: true,\n          autoScaleRatio: 0.1,\n          showGrid: false,\n        }],\n        selectItem: {\n          use: true,\n          showTextTip,\n          tipStyle: {\n            background: '#FF00FF',\n          },\n          useDeselectItem: true,\n          useSeriesOpacity,\n        },\n        maxTip: {\n          use: useMaxTip,\n          showTextTip: true,\n          tipStyle: {\n            background: '#FF0000',\n          },\n        },\n      });\n\n      const clickedLabel = ref(\"''\");\n      const onClick = (target) => {\n        clickedLabel.value = target.label;\n      };\n\n      const dblClickedLabel = ref(\"''\");\n      const onDblClick = (target) => {\n        dblClickedLabel.value = target.label;\n      };\n\n      const defaultSelectItem = ref({\n        seriesID: 'series1',\n        dataIndex: 1,\n      });\n\n      const toggleSelectData = () => {\n        const idx = defaultSelectItem.value.dataIndex;\n        defaultSelectItem.value.dataIndex = idx === 1 ? 2 : 1;\n      };\n\n      return {\n        chart,\n        chartData,\n        chartOptions,\n        clickedLabel,\n        dblClickedLabel,\n        defaultSelectItem,\n        useSeriesOpacity,\n        showTextTip,\n        useMaxTip,\n        onClick,\n        onDblClick,\n        toggleSelectData,\n      };\n    },\n  };\n</script>\n\n<style lang=\"scss\">\n</style>\n";