export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Group</p>\n    <ev-checkbox-group\n      v-model=\"checkboxGroup\"\n    >\n      <ev-checkbox label=\"Option A\">A</ev-checkbox>\n      <ev-checkbox label=\"Option B\" />\n      <div>\n        <ev-checkbox label=\"Option C\" />\n      </div>\n      <div>\n        <ev-checkbox label=\"Option D\" />\n      </div>\n    </ev-checkbox-group>\n    <div class=\"description\">\n      <span class=\"badge\">\n        Checkbox Group Values\n      </span>\n      {{ checkboxGroup }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Event</p>\n    <ev-checkbox-group\n      v-model=\"checkboxGroup2\"\n      @change=\"changeGroup2\"\n    >\n      <ev-checkbox label=\"Option A\">A</ev-checkbox>\n      <ev-checkbox label=\"Option B\">B</ev-checkbox>\n      <ev-checkbox label=\"Option C\" />\n      <ev-checkbox label=\"Option D\" />\n    </ev-checkbox-group>\n    <div class=\"description\">\n      <span class=\"badge\">\n        Checkbox Group Values\n      </span>\n      <button\n        class=\"btn\"\n        @click=\"clickButton\"\n      >\n        Add 'Option A'\n      </button>\n      &nbsp; {{ checkboxGroup2 }}\n    </div>\n    <div class=\"description\">\n      <span class=\"badge\">\n        Checkbox Group Change Event\n      </span>\n      &nbsp; {{ changeEventVal }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">All Check</p>\n    <ev-checkbox-group\n      v-model=\"checkboxGroup3\"\n      @change=\"changeGroupValues\"\n    >\n      <ev-checkbox\n        v-for=\"(info, idx) in checkboxList3\"\n        :key=\"`${info.label}_${idx}`\"\n        :label=\"`${info.label}`\"\n      >\n        {{ info.text }}\n      </ev-checkbox>\n    </ev-checkbox-group>\n    <div class=\"description\">\n      <span class=\"badge\">\n        Checkbox Group Values\n      </span>\n      <ev-checkbox\n        v-model=\"allCheck\"\n        v-model:indeterminate=\"indeterminate\"\n        @change=\"changeAllCheck\"\n      >\n        All Check & Indeterminate\n      </ev-checkbox>\n      {{ checkboxGroup3 }} / {{ indeterminate }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Button Type</p>\n    <ev-checkbox-group\n      v-model=\"buttonGroup\"\n      type=\"button\"\n    >\n      <ev-checkbox label=\"Option A\" />\n      <ev-checkbox label=\"Option B\" />\n      <ev-checkbox label=\"Option C\" />\n    </ev-checkbox-group>\n    <br>\n    <ev-checkbox-group\n      v-model=\"buttonGroup\"\n      type=\"button\"\n    >\n      <ev-checkbox\n        label=\"Option A\"\n        disabled\n      />\n      <ev-checkbox\n        label=\"Option B\"\n        disabled\n      />\n      <ev-checkbox label=\"Option C\" />\n    </ev-checkbox-group>\n  </div>\n</template>\n\n<script>\nimport { ref } from 'vue';\nimport { isEqual, sortBy } from 'lodash-es';\n\nexport default {\n  setup() {\n    const checkboxGroup = ref(['Option A', 'Option B']);\n\n    const checkboxGroup2 = ref([]);\n    const clickButton = () => {\n      if (!checkboxGroup2.value.includes('Option A')) {\n        checkboxGroup2.value.push('Option A');\n      }\n    };\n    const changeEventVal = ref([]);\n    const changeGroup2 = (val) => { changeEventVal.value = val; };\n\n    const checkboxGroup3 = ref([]);\n    const allCheck = ref(false);\n    const checkboxList3 = [\n      {\n        label: 'Option A',\n        text: 'A',\n      },\n      {\n        label: 'Option B',\n        text: 'B',\n      },\n      {\n        label: 'Option C',\n        text: 'C',\n      },\n      {\n        label: 'Option D',\n        text: 'D',\n      },\n    ];\n    const labels = ['Option A', 'Option B', 'Option C', 'Option D'];\n\n    const changeAllCheck = () => {\n      if (allCheck.value) {\n        checkboxGroup3.value = labels;\n      } else {\n        checkboxGroup3.value = [];\n      }\n    };\n    const indeterminate = ref(false);\n    const changeGroupValues = (val) => {\n      allCheck.value = isEqual(sortBy(val), sortBy(labels));\n      indeterminate.value = !!(val.length && val.length !== labels.length);\n    };\n\n    const buttonGroup = ref([]);\n\n    return {\n      checkboxGroup,\n      checkboxGroup2,\n      clickButton,\n      changeEventVal,\n      changeGroup2,\n      checkboxGroup3,\n      allCheck,\n      checkboxList3,\n      indeterminate,\n      changeAllCheck,\n      changeGroupValues,\n      buttonGroup,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\">\n</style>\n";