export default "<template>\n  <ev-chart\n    :data=\"chartData\"\n    :options=\"chartOptions\"\n  />\n</template>\n\n<script>\n  export default {\n    setup() {\n      const chartData = {\n        series: {\n          series1: { name: ' ' },\n        },\n        labels: ['', '', '', '', ''],\n        data: {\n          series1: [100, 150, 51, 150, 350],\n        },\n      };\n\n      const chartOptions = {\n        type: 'bar',\n        cPadRatio: 0.1,\n        axesX: [{\n          type: 'step',\n        }],\n        axesY: [{\n          showAxis: true,\n          type: 'linear',\n          startToZero: true,\n          autoScaleRatio: 0.1,\n        }],\n      };\n\n      return {\n        chartData,\n        chartOptions,\n      };\n    },\n  };\n</script>\n\n<style lang=\"scss\" scoped>\n</style>\n";