export default "<template>\n  <ev-chart\n    :data=\"chartData\"\n    :options=\"chartOptions\"\n  />\n</template>\n\n<script>\n  export default {\n    setup() {\n      const chartData = {\n        series: {\n          series1: { name: 'series#1', color: '#FEC64F', showValue: { use: true } },\n          series2: { name: 'series#2', color: '#48D1CC', showValue: { use: true } },\n        },\n        labels: [\n          'value1',\n          'value2',\n          'value3',\n          'value4',\n          'value5',\n        ],\n        data: {\n          series1: [100, 150, 51, 150, { value: 350, color: '#FF0000' }],\n          series2: [150, 100, 151, 50, 250],\n        },\n      };\n\n      const chartOptions = {\n        type: 'bar',\n        thickness: 0.8,\n        width: '100%',\n        padding: {\n          right: 80,\n        },\n        title: {\n          text: 'Chart Title',\n            show: true,\n        },\n        legend: {\n          show: true,\n            position: 'right',\n        },\n        axesX: [{\n          type: 'step',\n          showGrid: false,\n          labelStyle: {\n            fitWidth: true,\n            fitDir: 'left',\n          },\n          plotBands: [{\n            color: 'rgba(250, 222, 76, 0.8)',\n            from: 2.5,\n            to: 3.5,\n            label: {\n              show: true,\n              text: 'X Plot Band ZONE',\n              fontColor: '#FFA500',\n            },\n          }],\n        }],\n          axesY: [{\n          type: 'linear',\n          startToZero: true,\n          autoScaleRatio: 0.1,\n          showGrid: false,\n          plotLines: [{\n            value: 180,\n            color: '#FFA500',\n            label: {\n              show: true,\n              text: 'Warning',\n              fontColor: '#FFA500',\n            },\n          }, {\n            value: 300,\n            label: {\n              show: true,\n              lineWidth: 1,\n              lineColor: '#000000',\n              fillColor: '#FF0000',\n              fontColor: '#FFFFFF',\n              text: 'Critical',\n            },\n          }],\n            plotBands: [{\n              color: 'rgba(104, 173, 244, 0.3)',\n              from: 0, //  \n              to: 120,\n              label: {\n                show: true,\n                text: 'Y Plot Band Zone',\n                fontColor: '#68ADF4',\n              },\n            }],\n        }],\n      };\n\n      return {\n        chartData,\n        chartOptions,\n      };\n    },\n  };\n</script>\n";