export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Multiple Select</p>\n    <ev-select\n      v-model=\"selectVal1\"\n      :items=\"items1\"\n      placeholder=\"Please select values.\"\n      multiple\n      @change=\"changeEvent\"\n    />\n    <div class=\"description\">\n      first selectbox value : {{ selectVal1 }}\n    </div>\n    <div class=\"description\">\n      change val : {{ changeVal }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Multiple Clearable Select</p>\n    <ev-select\n      v-model=\"selectVal1\"\n      :items=\"items1\"\n      multiple\n      clearable\n    />\n    <div class=\"description\">\n      first selectbox value : {{ selectVal1 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Multiple Collapse Tags Select</p>\n    <ev-select\n      v-model=\"selectVal1\"\n      :items=\"items1\"\n      multiple\n      collapse-tags\n    />\n    <div class=\"description\">\n      first selectbox value : {{ selectVal1 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Multiple Disabled Select</p>\n    <ev-select\n      v-model=\"selectVal1\"\n      :items=\"items1\"\n      multiple\n      clearable\n      disabled\n    />\n    <div class=\"description\">\n      first selectbox value : {{ selectVal1 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Multiple Filterable Select</p>\n    <ev-select\n      v-model=\"selectVal1\"\n      :items=\"items1\"\n      multiple\n      filterable\n    />\n    <div class=\"description\">\n      first selectbox value : {{ selectVal1 }}\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref } from 'vue';\n\nexport default {\n  setup() {\n    const selectVal1 = ref(['value4', 'value3']);\n    const items1 = ref([\n      {\n        name: 'name0name0name0name0name0name0',\n        value: 'value0',\n        iconClass: 'ev-icon-query',\n      },\n      {\n        name: 'name1name1name1name1name1name1',\n        value: 'value1',\n      },\n      {\n        name: 'name2name2name2name2name2name2',\n        value: 'value2',\n      },\n      {\n        name: 'name3name3name3name3name3name3',\n        value: 'value3',\n        iconClass: 'ev-icon-bell',\n      },\n      {\n        name: 'name4name4name4name4name4name4',\n        value: 'value4',\n      },\n      {\n        name: 'name5name5name5name5name5name5',\n        value: 'value5',\n      },\n      {\n        name: 'name6name6name6name6name6name6',\n        value: 'value6',\n      },\n      {\n        name: 'name7name7name7name7name7name7',\n        value: 'value7',\n      },\n    ]);\n    const changeVal = ref();\n    const changeEvent = (val) => {\n      changeVal.value = val;\n    };\n\n    return {\n      selectVal1,\n      items1,\n      changeVal,\n      changeEvent,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\">\n</style>\n";