export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Common</p>\n    <ev-text-field\n      v-model=\"modelValue1\"\n      placeholder=\"Search\"\n      type=\"search\"\n      @search=\"searchInput\"\n    />\n    <div class=\"description\">\n      <span class=\"badge yellow\">\n        type=\"search\"\n      </span>\n      <span class=\"badge\">\n        Search Event\n      </span>\n      {{ searchValue }}\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref } from 'vue';\n\nexport default {\n  setup() {\n    const modelValue1 = ref();\n    const searchValue = ref();\n    const searchInput = (val) => {\n      searchValue.value = val;\n    };\n\n    return {\n      modelValue1,\n      searchValue,\n      searchInput,\n    };\n  },\n};\n</script>\n";