export default "<template>\n  <ev-chart\n      :data=\"chartData\"\n      :options=\"chartOptions\"\n  />\n</template>\n\n<script>\nimport { reactive } from 'vue';\n\n  export default {\n    setup() {\n      const chartData = reactive({\n        series: {\n          series1: {\n            name: 'series#1',\n            showValue: {\n              use: true,\n              decimalPoint: 2,\n            },\n          },\n        },\n        labels: {\n          x: ['2020', '2021', '2022'],\n          y: ['Q1', 'Q2', 'Q3', 'Q4'],\n        },\n        data: {\n          series1: [\n            { x: '2020', y: 'Q1', value: 46.57 },\n            { x: '2020', y: 'Q2', value: 50.22 },\n            { x: '2020', y: 'Q3', value: 45.10 },\n            { x: '2020', y: 'Q4', value: 57.35 },\n            { x: '2021', y: 'Q1', value: 60.55 },\n            { x: '2021', y: 'Q2', value: 63.35 },\n            { x: '2021', y: 'Q3', value: 58.67 },\n            { x: '2021', y: 'Q4', value: 57.55 },\n            { x: '2022', y: 'Q1', value: 66.12 },\n            { x: '2022', y: 'Q2', value: 80.15 },\n            { x: '2022', y: 'Q3', value: 82.22 },\n            { x: '2022', y: 'Q4', value: 84.00 },\n          ],\n        },\n      });\n\n\n      const chartOptions = {\n        type: 'heatMap',\n        width: '100%',\n        title: {\n          text: 'Chart Title',\n          show: true,\n        },\n        indicator: {\n          use: true,\n        },\n        axesX: [{\n          type: 'step',\n          showGrid: false,\n        }],\n        axesY: [{\n          type: 'step',\n          showGrid: false,\n        }],\n        heatMapColor: {\n          min: '#F9945A',\n          max: '#9D3B02',\n          rangeCount: 4,\n          stroke: {\n            show: true,\n            color: '#FFFFFF',\n            lineWidth: 2,\n            radius: 10,\n          },\n          decimalPoint: 2,\n        },\n        tooltip: {\n          use: true,\n        },\n        legend: {\n          type: 'gradient',\n          position: 'right',\n        },\n      };\n\n      return {\n        chartData,\n        chartOptions,\n      };\n    },\n  };\n</script>\n";