export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Common</p>\n    <ev-tree\n      :data=\"commonData\"\n      @click-node=\"getClickedNode\"\n    />\n    <div class=\"description\">\n      <div class=\"badge yellow\">\n         \n      </div>\n      {{ clickedNodeInfo }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Checkbox</p>\n    <ev-tree\n      :data=\"checkboxExampleData\"\n      :use-checkbox=\"true\"\n      @check=\"getCheckedNode\"\n    />\n    <div class=\"description\">\n      <div class=\"badge yellow\">\n          \n      </div>\n      {{ checkedNodeInfo }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Dblclick-node</p>\n    <ev-tree\n      :data=\"dbClickedData\"\n      @dblclick-node=\"getDblClickedNode\"\n    />\n    <div class=\"description\">\n      <div class=\"badge yellow\">\n          \n      </div>\n      {{ dbclickedNodeInfo }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Disabled</p>\n    <ev-tree :data=\"disableExData\"/>\n    <div class=\"description\">\n      'disabled'      .\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">ContextMenu</p>\n    <ev-tree\n      :data=\"contextmenuExData\"\n      :context-menu-items=\"menuItems\"\n    />\n    <div class=\"description\">\n      <div class=\"badge yellow\">\n         \n      </div>\n      {{ contextMenuInfo }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Custom Expand/Collapse Icon</p>\n    <ev-tree\n      :data=\"commonData\"\n      expand-icon=\"ev-icon-square-plus\"\n      collapse-icon=\"ev-icon-square-minus\"\n    />\n    <div class=\"description\">\n      'expand-icon' 'collapse-icon'      .\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">IconClass</p>\n    <ev-tree :data=\"iconClassExData\"/>\n    <div class=\"description\">\n      'iconClass'        .\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Filter node</p>\n    <ev-text-field\n      v-model=\"searchVm\"\n      placeholder=\"Search\"\n      type=\"search\"\n      @search=\"searchInput\"\n    />\n    <ev-tree\n      :data=\"searchExData\"\n      :use-checkbox=\"true\"\n      :search-word=\"searchValue\"\n    />\n    <div class=\"description\">\n      'ev-text-field'      'ev-tree'    .\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref } from 'vue';\n\nexport default {\n  setup() {\n    const commonData = ref([\n      {\n        title: 'Root',\n        value: 'root',\n        expand: true,\n        children: [\n          {\n            title: 'Parent A',\n            value: 'p1',\n            expand: true,\n            children: [\n              {\n                title: 'Leaf AA',\n                value: 'p1a',\n                expand: true,\n                children: [\n                  {\n                    title: 'Leaf AAA',\n                  },\n                ],\n              },\n              {\n                title: 'Leaf AB',\n                value: 'p1b',\n              },\n            ],\n          },\n        ],\n      },\n    ]);\n    const checkboxExampleData = ref([\n      {\n        title: 'Root',\n        expand: true,\n        children: [\n          {\n            title: 'Parent A',\n            expand: true,\n            children: [\n              {\n                title: 'Child AA',\n                expand: true,\n                children: [\n                  {\n                    title: 'GrandChild A',\n                    checked: true,\n                  },\n                ],\n              },\n              {\n                title: 'Child AB',\n              },\n            ],\n          },\n          {\n            title: 'Parent B',\n            expand: true,\n            children: [\n              {\n                title: 'Child BA',\n              },\n              {\n                title: 'Child BB',\n                checked: true,\n              },\n            ],\n          },\n        ],\n      },\n    ]);\n    const dbClickedData = ref([\n      {\n        title: 'Root',\n        value: 'root',\n        expand: true,\n        children: [\n          {\n            title: 'Parent A',\n            value: 'p1',\n            expand: true,\n            children: [\n              {\n                title: 'Leaf AA',\n                value: 'p1a',\n                expand: true,\n                children: [\n                  {\n                    title: 'Leaf AAA',\n                  },\n                ],\n              },\n              {\n                title: 'Leaf AB',\n                value: 'p1b',\n              },\n            ],\n          },\n        ],\n      },\n    ]);\n    const disableExData = ref([\n      {\n        title: 'Root',\n        expand: true,\n        children: [\n          {\n            title: 'Parent A',\n            expand: true,\n            disabled: true,\n          },\n          {\n            title: 'Parent B',\n            expand: true,\n            children: [\n              {\n                title: 'Leaf BA',\n                disabled: true,\n              },\n              {\n                title: 'Leaf BB',\n              },\n            ],\n          },\n        ],\n      },\n    ]);\n    const contextmenuExData = ref([\n      {\n        title: 'Root',\n        expand: true,\n        children: [\n          {\n            title: 'Parent A',\n            expand: true,\n          },\n          {\n            title: 'Parent B',\n            expand: true,\n            children: [\n              {\n                title: 'Leaf BA',\n              },\n              {\n                title: 'Leaf BB',\n              },\n            ],\n          },\n        ],\n      },\n    ]);\n    const iconClassExData = ref([\n      {\n        title: 'Root',\n        expand: true,\n        children: [\n          {\n            title: 'Parent A',\n            expand: true,\n            iconClass: 'ev-icon-folder',\n            children: [\n              {\n                title: 'Leaf AA',\n                expand: true,\n                iconClass: 'ev-icon-document-vertically',\n              },\n              {\n                title: 'Leaf AB',\n              },\n            ],\n          },\n          {\n            title: 'Parent B',\n            expand: true,\n            iconClass: 'ev-icon-folder2',\n            children: [\n              {\n                title: 'Leaf BA',\n              },\n              {\n                title: 'Leaf BB',\n              },\n            ],\n          },\n        ],\n      },\n    ]);\n    const searchExData = ref([\n      {\n        title: 'Root',\n        expand: true,\n        children: [\n          {\n            title: 'Parent A',\n            expand: true,\n            iconClass: 'ev-icon-folder',\n            children: [\n              {\n                title: 'Leaf AA',\n                expand: true,\n                iconClass: 'ev-icon-document-vertically',\n                children: [\n                  {\n                    title: 'Leaf AA_a1',\n                    expand: true,\n                    iconClass: 'ev-icon-folder',\n                    children: [\n                      {\n                        title: 'Team AAA_a1',\n                        iconClass: 'ev-icon-folder',\n                        children: [\n                          {\n                            title: 'Member A',\n                            iconClass: 'ev-icon-dolphin',\n                          },\n                          {\n                            title: 'tEAm C',\n                            expand: true,\n                            iconClass: 'ev-icon-folder',\n                            children: [\n                              {\n                                title: 'I am leaf.',\n                              },\n                              {\n                                title: 'I am another leaf.',\n                              },\n                            ],\n                          },\n                        ],\n                      },\n                      {\n                        title: 'Team A',\n                        iconClass: 'ev-icon-folder',\n                        children: [{\n                          title: 'I am a member of Team A.',\n                          iconClass: 'ev-icon-user2',\n                        }],\n                      },\n                    ],\n                  },\n                  {\n                    title: 'Leaf AA_a2',\n                  },\n                ],\n              },\n              {\n                title: 'Leaf AB',\n                expand: true,\n                iconClass: 'ev-icon-folder',\n                children: [\n                  {\n                    title: 'Leaf AB_b1',\n                    iconClass: 'ev-icon-moon',\n                  },\n                  {\n                    title: 'Leaf AB_b2',\n                  },\n                ],\n              },\n            ],\n          },\n          {\n            title: 'Parent B',\n            expand: true,\n            iconClass: 'ev-icon-folder2',\n            children: [\n              {\n                title: 'Leaf BA',\n              },\n              {\n                title: 'Leaf BB',\n              },\n            ],\n          },\n        ],\n      },\n    ]);\n\n    const clickedNodeInfo = ref({});\n    const checkedNodeInfo = ref({});\n    const dbclickedNodeInfo = ref({});\n    const contextMenuInfo = ref('');\n\n    const menuItems = ref([\n      {\n        text: 'ContextMenu1',\n        click: () => { contextMenuInfo.value = 'ContextMenu1'; },\n      },\n      {\n        text: 'ContextMenu2',\n        click: () => { contextMenuInfo.value = 'ContextMenu2'; },\n      },\n    ]);\n\n    const getCheckedNode = (checkedNode) => {\n      checkedNodeInfo.value = checkedNode;\n    };\n\n    const getClickedNode = (clickedNode) => {\n      clickedNodeInfo.value = clickedNode;\n    };\n\n    const getDblClickedNode = (clickedNode) => {\n      dbclickedNodeInfo.value = clickedNode;\n    };\n\n    const searchVm = ref('');\n    const searchValue = ref('');\n    const searchInput = (val) => {\n      searchValue.value = val;\n    };\n\n    return {\n      commonData,\n      checkboxExampleData,\n      dbClickedData,\n      disableExData,\n      contextmenuExData,\n      iconClassExData,\n      searchExData,\n      menuItems,\n      clickedNodeInfo,\n      dbclickedNodeInfo,\n      checkedNodeInfo,\n      contextMenuInfo,\n      searchValue,\n      searchVm,\n      getCheckedNode,\n      getClickedNode,\n      getDblClickedNode,\n      searchInput,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\">\n</style>\n";