export default "<template>\n  <div class=\"case\">\n    <ev-grid\n      v-model:selected=\"selected\"\n      v-model:checked=\"checked\"\n      :columns=\"columns\"\n      :rows=\"tableData\"\n      width=\"100%\"\n      height=\"300\"\n      :option=\"{\n        adjust: true,\n        rowHeight: 45,\n        useFilter: false,\n        useCheckbox: {\n          use: true,\n          mode: 'multi',\n          headerCheck: true,\n        },\n        style: {\n          border: 'rows',\n        },\n        page: pageInfo,\n        useSummary: true,\n      }\"\n    >\n      <template #increment_mb=\"{ item }\">\n        {{ getIncrementValue(item) }}\n      </template>\n    </ev-grid>\n    <div class=\"description\">\n      <div class=\"form-rows\">\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">Total (MB)</span>\n          <ev-select\n            v-model=\"totalSummaryType\"\n            :items=\"summaryTypes\"\n          />\n        </div>\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">Used (MB)</span>\n          <ev-select\n            v-model=\"usedSummaryType\"\n            :items=\"summaryTypes\"\n          />\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref, reactive, computed } from 'vue';\nimport { numberWithComma } from '@/common/utils';\n\nexport default {\n  setup() {\n    const totalSummaryType = ref('sum');\n    const usedSummaryType = ref('average');\n    const summaryTypes = ref([\n      { name: 'sum', value: 'sum' },\n      { name: 'average', value: 'average' },\n      { name: 'max', value: 'max' },\n      { name: 'min', value: 'min' },\n      { name: 'count', value: 'count' },\n    ]);\n    const tableData = ref([]);\n    const selected = ref([]);\n    const checked = ref([]);\n    const columns = ref([\n      { caption: 'Name',\n        field: 'name',\n        type: 'string',\n        summaryRenderer: 'Total', // text \n      },\n      { caption: 'RTS Port',\n        field: 'rts_port',\n        type: 'stringNumber',\n        width: 80,\n      },\n      { caption: 'Total (MB)',\n        field: 'total_mb',\n        type: 'number',\n        summaryType: totalSummaryType,\n        summaryRenderer: 'value: {0}', // text +    \n      },\n      { caption: 'Used (MB)',\n        field: 'used_mb',\n        type: 'number',\n        summaryType: usedSummaryType, // type  \n      },\n      { caption: 'Increment (MB)',\n        field: 'increment_mb',\n        type: 'number',\n        summaryType: 'sum',\n        summaryData: ['diff'],\n        summaryRenderer: '{0}({1}%)', //    \n      },\n      { caption: 'Diff', field: 'diff', type: 'float', decimal: 1, hide: true },\n    ]);\n    tableData.value = [\n      ['HIDB_DATA_1', '', undefined, 27506, 7185, 2000.7],\n      [null, '', 0, 23659, 0, 1500],\n      ['HIDB_LARGE_1', '25080', 28000, 21695, 1185, -4.7],\n      [null, '4004', 27000, 23685, 0, 0],\n      ['HIDB_INDEX_2', '4004', 26000, 23535, 0, 0],\n      ['', '4004', null, 23659, 0, 0],\n      ['HIDB_L_INDEX_1', '0', 24000, 23695, 0, 0],\n      ['HIDB_L_INDEX_2', '25080', 0, 21691, 0, 0],\n      ['HIDB_L_INDEX_3', '25080', -2000, 20021, 0, 0],\n      ['HIDB_DATA_3', '25080', -10, 14485, 0, 0],\n      ['HIDB_DATA_4', '', 20000, 14396, 2185, -11],\n      ['SYSAUX', '', 11000, 9485, 0, -11],\n      ['USERS', '4004', 10000, 6485, 0, 0],\n      ['UNDOTBS1', '4004', 9000, 3486, 0, 0],\n    ];\n    const pageInfo = reactive({\n      use: false,\n      perPage: 8,\n      total: computed(() => tableData.value.length),\n      useClient: true,\n    });\n    const getIncrementValue = (item) => {\n      const row = item.row[2];\n      const columnIndex = item.column.index;\n      let floatValue = row[columnIndex + 1];\n      floatValue = floatValue.toString().replace(/\\B(?=(\\d{3})+(?!\\d))/g, ',');\n      return `${numberWithComma(row[columnIndex])}(${floatValue}%)`;\n    };\n    return {\n      columns,\n      tableData,\n      selected,\n      checked,\n      pageInfo,\n      totalSummaryType,\n      summaryTypes,\n      usedSummaryType,\n      getIncrementValue,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\" scoped>\n.form-rows {\n  display: flex;\n}\n.form-row {\n  flex: 1;\n  margin: 5px;\n  .badge {\n    margin-bottom: 3px;\n  }\n}\n.ev-toggle {\n  display: block;\n}\n</style>\n";