export default "<template>\n  <div class=\"case\">\n    <ev-chart-group>\n      <ev-chart\n        :data=\"chartData\"\n        :options=\"chartOptions\"\n      />\n      <ev-chart-brush/>\n    </ev-chart-group>\n    <div class=\"description\">\n      <div\n        v-for=\"(option, idx) in titleOptions\"\n        :key=\"idx\"\n        class=\"section\"\n      >\n        <h3 v-if=\"idx===0\" class=\"section-title\"> X Axis </h3>\n        <h3 v-else class=\"section-title\"> Y Axis </h3>\n\n        <div class=\"section-body\">\n          <div class=\"section-item\">\n            <label>use</label>\n            <ev-toggle v-model=\"option.use\"/>\n          </div>\n          <div class=\"section-item\">\n            <label>text</label>\n            <ev-text-field v-model=\"option.text\"/>\n          </div>\n          <div class=\"section-item\">\n            <label>fontWeight</label>\n            <ev-input-number\n              v-model=\"option.fontWeight\"\n              :step=\"100\"\n              :min=\"100\"\n              :max=\"900\"\n            />\n          </div>\n          <div class=\"section-item\">\n            <label>fontSize</label>\n            <ev-input-number\n              v-model=\"option.fontSize\"\n              :step=\"1\"\n              :min=\"1\"\n              :max=\"30\"\n            />\n          </div>\n          <div class=\"section-item\">\n            <label>fontFamily</label>\n            <ev-text-field v-model=\"option.fontFamily\"/>\n          </div>\n          <div class=\"section-item\">\n            <label>textAlign</label>\n            <ev-select\n              v-model=\"option.textAlign\"\n              :items=\"[{\n                name: 'right',\n                value: 'right',\n              }, {\n                name: 'center',\n                value: 'center',\n              }, {\n                name: 'left',\n                value: 'left',\n              }]\"\n            />\n          </div>\n          <div class=\"section-item\">\n            <label>fontStyle</label>\n            <ev-select\n              v-model=\"option.fontStyle\"\n              :items=\"[{\n                name: 'normal',\n                value: 'normal',\n              }, {\n                name: 'italic',\n                value: 'italic',\n              }]\"\n            />\n          </div>\n          <div class=\"section-item\">\n            <label>color</label>\n            <ev-text-field v-model=\"option.color\"/>\n          </div>\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { reactive, ref } from 'vue';\nimport dayjs from 'dayjs';\n\nexport default {\n  setup() {\n    const xAxisTitle = reactive({\n      use: true,\n      text: '1 Week',\n      fontWeight: 400,\n      fontSize: 12,\n      fontFamily: 'Roboto',\n      textAlign: 'right',\n      fontStyle: 'normal',\n      color: '#808080',\n    });\n\n    const yAxisTitle = reactive({\n      use: true,\n      text: 'Amount',\n      fontWeight: 400,\n      fontSize: 12,\n      fontFamily: 'Roboto',\n      textAlign: 'right',\n      fontStyle: 'normal',\n      color: '#808080',\n    });\n\n    const titleOptions = ref([xAxisTitle, yAxisTitle]);\n\n    const time = dayjs().format('YYYY-MM-DD HH:mm:ss');\n\n    const chartData = reactive({\n      series: {\n        series1: { name: 'series#1' },\n      },\n      labels: [\n        dayjs(time),\n        dayjs(time).add(1, 'day'),\n        dayjs(time).add(2, 'day'),\n        dayjs(time).add(3, 'day'),\n        dayjs(time).add(4, 'day'),\n        dayjs(time).add(5, 'day'),\n        dayjs(time).add(6, 'day'),\n      ],\n      data: {\n        series1: [10, 20, 21, 57, 12, 86, 44],\n      },\n    });\n\n    const chartOptions = reactive({\n      type: 'line',\n      width: '100%',\n      height: '300px',\n      title: {\n        text: 'Chart Title',\n        show: true,\n      },\n      legend: {\n        show: false,\n      },\n      axesX: [{\n        type: 'time',\n        timeFormat: 'MM/DD',\n        interval: 'day',\n        title: xAxisTitle,\n      }],\n      axesY: [{\n        type: 'linear',\n        showGrid: true,\n        startToZero: true,\n        autoScaleRatio: 0.1,\n        title: yAxisTitle,\n      }],\n    });\n\n    return {\n      chartData,\n      chartOptions,\n      titleOptions,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\" scoped>\n.section {\n  width: 100%;\n\n  &-title {\n    padding: 10px;\n    background-color: rgba(#FADE4C, 0.6);\n  }\n\n  &-body {\n    display: flex;\n    padding: 0 0 10px 10px;\n    flex-direction: row;\n    flex-wrap: wrap;\n\n    .section-item {\n      display: flex;\n      width: 50%;\n      flex-direction: row;\n      margin-top: 10px;\n\n      label {\n        width: 100px;\n        line-height: 35px;\n        margin-right: 10px;\n        font-weight: 700;\n      }\n\n      .ev-toggle {\n        margin-top: 7px;\n      }\n\n      .ev-text-field, .ev-input-number, .ev-select {\n        width: auto;\n      }\n    }\n  }\n}\n</style>\n";