export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Common</p>\n    <div class=\"loading-wrapper\">\n      <ev-loading v-model=\"isLoading1\" />\n      <div> TITLE </div>\n      <div> CONTENTS </div>\n    </div>\n    <div class=\"description\">\n      <button\n        class=\"btn\"\n        @click=\"changeLoading1\"\n      >\n        click to change loading\n      </button>\n      &nbsp; {{ isLoading1 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Full Screen</p>\n    <div class=\"loading-wrapper\">\n      <ev-loading\n        v-model=\"isLoading2\"\n        :fullscreen=\"true\"\n        :click-outside=\"true\"\n        :icon-class=\"'ev-icon-shard'\"\n        :icon-style=\"{\n          'font-size': '60px',\n        }\"\n      />\n      <div> TITLE2 </div>\n      <div> CONTENTS2 </div>\n    </div>\n    <div class=\"description\">\n      <button\n        class=\"btn\"\n        @click=\"changeLoading2\"\n      >\n        click to change loading\n      </button>\n      &nbsp; {{ isLoading2 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Custom Loading</p>\n    <div class=\"loading-wrapper\">\n      <ev-loading\n        v-model=\"isLoading3\"\n        :click-outside=\"true\"\n      >\n        <i class=\"ev-icon-configuration-line ev-loading-icon\" />\n        <div class=\"loading-text\">\n          <span>{{ loadingText }}</span>\n        </div>\n      </ev-loading>\n      <div> TITLE3 </div>\n      <div> CONTENTS3 </div>\n    </div>\n    <div class=\"description\">\n      <button\n        class=\"btn\"\n        @click=\"changeLoading3\"\n      >\n        click to change loading\n      </button>\n      &nbsp; {{ isLoading3 }}\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref } from 'vue';\n\nexport default {\n  setup() {\n    const isLoading1 = ref(false);\n    const changeLoading1 = () => {\n      isLoading1.value = !isLoading1.value;\n    };\n\n    const isLoading2 = ref(false);\n    const changeLoading2 = () => {\n      isLoading2.value = !isLoading2.value;\n    };\n\n    const loadingText = ref('NOW LOADING');\n    const isLoading3 = ref(false);\n    let timer = null;\n    let num = 0;\n    const changeLoading3 = () => {\n      isLoading3.value = !isLoading3.value;\n      if (isLoading3.value) {\n        timer = setInterval(() => {\n          num++;\n          const dotArr = new Array(num % 5).fill('.');\n          loadingText.value = `NOW LOADING${dotArr.join('')}`;\n        }, 300);\n      } else if (!isLoading3.value) {\n        clearInterval(timer);\n        timer = null;\n        num = 0;\n        loadingText.value = 'NOW LOADING';\n      }\n    };\n\n    return {\n      isLoading1,\n      changeLoading1,\n      isLoading2,\n      changeLoading2,\n      isLoading3,\n      changeLoading3,\n      loadingText,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\">\n.loading-wrapper {\n  position: relative;\n  width: 400px;\n  height: 400px;\n  background-color: #5AC8FA;\n  overflow: auto;\n}\n.ev-loading-icon {\n  display: inline-block;\n  font-size: 25px;\n  animation: rotating 2s linear infinite;\n}\n.loading-text {\n  font-size: 12px;\n  padding: 10px;\n  user-select: none;\n}\n\n@keyframes rotating {\n  from {\n    transform: rotate(360deg);\n  }\n  to {\n    transform: rotate(0);\n  }\n}\n</style>\n";