export default "<template>\n  <ev-chart\n    :data=\"chartData\"\n    :options=\"chartOptions\"\n  />\n</template>\n\n<script>\n  export default {\n    setup() {\n      const chartData = {\n        series: {\n          series1: { name: 'series#1', color: '#FEC64F', showValue: { use: true } },\n          series2: { name: 'series#2', color: '#48D1CC', showValue: { use: true, decimalPoint: 1 } },\n        },\n        labels: [\n          'value1',\n          'value2',\n          'value3',\n          'value4',\n          'value5',\n        ],\n        data: {\n          series1: [null, 150, 0, 150, { value: 350, color: '#FF0000' }],\n          series2: [null, 100.2, 0, 50.221, 250.123],\n        },\n      };\n\n      const chartOptions = {\n        type: 'bar',\n        thickness: 0.8,\n        width: '100%',\n        padding: {\n          right: 40,\n        },\n        title: {\n          text: 'Chart Title',\n            show: true,\n        },\n        legend: {\n          show: true,\n            position: 'right',\n        },\n        axesX: [{\n          type: 'step',\n          showGrid: false,\n          labelStyle: {\n            fitWidth: true,\n            fitDir: 'left',\n          },\n        }],\n          axesY: [{\n          type: 'linear',\n          startToZero: true,\n          autoScaleRatio: 0.1,\n          showGrid: false,\n        }],\n        tooltip: {\n          showAllValueInRange: true,\n          formatter: ({ y }) => `${y}`,\n        },\n      };\n\n      return {\n        chartData,\n        chartOptions,\n      };\n    },\n  };\n</script>\n\n<style lang=\"scss\">\n</style>\n";