export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Common</p>\n    <ev-text-field\n      v-model=\"modelValue1\"\n      placeholder=\"Please enter the content\"\n      type=\"text\"\n    />\n    <div class=\"description\">\n      <span class=\"badge yellow\">\n        type=\"text\"\n      </span>\n      <span class=\"badge\">\n        Text Field Value\n      </span>\n      {{ modelValue1 }}\n    </div>\n  </div>\n\n  <div class=\"case\">\n    <p class=\"case-title\">Readonly</p>\n    <ev-text-field\n      v-model=\"modelValue2\"\n      type=\"text\"\n      readonly\n    />\n  </div>\n\n  <div class=\"case\">\n    <p class=\"case-title\">Disabled</p>\n    <ev-text-field\n      v-model=\"modelValue3\"\n      type=\"text\"\n      disabled\n    />\n  </div>\n\n  <div class=\"case\">\n    <p class=\"case-title\">Clearable</p>\n    <ev-text-field\n      v-model=\"modelValue4\"\n      placeholder=\"Please enter the content\"\n      type=\"text\"\n      clearable\n    />\n  </div>\n\n  <div class=\"case\">\n    <p class=\"case-title\">Max Length</p>\n    <div style=\"display: flex;\">\n      <ev-text-field\n        v-model=\"modelValue5\"\n        placeholder=\"Please enter the content\"\n        type=\"text\"\n        :max-length=\"3\"\n      />\n      <ev-text-field\n        v-model=\"modelValue5\"\n        placeholder=\"Please enter the content\"\n        type=\"text\"\n        :max-length=\"3\"\n        show-max-length\n      />\n    </div>\n  </div>\n\n  <div class=\"case\">\n    <p class=\"case-title\">Error Message</p>\n    <ev-text-field\n      v-model=\"modelValue6\"\n      placeholder=\"Please enter the content\"\n      type=\"text\"\n      :error-msg=\"errMsg\"\n      @input=\"checkValid\"\n    />\n  </div>\n</template>\n\n<script>\nimport { ref } from 'vue';\n\nexport default {\n  setup() {\n    const modelValue1 = ref();\n    const modelValue2 = ref('Read Only Content');\n    const modelValue3 = ref('Disabled Content');\n    const modelValue4 = ref();\n    const modelValue5 = ref();\n    const modelValue6 = ref('1234');\n    const errMsg = ref();\n    const checkValid = () => {\n      const regexp = /^[0-9]*$/;\n      if (!regexp.test(modelValue6.value)) {\n        errMsg.value = '  !';\n      } else {\n        errMsg.value = '';\n      }\n    };\n    checkValid();\n    return {\n      modelValue1,\n      modelValue2,\n      modelValue3,\n      modelValue4,\n      modelValue5,\n      modelValue6,\n      checkValid,\n      errMsg,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\" scoped>\n.case .ev-text-field {\n  width: 300px;\n  margin-right: 15px;\n}\n</style>\n";