export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Common</p>\n    <div\n      class=\"sample-context\"\n      @contextmenu.prevent=\"openContextMenu\"\n    >\n         \n      <ev-context-menu\n        ref=\"menu\"\n        :items=\"menuItems\"\n      />\n    </div>\n    <div class=\"description\">\n      <span class=\"badge\">\n        CLICK RIGHT\n      </span>\n    </div>\n\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Custom</p>\n    <div\n      class=\"sample-context\"\n      @contextmenu.prevent=\"menu2.show\"\n    >\n      <ev-context-menu\n        ref=\"menu2\"\n        :items=\"menuItems2\"\n      />\n         \n    </div>\n    <div class=\"description\">\n      <span\n        class=\"badge\"\n        @click=\"addChild\"\n      >\n        ADD CHILD\n      </span>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref } from 'vue';\n\nexport default {\n  setup() {\n    const menu = ref(null);\n    const menuItems = ref([\n      {\n        text: 11111,\n        iconClass: 'ev-icon-s-panel-out',\n        click: () => console.log('CLICK text1'),\n      },\n      {\n        text: 'TEXT22222222222222',\n        iconClass: 'ev-icon-s-pause',\n        children: [\n          {\n            text: 'TEXT2-111111111111111',\n            iconClass: 'ev-icon-server2',\n            click: () => console.log('CLICK TEXT2-111111111111111'),\n          },\n          {\n            text: 'TEXT2-2',\n            value: 'value22',\n            iconClass: 'ev-icon-server',\n          },\n          {\n            text: 'TEXT2-3',\n            iconClass: 'ev-icon-compress',\n            children: [\n              {\n                text: 'TEXT2-3-1',\n                iconClass: 'ev-icon-bell-warning',\n                click: () => console.log('CLICK TEXT2-3-1'),\n              },\n              {\n                text: 'TEXT2-3-2',\n                iconClass: 'ev-icon-expand2',\n              },\n            ],\n          },\n        ],\n      },\n      {\n        text: 'TEXT3',\n        click: () => console.log('CLICK TEXT3'),\n      },\n      {\n        text: 'TEXT4',\n        click: () => console.log('CLICK TEXT4'),\n      },\n      {\n        text: 'TEXT5',\n        children: [\n          {\n            text: 'TEXT5-1',\n            click: () => console.log('CLICK TEXT5-1'),\n          },\n          {\n            text: 'TEXT5-2',\n            iconClass: 'ev-icon-expand',\n          },\n        ],\n      },\n    ]);\n    const openContextMenu = (e) => {\n      menu.value.show(e);\n    };\n\n    const menu2 = ref(null);\n    const menuItems2 = ref([\n      {\n        text: 'TEXT1',\n        iconClass: 'ev-icon-s-panel-out',\n        disabled: true,\n        click: () => console.log('CLICK text1'),\n      },\n      {\n        text: 'TEXT2',\n        disabled: true,\n        iconClass: 'ev-icon-s-pause',\n        children: [\n          {\n            text: 'TEXT2-1',\n            iconClass: 'ev-icon-server2',\n            click: () => console.log('CLICK text2-1'),\n          },\n          {\n            text: 'TEXT2-2',\n            disabled: true,\n            iconClass: 'ev-icon-server',\n          },\n        ],\n      },\n      {\n        text: 'TEXT3',\n      },\n    ]);\n\n    const addChild = () => {\n      menuItems2.value.push({\n        text: 'TEXT4',\n        iconClass: 'ev-icon-bell-warning',\n        click: () => { console.log('CLICK TEXT4'); },\n      });\n    };\n\n    return {\n      menu,\n      menuItems,\n      openContextMenu,\n      menu2,\n      menuItems2,\n      addChild,\n    };\n  },\n};\n</script>\n\n<style>\n.sample-context {\n  width: 400px;\n  height: 400px;\n  max-width: 400px;\n  max-height: 400px;\n  background: #8D99A7;\n}\n</style>\n";