export default "<template>\n  <ev-chart\n      :data=\"chartData\"\n      :options=\"chartOptions\"\n  />\n</template>\n\n<script>\nimport dayjs from 'dayjs';\n\nexport default {\n  setup() {\n    const time = dayjs().format('YYYY-MM-DD HH:mm:ss');\n    const chartData = {\n      series: {\n        series1: { name: 'series#1' },\n        series2: { name: 'series#2' },\n      },\n      labels: [\n        dayjs(time),\n        dayjs(time).add(1, 'day'),\n        dayjs(time).add(2, 'day'),\n        dayjs(time).add(3, 'day'),\n        dayjs(time).add(4, 'day'),\n        dayjs(time).add(5, 'day'),\n        dayjs(time).add(6, 'day'),\n      ],\n      data: {\n        series1: [100, 25, 36, 47, 0, 50, 80],\n        series2: [80, 36, 25, 47, 15, 100, 0],\n      },\n    };\n\n    const chartOptions = {\n      type: 'line',\n      width: '100%',\n      padding: {\n        right: 50,\n      },\n      title: {\n        text: 'Chart Title',\n        show: true,\n      },\n      legend: {\n        show: true,\n        position: 'right',\n      },\n      axesX: [{\n        type: 'time',\n        showGrid: false,\n        timeFormat: 'MM/DD',\n        interval: 'day',\n        plotLines: [{\n          color: '#FF0000',\n          value: chartData.labels[5],\n          segments: [6, 2],\n          label: {\n            show: true,\n            text: 'X Plot Line',\n            textAlign: 'right',\n          },\n        }],\n        plotBands: [{\n          color: 'rgba(250, 222, 76, 0.8)',\n          from: chartData.labels[2],\n          to: chartData.labels[3],\n          label: {\n            show: true,\n            text: 'X Plot Band ZONE',\n            fontColor: '#FFA500',\n          },\n        }],\n      }],\n      axesY: [{\n        type: 'linear',\n        showGrid: true,\n        startToZero: true,\n        autoScaleRatio: 0.1,\n        plotLines: [{\n          color: '#FF0000',\n          value: 50,\n          segments: [6, 2],\n          label: {\n            show: true,\n            text: 'Y Plot Line',\n          },\n        }],\n        plotBands: [{\n          color: 'rgba(250, 222, 76, 0.8)',\n          from: 20,\n          to: 40,\n          label: {\n            show: true,\n            text: 'Y Plot Band',\n            fontColor: '#FFA500',\n            verticalAlign: 'bottom',\n          },\n        }],\n      }],\n    };\n\n    return {\n      chartData,\n      chartOptions,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\">\n</style>\n";