export default "<template>\n  <div class=\"case\">\n    <ev-chart\n      :data=\"chartData\"\n      :options=\"chartOptions\"\n    />\n    <div class=\"description\">\n      <div class=\"row\">\n        <div class=\"row-item\">\n          <label>  </label>\n          <ev-toggle v-model=\"isRealTime\" />\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref, shallowRef, watch, onUnmounted } from 'vue';\n\nexport default {\n  setup() {\n    const isRealTime = ref(true);\n\n    const series = {\n      series1: {\n        name: 'series1',\n        pointSize: 0.5,\n        color: '#DF6264',\n        pointFill: '#DF6264',\n        overflowColor: '#FF00FF',\n      },\n      series2: {\n        name: 'series2',\n        pointSize: 0.5,\n        color: '#3CA0FF',\n        pointFill: '#3CA0FF',\n        overflowColor: '#A3D3FF',\n      },\n    };\n\n    let series2 = [];\n    let series1 = [];\n    // chartData shallowRef or shallowReactive .\n    const chartData = shallowRef({\n      series,\n      data: {\n        series1,\n        series2,\n      },\n    });\n\n    const chartOptions = ref({\n      type: 'scatter',\n      width: '100%',\n      height: '100%',\n      padding: { top: 20, right: 2, bottom: 4, left: 2 },\n      axesX: [{\n        type: 'time',\n        timeFormat: 'HH:mm',\n        interval: 'minute',\n        showAxis: true,\n        showGrid: false,\n        axisLineColor: '#C9CFDC',\n        labelStyle: {\n          show: true,\n          fontSize: 12,\n          color: '#25262E',\n          fontFamily: 'Roboto',\n          fitDir: 'right',\n        },\n      }],\n      axesY: [{\n        type: 'linear',\n        showAxis: true,\n        startToZero: false,\n        showGrid: true,\n        axisLineColor: '#C9CFDC',\n        gridLineColor: '#C9CFDC',\n        labelStyle: {\n          show: true,\n          fontSize: 12,\n          color: '#25262E',\n          fontFamily: 'Roboto',\n          fitWidth: false,\n          fitDir: 'right',\n        },\n      }],\n      legend: {\n        show: false,\n      },\n      displayOverflow: true,\n      realTimeScatter: {\n        use: true,\n        range: 300, //  5,  \n      },\n    });\n\n    let timeoutId;\n\n    let isInit = true;\n    const getRandomInt = (min, max) => Math.floor(Math.random() * (max - min + 1)) + min;\n\n    const generateData = (num) => {\n      const data = [];\n\n      const floor = number => Math.floor(number / 1000) * 1000;\n\n      for (let i = 0; i < num; i++) {\n        let randomX = 0;\n        let randomY = 0;\n        if (!isInit) {\n          randomX = floor(Date.now() + getRandomInt(-3000, 0)); // -3 ~ \n          randomY = floor(getRandomInt(3000, 95000));\n        } else {\n          randomX = floor(Date.now() + getRandomInt(-300000, 0)); // -5 ~ \n          randomY = floor(getRandomInt(3000, 57000));\n        }\n        const randomType = getRandomInt(0, 1);\n\n        data.push({\n          x: randomX,\n          y: randomY,\n          type: randomType,\n        });\n      }\n      return data;\n    };\n\n    let data;\n    const setDataHandler = () => {\n      series1 = [];\n      series2 = [];\n\n      if (isInit) {\n        data = generateData(10000);\n        isInit = false;\n      } else {\n        data = generateData(100);\n      }\n\n      for (let i = 0; i < data.length; i++) {\n        const dataX = data[i].x;\n        const dataY = data[i].y;\n        const type = data[i].type;\n\n        if (type === 1) {\n          series1.push({\n            x: dataX,\n            y: dataY / 1000,\n          });\n        } else {\n          series2.push({\n            x: dataX,\n            y: dataY / 1000,\n          });\n        }\n      }\n\n      chartData.value = {\n        series,\n        data: {\n          series1,\n          series2,\n        },\n      };\n    };\n\n    setDataHandler();\n\n    const tick = () => {\n      setDataHandler();\n      timeoutId = setTimeout(tick, 3000);\n    };\n\n    watch(() => isRealTime.value, () => {\n      if (isRealTime.value) {\n        timeoutId = setTimeout(tick, 3000);\n      } else {\n        clearTimeout(timeoutId);\n      }\n    }, { immediate: true });\n\n    onUnmounted(() => {\n      clearTimeout(timeoutId);\n    });\n\n    return {\n      isRealTime,\n      chartData,\n      chartOptions,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\" scoped>\n.description-label {\n  vertical-align: top;\n  margin-right: 3px;\n}\n\n.row {\n  display: flex;\n  margin-top: 15px;\n  justify-content: space-between;\n  gap: 12px;\n\n  .row-item {\n    flex: 1;\n    display: flex;\n    align-items: center;\n    gap: 4px;\n    .item-title {\n      line-height: 33px;\n      margin-right: 3px;\n      min-width: 80px;\n    }\n  }\n}\n</style>\n";