export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Calendar (mode: dateTimeRange)</p>\n    <ev-calendar\n      v-model=\"calendar9\"\n      class=\"case-block\"\n      mode=\"dateTimeRange\"\n      :options=\"{\n        timeFormat: ['HH:mm:30', 'HH:20:ss'],\n      }\"\n    />\n    <div class=\"description\">\n      <span class=\"badge\">\n        date\n      </span>\n      {{ calendar9 }}\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref } from 'vue';\n\nexport default {\n  setup() {\n    const calendar9 = ref(['2021-11-10 16:01:01', '2021-12-10 17:10:15']);\n\n    return {\n      calendar9,\n    };\n  },\n};\n</script>\n\n<style>\n.case-block {\n  margin-right: 30px;\n  border: 1px solid black;\n}\n</style>\n";