export default "<template>\n  <ev-chart\n    :data=\"chartData\"\n    :options=\"chartOptions\"\n  />\n</template>\n\n<script>\n  import dayjs from 'dayjs';\n\n  export default {\n    setup() {\n      const time = dayjs().format('YYYY-MM-DD HH:mm:ss');\n      const chartData = {\n        series: {\n          series1: { name: 'series#1', fill: true, point: false },\n          series2: { name: 'series#2', fill: true, point: false },\n        },\n        labels: [\n          dayjs(time),\n          dayjs(time).add(1, 'day'),\n          dayjs(time).add(2, 'day'),\n          dayjs(time).add(3, 'day'),\n          dayjs(time).add(4, 'day'),\n          dayjs(time).add(5, 'day'),\n          dayjs(time).add(6, 'day'),\n        ],\n        data: {\n          series1: [100, 25, 36, 47, 0, 50, 80],\n          series2: [80, 36, 25, 47, 15, 100, 0],\n        },\n      };\n\n      const chartOptions = {\n        type: 'line',\n        width: '100%',\n        title: {\n          text: 'Chart Title',\n          show: true,\n        },\n        legend: {\n          show: true,\n          position: 'right',\n        },\n        axesX: [{\n          type: 'time',\n          showGrid: false,\n          timeFormat: 'MM/DD',\n          interval: 'day',\n          formatter: (value) => {\n            const day = dayjs(value).format('MM/DD');\n\n            if (day === '10/25') {\n              return `${day} (Peak!)`;\n            }\n\n            return day;\n          },\n        }],\n        axesY: [{\n          type: 'linear',\n          showGrid: true,\n          startToZero: true,\n          autoScaleRatio: 0.1,\n        }],\n        maxTip: {\n          use: true,\n          showIndicator: true,\n          indicatorColor: '#FF0000',\n          tipStyle: {\n            background: '#000000',\n            textColor: '#FFFFFF',\n          },\n        },\n      };\n\n      return {\n        chartData,\n        chartOptions,\n      };\n    },\n  };\n</script>\n\n<style lang=\"scss\">\n</style>\n";