export default "<template>\n  <div class=\"case\">\n    <ev-chart\n      v-model:selectedSeries=\"defaultSelectSeries\"\n      :data=\"chartData1\"\n      :options=\"chartOptions1\"\n      @click=\"onClick\"\n    />\n    <ev-chart\n      v-model:selectedSeries=\"defaultSelectSeries\"\n      :data=\"chartData2\"\n      :options=\"chartOptions2\"\n      @click=\"onClick\"\n    />\n    <div class=\"description\">\n      <ev-toggle v-model=\"isLive\" />\n      <span>\n          \n      </span>\n      <br>\n      <br>\n      <ev-button\n        type=\"primary\"\n        shape=\"radius\"\n        @click=\"changeSelectedSeries('inc')\"\n      >\n        +\n      </ev-button>\n      <ev-button\n        type=\"primary\"\n        shape=\"radius\"\n        @click=\"changeSelectedSeries('dec')\"\n      >\n        -\n      </ev-button>\n      <span>\n        v-model:selectedSeries \n      </span>\n      <br>\n      <br>\n      <div>\n        <div class=\"badge yellow\">\n          v-model:selectedSeries\n        </div>\n        {{ defaultSelectSeries }}\n        <br>\n        <br>\n        <div class=\"badge yellow\">\n             (selected)\n        </div>\n        {{ clickedSeries }}\n        <br>\n        <br>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { onBeforeUnmount, onMounted, reactive, ref, watch } from 'vue';\nimport dayjs from 'dayjs';\n\nexport default {\n  setup() {\n    const chartData1 = reactive({\n      series: {\n        series1: { name: 'series#1' },\n        series2: { name: 'series#2' },\n        series3: { name: 'series#3' },\n        series4: { name: 'series#4' },\n        series5: { name: 'series#5' },\n      },\n      labels: [],\n      data: {\n        series1: [],\n        series2: [],\n        series3: [],\n        series4: [],\n        series5: [],\n      },\n    });\n    const chartData2 = reactive({\n      series: {\n        series1: { name: 'series1', fill: true, point: false },\n        series2: { name: 'series2', fill: true, point: false },\n        series3: { name: 'series3', fill: true, point: false },\n        series4: { name: 'series4', fill: true, point: false },\n        series5: { name: 'series5', fill: true, point: false },\n      },\n      labels: [],\n      groups: [['series1', 'series2', 'series3', 'series4', 'series5']],\n      data: {\n        series1: [],\n        series2: [],\n        series3: [],\n        series4: [],\n        series5: [],\n      },\n    });\n\n    const chartOptions1 = ref({\n      type: 'line',\n      width: '100%',\n      height: '80%',\n      title: {\n        show: false,\n      },\n      legend: {\n        show: true,\n        position: 'right',\n      },\n      axesX: [{\n        type: 'time',\n        timeFormat: 'HH:mm:ss',\n        interval: 'second',\n      }],\n      axesY: [{\n        type: 'linear',\n        showGrid: true,\n        startToZero: true,\n        autoScaleRatio: 0.1,\n      }],\n      selectSeries: {\n        use: true,\n        limit: 2,\n        useDeselectOverflow: true,\n      },\n    });\n    const chartOptions2 = ref({\n      type: 'line',\n      width: '100%',\n      height: '80%',\n      title: {\n        show: false,\n      },\n      legend: {\n        show: true,\n        position: 'right',\n      },\n      axesX: [{\n        type: 'time',\n        timeFormat: 'HH:mm:ss',\n        interval: 'second',\n      }],\n      axesY: [{\n        type: 'linear',\n        showGrid: true,\n        startToZero: true,\n        autoScaleRatio: 0.1,\n      }],\n      selectSeries: {\n        use: true,\n        limit: 2,\n        useDeselectOverflow: true,\n      },\n    });\n\n    const clickedSeries = ref();\n    const onClick = (e) => {\n      clickedSeries.value = e.selected;\n    };\n\n    const defaultSelectSeries = ref({\n      seriesId: ['series1'],\n    });\n    const changeSelectedSeries = (type) => {\n      const selectedList = defaultSelectSeries.value.seriesId;\n      let idx;\n      if (selectedList.length === 0) {\n        selectedList.push('series1');\n      } else {\n        idx = +(selectedList.pop()[6]);\n        if (type === 'inc') {\n          idx = idx < 5 ? idx + 1 : 1;\n        } else {\n          idx = idx > 1 ? idx - 1 : 5;\n        }\n      }\n      selectedList.push(`series${idx}`);\n    };\n\n    const isLive = ref(false);\n    const liveInterval = ref();\n    let timeValue = dayjs().format('YYYY-MM-DD HH:mm:ss');\n\n    const addRandomChartData = () => {\n      if (isLive.value) {\n        chartData1.labels.shift();\n        chartData2.labels.shift();\n      }\n\n      timeValue = dayjs(timeValue).add(6, 'second');\n      chartData1.labels.push(dayjs(timeValue));\n      chartData2.labels.push(dayjs(timeValue));\n\n      Object.values(chartData1.data).forEach((seriesData) => {\n        if (isLive.value) {\n          seriesData.shift();\n        }\n        seriesData.push(Math.floor(Math.random() * ((5000 - 5) + 1)) + 5);\n      });\n      Object.values(chartData2.data).forEach((seriesData) => {\n        if (isLive.value) {\n          seriesData.shift();\n        }\n        seriesData.push(Math.floor(Math.random() * ((5000 - 5) + 1)) + 5);\n      });\n    };\n\n    onMounted(() => {\n      for (let ix = 0; ix < 10; ix++) {\n        addRandomChartData();\n      }\n    });\n\n    watch(isLive, (newValue) => {\n      if (newValue) {\n        addRandomChartData();\n        liveInterval.value = setInterval(addRandomChartData, 6000);\n      } else {\n        clearInterval(liveInterval.value);\n      }\n    });\n\n    onBeforeUnmount(() => {\n      clearInterval(liveInterval.value);\n    });\n\n    return {\n      chartData1,\n      chartData2,\n      chartOptions1,\n      chartOptions2,\n      clickedSeries,\n      defaultSelectSeries,\n      isLive,\n      onClick,\n      changeSelectedSeries,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\" scoped>\n.description {\n  position: relative;\n}\n</style>\n";