export default "<template>\n  <div class=\"case\">\n    <ev-tree-grid\n      v-model:selected=\"selected\"\n      v-model:checked=\"checked\"\n      :columns=\"columns\"\n      :rows=\"tableData\"\n      :width=\"widthMV\"\n      :height=\"heightMV\"\n      :option=\"{\n        adjust: adjustMV,\n        showHeader: showHeaderMV,\n        rowHeight: rowHeightMV,\n        columnWidth: columnWidthMV,\n        useCheckbox: {\n          use: useCheckboxMV,\n          mode: checkboxModeMV,\n          headerCheck: headerCheckMV,\n        },\n        searchValue: searchVm,\n        customContextMenu: menuItems,\n        style: {\n          stripe: stripeMV,\n          border: borderMV,\n        },\n      }\"\n      @check-row=\"onCheckedRow\"\n      @check-all=\"onCheckedRow\"\n      @click-row=\"onClickRow\"\n      @dblclick-row=\"onDoubleClickRow\"\n    >\n      <!-- toolbar -->\n      <template #toolbar=\"{ item }\">\n        <ev-button\n          type=\"info\"\n          @click=\"addNode\"\n        >\n          Add\n        </ev-button>\n        <ev-button\n          type=\"info\"\n          @click=\"onClickCustom\"\n        >\n          Set Search Value\n        </ev-button>\n        <ev-text-field\n          v-model=\"searchVm\"\n          class=\"search\"\n          type=\"search\"\n          placeholder=\"Search\"\n          @input=\"item.onSearch\"\n        />\n      </template>\n    </ev-tree-grid>\n  </div>\n</template>\n\n<script>\nimport { ref } from 'vue';\n\nexport default {\n  setup() {\n    const tableData = ref([]);\n    const selected = ref([]);\n    const checked = ref([]);\n    const widthMV = ref('100%');\n    const heightMV = ref(300);\n    const adjustMV = ref(true);\n    const showHeaderMV = ref(true);\n    const stripeMV = ref(false);\n    const rowHeightMV = ref(35);\n    const columnWidthMV = ref(80);\n    const useCheckboxMV = ref(true);\n    const checkboxModeMV = ref('multi');\n    const headerCheckMV = ref(true);\n    const checkedRowsMV = ref();\n    const clickedRowMV = ref();\n    const DbClickedRowsMV = ref();\n    const menuItems = ref([\n      {\n        text: 'Menu1',\n        click: (param) => {\n          console.log(`[Menu1] Selected Row Data: ${JSON.stringify(param?.selectedRow?.[0]?.data)}`);\n        },\n      }, {\n        text: 'Menu2',\n        click: param => console.log('[Menu2]', param),\n      },\n    ]);\n    const borderMV = ref('');\n    const searchVm = ref('');\n    const onCheckedRow = () => {\n      let checkedRow = '';\n      checkedRowsMV.value = checked.value.reduce((prev, curr) => {\n        checkedRow += JSON.stringify(curr?.data);\n        return checkedRow;\n      }, JSON.stringify(checked.value[0]?.data));\n    };\n    const onDoubleClickRow = (e) => {\n      const rowData = e.rowData.data;\n      DbClickedRowsMV.value = JSON.stringify(rowData);\n    };\n    const onClickRow = (e) => {\n      const rowData = e.rowData.data;\n      clickedRowMV.value = JSON.stringify(rowData);\n    };\n    const getData = () => {\n      tableData.value = [\n        {\n          id: 'attributes.ini',\n          children: [\n            {\n              id: 'pythontrace',\n              children: [\n                { id: 'level' },\n                { id: 'trace_errors' },\n                { id: 'trace_indexes' },\n                { id: 'truncate_doclists' },\n              ],\n            },\n          ],\n        },\n        {\n          id: 'diserver.ini',\n          children: [\n            { id: 'api' },\n            { id: 'blobs' },\n            { id: 'connection' },\n            { id: 'make' },\n          ],\n        },\n        {\n          id: 'docstore.ini',\n          children: [\n            { id: 'docstore' },\n          ],\n        },\n      ];\n    };\n    const columns = ref([\n      { caption: 'ID', field: 'id', type: 'number' },\n      { caption: 'Date', field: 'date', type: 'string' },\n      { caption: 'Name', field: 'name', type: 'number' },\n    ]);\n    const onClickCustom = () => {\n      searchVm.value = 'diserver';\n    };\n\n    const addNode = () => {\n      tableData.value[0].children.push({\n        id: 'newenwenwenwenwenwenwenwenw',\n        date: '2016-05-02',\n        name: '3',\n      });\n    };\n    getData();\n    return {\n      columns,\n      tableData,\n      selected,\n      checked,\n      widthMV,\n      heightMV,\n      adjustMV,\n      showHeaderMV,\n      stripeMV,\n      rowHeightMV,\n      columnWidthMV,\n      useCheckboxMV,\n      checkboxModeMV,\n      headerCheckMV,\n      checkedRowsMV,\n      clickedRowMV,\n      DbClickedRowsMV,\n      menuItems,\n      borderMV,\n      searchVm,\n      onCheckedRow,\n      onDoubleClickRow,\n      onClickRow,\n      onClickCustom,\n      addNode,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\" scoped>\n.description {\n  min-width: 200px;\n}\n.form-rows {\n  display: flex;\n  margin-bottom: 5px;\n}\n.form-row {\n  width: 50%;\n}\n.ev-text-field, .ev-input-number, .ev-select {\n  width: 30%;\n}\n.badge {\n  margin-bottom: 2px;\n  margin-right: 5px !important;\n}\n.ev-toggle {\n  margin-right: 10px;\n}\n</style>\n";