export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Common</p>\n    <ev-slider\n      v-model=\"modelValue1\"\n      show-input\n    />\n    <div class=\"description\">\n      <span class=\"badge dark\">\n        Slider Value\n        {{ modelValue1 }}\n      </span>\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Maximum & Minimum Value</p>\n    <ev-slider\n      v-model=\"modelValue2\"\n      :min=\"2\"\n      :max=\"10\"\n      show-input\n      show-step\n    />\n    <div class=\"description\" style=\"margin-bottom: 20px;\">\n      <span class=\"badge yellow\">\n        min=2\n      </span>\n      <span class=\"badge yellow\">\n        max=10\n      </span>\n      <span class=\"badge dark\">\n        Slider Value\n        {{ modelValue2 }}\n      </span>\n    </div>\n    <ev-slider\n      v-model=\"modelValue3\"\n      :min=\"-30\"\n      :max=\"10\"\n      show-input\n      show-step\n    />\n    <div class=\"description\">\n      <span class=\"badge yellow\">\n        min=-30\n      </span>\n      <span class=\"badge yellow\">\n        max=10\n      </span>\n      <span class=\"badge dark\">\n        Slider Value\n        {{ modelValue3 }}\n      </span>\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Step</p>\n    <ev-slider\n      v-model=\"modelValue4\"\n      :step=\"8\"\n      show-input\n      show-step\n    />\n    <div class=\"description\">\n      <span class=\"badge yellow\">\n        step=8\n      </span>\n      <span class=\"badge dark\">\n        Slider Value\n        {{ modelValue4 }}\n      </span>\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Range</p>\n    <ev-slider\n      v-model=\"modelValue5\"\n      :step=\"15\"\n      range\n      show-input\n      show-step\n    />\n    <div class=\"description\">\n      <span class=\"badge\">\n        step=15\n      </span>\n      <span class=\"badge dark\">\n        Slider Value\n        {{ modelValue5 }}\n      </span>\n    </div>\n  </div>\n</template>\n\n<script>\n  import { ref } from 'vue';\n\n  export default {\n    setup() {\n      const modelValue1 = ref(42);\n      const modelValue2 = ref(8);\n      const modelValue3 = ref(-12);\n      const modelValue4 = ref(30);\n      const modelValue5 = ref([1, 20]);\n      return {\n        modelValue1,\n        modelValue2,\n        modelValue3,\n        modelValue4,\n        modelValue5,\n      };\n    },\n  };\n</script>\n";