export default "<template>\n  <div class=\"case\">\n    <ev-chart\n      :data=\"chartData\"\n      :options=\"chartOptions\"\n    />\n\n    <div class=\"description\">\n      <div class=\"section-body\">\n\n        <h2> Common </h2>\n\n        <div class=\"section-item\">\n          <label>  </label>\n          <ev-toggle v-model=\"isLive\"/>\n        </div>\n\n        <div class=\"section-item\" />\n\n        <div class=\"section-item\">\n          <label title=\"legend  \">\n            show\n          </label>\n          <ev-toggle v-model=\"show\"/>\n        </div>\n\n        <div class=\"section-item\">\n          <label title=\"legend \">position</label>\n          <ev-select\n              v-model=\"position\"\n              :items=\"[{\n              name: 'right',\n              value: 'right',\n            }, {\n              name: 'bottom',\n              value: 'bottom',\n            }, {\n              name: 'top',\n              value: 'top',\n            }, {\n              name: 'left',\n              value: 'left',\n            }]\"\n          />\n        </div>\n\n        <div class=\"section-item\">\n          <label title=\"legend  (position - left, right )\">\n            width\n          </label>\n          <ev-input-number\n            v-model=\"width\"\n            :step=\"10\"\n            :min=\"0\"\n            :max=\"500\"\n          />\n        </div>\n\n        <div class=\"section-item\">\n          <label title=\"legend  (position - top, bottom )\">\n            height\n          </label>\n          <ev-input-number\n            v-model=\"height\"\n            :step=\"1\"\n            :min=\"0\"\n            :max=\"300\"\n          />\n        </div>\n\n        <h2> Table </h2>\n\n        <div class=\"section-item\">\n          <label title=\"Table   \">use</label>\n          <ev-toggle v-model=\"useTable\"/>\n        </div>\n\n        <div class=\"section-item\" />\n\n        <div class=\"section-item\">\n          <label title=\"Table - minimum   \">min</label>\n          <ev-toggle v-model=\"useMin\"/>\n        </div>\n\n        <div class=\"section-item\">\n          <label title=\"Table - maximum   \">max</label>\n          <ev-toggle v-model=\"useMax\"/>\n        </div>\n\n        <div class=\"section-item\">\n          <label title=\"Table - average   \">avg</label>\n          <ev-toggle v-model=\"useAvg\"/>\n        </div>\n\n        <div class=\"section-item\">\n          <label title=\"Table - total   \">total</label>\n          <ev-toggle v-model=\"useTotal\"/>\n        </div>\n\n        <div class=\"section-item\">\n          <label title=\"Table - current   \">last</label>\n          <ev-toggle v-model=\"useLast\"/>\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { onBeforeUnmount, onMounted, reactive, ref, watch } from 'vue';\n  import dayjs from 'dayjs';\n\n  export default {\n    setup() {\n      const chartData = reactive({\n        series: {\n          series1: { name: 'series#1', type: 'line', combo: true, fill: true },\n          series2: { name: 'series#2', type: 'bar' },\n          series3: { name: 'series#3', type: 'bar' },\n          series4: { name: 'series#4', type: 'line', combo: true, point: false },\n          series5: { name: 'series#5', type: 'line', combo: true },\n        },\n        labels: [],\n        groups: [['series1', 'series4', 'series5']],\n        data: {\n          series1: [],\n          series2: [],\n          series3: [],\n          series4: [],\n          series5: [],\n        },\n      });\n\n      const show = ref(true);\n      const position = ref('bottom');\n      const useTable = ref(true);\n      const width = ref(140);\n      const height = ref(150);\n      const useMin = ref(true);\n      const useMax = ref(true);\n      const useAvg = ref(true);\n      const useTotal = ref(true);\n      const useLast = ref(true);\n\n      const chartOptions = reactive({\n        width: '100%',\n        thickness: 0.8,\n        cPadRatio: 0.5,\n        axesX: [{\n          type: 'time',\n          showGrid: false,\n          categoryMode: true,\n          timeFormat: 'mm:ss',\n          interval: 'second',\n        }],\n        axesY: [{\n          type: 'linear',\n          showGrid: false,\n          startToZero: true,\n          autoScaleRatio: 0.1,\n        }],\n        tooltip: {\n          showAllValueInRange: true,\n        },\n        legend: {\n          show,\n          position,\n          width,\n          height,\n          table: {\n            use: useTable,\n            style: {\n              header: {\n                color: '#495057',\n                borderBottom: '2px solid #DDDDDD',\n              },\n              row: {\n                borderBottom: '1px solid #DDDDDD',\n              },\n            },\n            columns: {\n              name: {\n                title: 'Name',\n                style: {\n                  minWidth: '100px',\n                  maxWidth: '300px',\n                  textAlign: 'left',\n                },\n              },\n              min: {\n                use: useMin,\n                decimalPoint: 4,\n                style: {\n                  maxWidth: '100px',\n                },\n              },\n              max: {\n                use: useMax,\n                decimalPoint: 4,\n              },\n              avg: {\n                use: useAvg,\n                decimalPoint: 4,\n                formatter: value => `${value.toFixed(0)}`,\n              },\n              total: {\n                use: useTotal,\n                decimalPoint: 4,\n                formatter: value => `${value.toFixed(0)}`,\n              },\n              last: {\n                use: useLast,\n                title: 'CURRENT',\n                style: {\n                  color: '#219EBC',\n                },\n              },\n            },\n          },\n        },\n      });\n\n      const isLive = ref(false);\n      const liveInterval = ref();\n      let timeValue = dayjs().format('YYYY-MM-DD HH:mm:ss');\n\n      const addRandomChartData = (ix) => {\n        if (isLive.value) {\n          chartData.labels.shift();\n        }\n\n        timeValue = dayjs(timeValue).add(1, 'second');\n        chartData.labels.push(dayjs(timeValue));\n\n        Object.values(chartData.data).forEach((seriesData) => {\n          if (isLive.value) {\n            seriesData.shift();\n          }\n\n          seriesData.push(ix > 6 ? null : Math.random() * (100 - 1) + 1);\n        });\n      };\n\n      onMounted(() => {\n        for (let ix = 0; ix < 10; ix++) {\n          addRandomChartData(ix);\n        }\n      });\n\n      watch(isLive, (newValue) => {\n        if (newValue) {\n          addRandomChartData();\n          liveInterval.value = setInterval(addRandomChartData, 1000);\n        } else {\n          clearTimeout(liveInterval.value);\n        }\n      });\n\n      onBeforeUnmount(() => {\n        clearTimeout(liveInterval.value);\n      });\n\n      return {\n        chartData,\n        chartOptions,\n        isLive,\n        show,\n        position,\n        useTable,\n        width,\n        height,\n        useMin,\n        useMax,\n        useAvg,\n        useTotal,\n        useLast,\n      };\n    },\n  };\n</script>\n\n<style lang=\"scss\" scoped>\n.section {\n  width: 100%;\n\n  &-title {\n    padding: 10px;\n    background-color: rgba(#FADE4C, 0.6);\n  }\n\n  &-body {\n    display: flex;\n    padding: 0 0 10px 10px;\n    flex-direction: row;\n    flex-wrap: wrap;\n\n    h2 {\n      width: 100%;\n      margin: 20px 0 5px 0;\n    }\n\n    .section-item {\n      display: flex;\n      width: 50%;\n      padding: 5px;\n      flex-direction: row;\n\n      label {\n        width: 140px;\n        line-height: 35px;\n        margin-right: 10px;\n        font-weight: 700;\n      }\n\n      .ev-toggle {\n        margin-top: 7px;\n      }\n\n      .ev-text-field, .ev-input-number, .ev-select {\n        width: auto;\n      }\n    }\n  }\n}\n</style>\n";