export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Range</p>\n    <ev-time-picker\n      v-model=\"rangeTime\"\n      @change=\"changeTime\"\n    />\n    <div class=\"description\">\n      <span class=\"badge\"> Time </span>\n      {{ rangeTime }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Single</p>\n    <ev-time-picker\n      v-model=\"commonTime\"\n      type=\"single\"\n    />\n    <div class=\"description\">\n      <span class=\"badge\">Time</span>\n      {{ commonTime }}\n    </div>\n  </div>\n  <div class=\"case clearable-example\">\n    <p class=\"case-title\">Clearable</p>\n    <ev-time-picker\n      v-model=\"clearableTime\"\n      type=\"single\"\n      clearable\n    />\n    <div class=\"description blank-mb\">\n      <span class=\"badge\">Time</span>\n      {{ clearableTime }}\n    </div>\n    <ev-time-picker\n      v-model=\"clearableRangeTime\"\n      clearable\n    />\n    <div class=\"description\">\n      <span class=\"badge\">Time</span>\n      {{ clearableRangeTime }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Disabled</p>\n    <ev-time-picker\n      v-model=\"disabledTime\"\n      type=\"single\"\n      disabled\n    />\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">ReadOnly</p>\n    <ev-time-picker\n      v-model=\"readOnlyTime\"\n      type=\"single\"\n      readonly\n    />\n  </div>\n</template>\n\n<script>\nimport { ref } from 'vue';\n\nexport default {\n  setup() {\n    const commonTime = ref('09:00');\n    const readOnlyTime = ref('00:00');\n    const clearableTime = ref('01:00');\n    const disabledTime = ref('09:00');\n    const rangeTime = ref(['12:00', '23:59']);\n    const clearableRangeTime = ref(['07:00', '17:30']);\n    const changeTime = (e, value) => {\n      console.log(e, value);\n    };\n\n    return {\n      commonTime,\n      clearableTime,\n      clearableRangeTime,\n      disabledTime,\n      rangeTime,\n      readOnlyTime,\n      changeTime,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\">\n.clearable-example {\n  .blank-mb {\n    margin-bottom: 25px;\n  }\n}\n</style>\n";