export default "<template>\n  <div class=\"case\">\n    <ev-chart-group :options=\"brushOptions\">\n      <ev-chart\n        v-model:selectedItem=\"defaultSelectItem\"\n        :data=\"chartData\"\n        :options=\"chartOptions\"\n        @click=\"onClick\"\n        @dbl-click=\"onDblClick\"\n      />\n      <ev-chart-brush/>\n    </ev-chart-group>\n    <div class=\"description\">\n      <div class=\"badge yellow\">\n          v-model\n      </div>\n      {{ defaultSelectItem }}\n      <br><br>\n      <div class=\"badge yellow\">\n         \n      </div>\n      {{ clickedLabel }}\n      <br><br>\n      <div class=\"badge yellow\">\n          \n      </div>\n      {{ dblClickedLabel }}\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref, reactive } from 'vue';\nimport dayjs from 'dayjs';\n\nexport default {\n  setup() {\n    const time = dayjs().format('YYYY-MM-DD');\n    const chartData = reactive({\n      series: {\n        series1: { name: 'series#1' },\n        series2: { name: 'series#2' },\n      },\n      labels: [\n        dayjs(time),\n        dayjs(time).add(1, 'day'),\n        dayjs(time).add(2, 'day'),\n        dayjs(time).add(3, 'day'),\n        dayjs(time).add(4, 'day'),\n        dayjs(time).add(5, 'day'),\n        dayjs(time).add(6, 'day'),\n      ],\n      data: {\n        series1: [100, 25, 36, 47, 0, 50, 80],\n        series2: [80, 36, 25, 47, 15, 100, 0],\n      },\n    });\n\n    const chartOptions = reactive({\n      type: 'line',\n      width: '100%',\n      title: {\n        text: 'Chart Title',\n        show: true,\n      },\n      legend: {\n        show: true,\n        position: 'right',\n      },\n      axesX: [{\n        type: 'time',\n        showGrid: false,\n        timeFormat: 'YYYY-MM-DD',\n        interval: 'day',\n      }],\n      axesY: [{\n        type: 'linear',\n        showGrid: true,\n        startToZero: true,\n        autoScaleRatio: 0.3,\n      }],\n      selectItem: {\n        use: true,\n        showTextTip: true,\n        tipText: 'label',\n        fixedPosTop: true,\n        showIndicator: true,\n      },\n      maxTip: {\n        use: true,\n        tipStyle: {\n          background: '#FF00FF',\n        },\n      },\n    });\n\n    const brushOptions = reactive({\n      zoom: {\n        toolbar: {\n          show: true,\n        },\n      },\n    });\n\n    const clickedLabel = ref(\"''\");\n    const onClick = (target) => {\n      clickedLabel.value = dayjs(target.label).format('YYYY-MM-DD');\n    };\n\n    const dblClickedLabel = ref(\"''\");\n    const onDblClick = (target) => {\n      dblClickedLabel.value = dayjs(target.label).format('YYYY-MM-DD');\n    };\n\n    const defaultSelectItem = ref({\n      seriesID: '',\n      dataIndex: null,\n    });\n\n    return {\n      chartData,\n      chartOptions,\n      brushOptions,\n      clickedLabel,\n      dblClickedLabel,\n      defaultSelectItem,\n      onClick,\n      onDblClick,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\">\n</style>\n";