export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Calendar (mode: date)</p>\n    <ev-calendar\n      v-model=\"calendar1\"\n      class=\"case-block\"\n      :options=\"{\n        disabledDate: (time) => time.getTime() > Date.now()\n      }\"\n    />\n    <ev-calendar\n      v-model=\"calendar2\"\n      class=\"case-block\"\n    />\n    <div class=\"description\">\n      <span class=\"badge\">\n        date\n      </span>\n      {{ calendar1 }}\n      <br><br>\n      <span class=\"badge\">\n        date\n      </span>\n      {{ calendar2 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Calendar (mode: dateTime)</p>\n    <ev-calendar\n      v-model=\"calendar3\"\n      class=\"case-block\"\n      mode=\"dateTime\"\n      :options=\"{\n        timeFormat: 'HH:20:00',\n      }\"\n    />\n    <br>\n    <br>\n    <ev-calendar\n      v-model=\"calendar4\"\n      class=\"case-block\"\n      mode=\"dateTime\"\n    />\n    <div class=\"description\">\n      <span class=\"badge\">\n        dateTime\n      </span>\n      {{ calendar3 }}\n      <br><br>\n      <span class=\"badge\">\n        dateTime\n      </span>\n      {{ calendar4 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Calendar (mode: dateMulti, type: day, limit: 3)</p>\n    <ev-calendar\n      v-model=\"calendar5\"\n      class=\"case-block\"\n      mode=\"dateMulti\"\n      :options=\"{\n        multiType: 'date',\n        multiDayLimit: 3,\n        disabledDate: (time) => time.getDay() === 0 || time.getDay() === 6\n      }\"\n    />\n    <div class=\"description\">\n      <span class=\"badge\">\n        date\n      </span>\n      {{ calendar5 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Calendar (mode: dateMulti, type: weekday)</p>\n    <ev-calendar\n      v-model=\"calendar6\"\n      class=\"case-block\"\n      mode=\"dateMulti\"\n      :options=\"{\n        multiType: 'weekday',\n        disabledDate: (time) => time.getTime() <= Date.now()\n      }\"\n    />\n    <div class=\"description\">\n      <span class=\"badge\">\n        date\n      </span>\n      {{ calendar6 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Calendar (mode: dateMulti, type: week)</p>\n    <ev-calendar\n      v-model=\"calendar7\"\n      class=\"case-block\"\n      mode=\"dateMulti\"\n      :options=\"{\n        multiType: 'week',\n        disabledDate: (time) => time.getTime() > Date.now()\n      }\"\n    />\n    <div class=\"description\">\n      <span class=\"badge\">\n        date\n      </span>\n      {{ calendar7 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Calendar (mode: dateRange)</p>\n    <ev-calendar\n      v-model=\"calendar8\"\n      class=\"case-block\"\n      mode=\"dateRange\"\n      :options=\"{\n        disabledDate: (time) => time.getDay() === 2 || time.getDay() === 4\n      }\"\n    />\n    <div class=\"description\">\n      <span class=\"badge\">\n        date\n      </span>\n      {{ calendar8 }}\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref } from 'vue';\n\nexport default {\n  setup() {\n    const calendar1 = ref(null);\n    const calendar2 = ref('2020-09-15');\n    const calendar3 = ref(null);\n    const calendar4 = ref('2020-10-14 14:38:01');\n    const calendar5 = ref(['2020-10-21', '2020-11-02']);\n    const calendar6 = ref([]);\n    const calendar7 = ref([]);\n    const calendar8 = ref(['2020-10-21', '2020-11-05']);\n\n    return {\n      calendar1,\n      calendar2,\n      calendar3,\n      calendar4,\n      calendar5,\n      calendar6,\n      calendar7,\n      calendar8,\n    };\n  },\n};\n</script>\n\n<style>\n.case-block {\n  margin-right: 30px;\n  border: 1px solid black;\n}\n</style>\n";