export default "<template>\n  <div class=\"case\">\n    <ev-pagination\n      v-model=\"current\"\n      :total=\"total\"\n      :per-page=\"perPage\"\n      :visible-page=\"visiblePage\"\n      :show-page-info=\"isPageInfo\"\n      :order=\"order\"\n    >\n    </ev-pagination>\n    <div class=\"description\">\n      <div class=\"form-rows\">\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">Current Page</span>\n          <ev-input-number v-model=\"current\" />\n        </div>\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">Visible Page</span>\n          <ev-input-number\n            v-model=\"visiblePage\"\n            :min=\"7\"\n          />\n        </div>\n      </div>\n      <div class=\"form-rows\">\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">Total Data</span>\n          <ev-input-number v-model=\"total\" />\n        </div>\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">Data per page</span>\n          <ev-input-number v-model=\"perPage\" />\n        </div>\n      </div>\n      <div class=\"form-rows\">\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">Order</span>\n          <ev-select\n            v-model=\"order\"\n            :items=\"orderItems\"\n            placeholder=\"Please select value.\"\n          />\n        </div>\n        <div class=\"form-row\">\n          <span class=\"badge yellow\">Page Info</span>\n          <ev-toggle v-model=\"isPageInfo\"/>\n        </div>\n      </div>\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref } from 'vue';\nimport EvPagination from '@/components/pagination/Pagination';\nimport EvInputNumber from '@/components/inputNumber/InputNumber';\n\nexport default {\n  components: {\n    EvInputNumber,\n    EvPagination,\n  },\n  setup() {\n    const visiblePage = ref(8);\n    const total = ref(150);\n    const current = ref(1);\n    const perPage = ref(10);\n    const order = ref('left');\n    const isPageInfo = ref(false);\n    const orderItems = ref([\n      { name: 'left', value: 'left' },\n      { name: 'right', value: 'right' },\n      { name: 'center', value: 'center' },\n    ]);\n\n    return {\n      total,\n      current,\n      perPage,\n      order,\n      isPageInfo,\n      visiblePage,\n      orderItems,\n    };\n  },\n};\n</script>\n\n<style lang=\"scss\" scoped>\n.form-rows {\n  display: flex;\n}\n.form-row {\n  flex: 1;\n  margin: 5px;\n  .badge {\n    margin-bottom: 3px;\n  }\n}\n.ev-toggle {\n  display: block;\n}\n</style>\n";