export default "<template>\n  <ev-chart\n    :data=\"chartData\"\n    :options=\"chartOptions\"\n  />\n</template>\n\n<script>\n  export default {\n    setup() {\n      const chartData = {\n        series: {\n          series1: { name: 'series#1' }, //  \n          series2: { name: 'series#2', color: '#FF00FF' }, //  \n          series3: { name: 'series#3', color: [[0, '#FF6767'], [0.5, '#FFD1B9'], [1, '#FF9A67']] }, //   \n          series4: { name: 'series#4', color: [[], [1, 'rgba(255, 239, 35, 0.5)']] }, //     \n        },\n        labels: ['value1', 'value2', 'value3', 'value5', 'value5'],\n        data: {\n          series1: [100, 150, 100, 150, 350],\n          series2: [23, 54, 51, 229, 150],\n          series3: [20, 75, 200, 100, 51],\n          series4: [200, 95, 40, 50, 151],\n        },\n      };\n\n      const chartOptions = {\n        type: 'bar',\n        borderRadius: 14,\n        axesX: [{\n          type: 'step',\n        }],\n        axesY: [{\n          showAxis: true,\n          type: 'linear',\n          startToZero: true,\n          autoScaleRatio: 0.1,\n        }],\n        legend: {\n          show: true,\n          position: 'bottom',\n          height: 50,\n          padding: {\n            left: 60,\n            top: 20,\n          },\n        },\n      };\n\n      return {\n        chartData,\n        chartOptions,\n      };\n    },\n  };\n</script>\n\n<style lang=\"scss\" scoped>\n</style>\n";